import React from "react";
import react from "@servicenow/ui-renderer-react";
import { t } from "sn-translate";
import { keyUtils } from "@devsnc/library-form";
import "sn-component-file-icon";
import "@servicenow/now-icon";
import "@servicenow/now-dropdown";
import "@servicenow/now-progress-bar";
import {
	ATTACHMENT_PREVIEW_LOAD_ACTION,
	ATTACHMENT_PREVIEW_DISABLED_PROPERTY,
	FILE_SIZES,
	ALLOWED_THUMBNAIL,
	ATTACHMENT_ACTIONS,
	ATTACHMENT_CARD_DROPDOWN,
	ATTACHMENT_ERROR_MESSAGE,
	NOW_DROPDOWN_ELEMENT
} from "../constants/attachmentConstants.js";
import { ADD_NOTIFICATIONS } from "../constants/constants";
import { getProperty } from "sn-uxpage-presource";

const MENU_TOOLTIP = t("Menu");
const VIEW_FILE = t("View file");
const MORE_ARIA_LABEL = t("Show actions for");

const buttonActionKey = key => `action${_.camelCase(key)}Ref`;

class AttachmentCard extends React.Component {
	constructor(props) {
		super(props);
		this.onTitleBlur = this.onTitleBlur.bind(this);
		this.enableEditing = this.enableEditing.bind(this);
		this.onTitleKeyPress = this.onTitleKeyPress.bind(this);
		this.onTitleKeyDown = this.onTitleKeyDown.bind(this);
		this.isFileUnavailable = this.isFileUnavailable.bind(this);
		this.isFileUploading = this.isFileUploading.bind(this);
		this.onCardKeyPress = this.onCardKeyPress.bind(this);
		this.initAttachmentPreview = this.initAttachmentPreview.bind(this);
		this.getExtensionFromFileName = this.getExtensionFromFileName.bind(this);
		this.focusCardMenu = this.focusCardMenu.bind(this);
		this.state = {
			isEditing: false,
			attachmentActions: this.createAttachmentActionMenu(props.properties.item),
			isAttachmentPreviewDisabled: getProperty(
				ATTACHMENT_PREVIEW_DISABLED_PROPERTY,
				false
			)
		};
	}

	onCardHiddenButtonFocus = () => {
		this.setState({
			isFocused: true
		});
	};

	onCardBlur = () => {
		this.setState({
			isFocused: false
		});
	};

	createAttachmentActionMenu = item => {
		const actions = [];
		const { canCreate } = this.props.properties;

		if (!this.isFileUnavailable(item))
			actions.push(
				this.createActionItem(ATTACHMENT_ACTIONS.DOWNLOAD, item.sysId)
			);

		if (!canCreate) return actions;

		if (item.canDelete)
			actions.push(
				this.createActionItem(ATTACHMENT_ACTIONS.REMOVE, item.sysId)
			);

		if (item.canEditName && !this.isFileUnavailable(item))
			actions.push(ATTACHMENT_ACTIONS.RENAME);

		return actions;
	};

	/**
	 * This function returns a new object back with the action menu and the id of
	 * the attachment.
	 */
	createActionItem = (action, attachmentId) => {
		return {
			...action,
			attachmentId
		};
	};

	componentWillReceiveProps(props) {
		if (this.isPropDifferent(props, "item"))
			this.setState({
				attachmentActions: this.createAttachmentActionMenu(
					props.properties.item
				)
			});

		if (this.isStateDifferent(props, "enableEditingTriggered"))
			this.enableEditing();
	}

	isPropDifferent(changes, prop) {
		return (
			changes.properties[prop] &&
			this.props.properties[prop] !== changes.properties[prop]
		);
	}

	isStateDifferent(changes, prop) {
		return changes[prop] && this.props[prop] !== changes[prop];
	}

	refCard = node => {
		if (!node) return;
		this.setState({
			cardRef: node
		});
	};

	refHiddenCardButton = node => {
		if (!node) return;
		this.setState({
			refHiddenCardButton: node
		});
	};

	refCardMenu = node => {
		if (!node || node === this.state.cardMenuRef) return;
		this.setState({
			cardMenuRef: node
		});
	};

	render() {
		const { item, mode, displayUploadProgressBar } = this.props.properties;
		const card = this.state.isEditing
			? this.renderCardWithInput(item)
			: this.renderCard(item);
		const focusClass = this.state.isFocused ? " icons-focused is-focus" : "";
		const unavailable = this.isFileUnavailable(item);
		const isDisabled = unavailable ? " is-disabled" : "";
		const isCompact = mode === "compact" ? " -compact" : "";
		const attachClassName = `sn-attachments${focusClass}${isDisabled}${isCompact}`;
		return (
			<React.Fragment>
				<div
					className={attachClassName}
					onClick={this.onCardClick}
					onBlur={this.onCardBlur}
				>
					<button
						className="hidden"
						aria-label={this.getCardAriaLabel(item)}
						aria-haspopup="dialog"
						onKeyPress={this.onCardKeyPress}
						onFocus={this.onCardHiddenButtonFocus}
						ref={this.refHiddenCardButton}
					/>
					<div className="sn-attachments-content -static has-media">
						{unavailable && (
							<now-icon
								size={mode === "compact" ? "lg" : "xl"}
								icon="document-infected-outline"
							/>
						)}
						{!unavailable && this.renderFileThumbnail(item, mode)}
					</div>
					{card}
				</div>
				{displayUploadProgressBar && <now-progress-bar size="xs" />}
			</React.Fragment>
		);
	}

	renderFileThumbnail = (item, mode) => {
		return this.isImageType(item.type) ? (
			<img
				className="sn-attachments-img"
				src={"/" + item.sysId + ".iix?t=small"}
				alt={item.name}
			/>
		) : (
			<sn-file-icon
				className="sn-icon"
				size={mode === "compact" ? "xxl" : "2x"}
				mime-type={item.type}
				extension={this.getExtensionFromFileName(item.name)}
			/>
		);
	};

	isImageType = type => {
		return ALLOWED_THUMBNAIL.includes(type.substring(0, type.indexOf("/")));
	};

	onCardKeyPress(e) {
		if (keyUtils.onEnter(e) || keyUtils.onSpace(e))
			this.initAttachmentPreview(e);
	}

	onCardClick = e => {
		this.state.refHiddenCardButton.focus();
		this.initAttachmentPreview(e);
	};

	/**
	 * Load the attachment preview for the current attachment card
	 * @param e Event
	 */
	initAttachmentPreview(e) {
		if (
			e.target.tagName === NOW_DROPDOWN_ELEMENT ||
			this.state.isAttachmentPreviewDisabled
		)
			return;

		const {
			dispatch,
			properties: { item }
		} = this.props;
		const securityErrorMessage = t(
			ATTACHMENT_ERROR_MESSAGE.NOT_AVAILABLE,
			item.name
		);

		if (this.isFileUnavailable(item)) {
			this.dispatchFileErrorNotification(securityErrorMessage);
			return;
		}

		if (this.isFileUploading(item)) {
			this.dispatchFileErrorNotification(t(ATTACHMENT_ERROR_MESSAGE.UPLOADING));
			return;
		}

		dispatch(ATTACHMENT_PREVIEW_LOAD_ACTION, { item });

		e.stopPropagation();
	}

	getCardAriaLabel(item) {
		if (this.state.isAttachmentPreviewDisabled) return item.name;
		return `${VIEW_FILE}: ${item.name}`;
	}

	/**
	 * Render card with input to update name
	 * @param item
	 * @returns {*}
	 */
	renderCardWithInput(item) {
		return (
			<div className="sn-attachments-content -visible">
				<div className="sn-control is-focus">
					<div className="sn-control-field">
						<input
							id="attachment_input"
							name="attachment_input"
							type="text"
							defaultValue={item.name}
							className="sn-input"
							onKeyPress={this.onTitleKeyPress}
							onKeyDown={this.onTitleKeyDown}
							onClick={this.preventClickDefault}
							onBlur={this.onTitleBlur}
							autoFocus
						/>
					</div>
				</div>
				<span>
					{" "}
					{this.isFileUnavailable(item)
						? t("File unavailable")
						: this.getAttachmentSize(item.size)}
				</span>
			</div>
		);
	}

	preventClickDefault = e => {
		e.stopPropagation();
		e.preventDefault();
	};

	getAttachmentSize = attachmentSize => {
		if (!attachmentSize) return "0 B";

		attachmentSize = parseInt(attachmentSize) || 0;
		let sizeUnit = 0;
		while (attachmentSize > 1024) {
			attachmentSize = attachmentSize / 1024;
			sizeUnit++;
		}

		return `${attachmentSize.toFixed(sizeUnit > 0 ? 1 : 0)} ${
			FILE_SIZES[sizeUnit]
		}`;
	};

	getAriaAttributes(item) {
		return {
			configAria: {
				"aria-label": `${MORE_ARIA_LABEL} ${item.name}`,
				"aria-haspopup": "listbox"
			}
		};
	}

	/**
	 * Render card to display specific metadata.  The attributes are spread, given the consistent class
	 * name and data attributes.
	 * @param item
	 * @returns {*}
	 */
	renderCard(item) {
		return (
			<React.Fragment>
				<div className="sn-attachments-content" ref={this.refCard}>
					<span className="sn-attachments-content-title">
						<span>{item.name}</span>
					</span>
					<span>
						{" "}
						{this.isFileUnavailable(item) || this.isFileUploading(item)
							? t("File unavailable")
							: this.getAttachmentSize(item.size)}
					</span>
					<now-tooltip target-ref={this.state.cardRef} content={item.name} />
				</div>
				{!this.isFileUploading(item) && this.state.attachmentActions.length > 0 && (
					<div className="sn-attachments-content -dropdown">
						<now-dropdown
							ref={this.refCardMenu}
							bare
							hide-caret
							icon="ellipsis-v-outline"
							items={this.state.attachmentActions}
							select="none"
							manage-selecteditems
							size="sm"
							variant="secondary"
							tooltip-content={MENU_TOOLTIP}
							component-name={ATTACHMENT_CARD_DROPDOWN}
							{...this.getAriaAttributes(item)}
						/>
					</div>
				)}
			</React.Fragment>
		);
	}

	/**
	 * On blur, reset the attachment state
	 */
	onTitleBlur() {
		this.resetAttachmentState();
	}

	/**
	 * Update the attachment name on enter
	 * @param e
	 */
	onTitleKeyPress(e) {
		e.stopPropagation();
		const element = e.srcElement || e.target;
		const { maxFileNameLength = 0 } = this.props.properties;

		if (element.value.length > maxFileNameLength) {
			const message = t(
				ATTACHMENT_ERROR_MESSAGE.FILE_NAME_MAX_LENGTH,
				maxFileNameLength
			);
			this.dispatchFileErrorNotification(message);
		} else if (keyUtils.onEnter(e)) {
			e.preventDefault();
			if (!element.value) {
				const message = t(ATTACHMENT_ERROR_MESSAGE.EMPTY_NAME);
				this.dispatchFileErrorNotification(message);
			} else if (this.hasExtensionChanged(element.value)) {
				const { item } = this.props.properties;
				const message = t(
					ATTACHMENT_ERROR_MESSAGE.INVALID_EXTENSION,
					item.name,
					element.value
				);
				this.dispatchFileErrorNotification(message);
			} else {
				this.props.properties.item.name = element.value;
				this.update();
				this.resetAttachmentState();
				this.focusCardMenu();
			}
		}
	}

	onTitleKeyDown(e) {
		if (keyUtils.onTab(e) || keyUtils.onEscape(e)) {
			this.resetAttachmentState();
			if (keyUtils.onEscape(e)) {
				e.stopPropagation();
				e.preventDefault();
				this.focusCardMenu();
			}
		}
	}

	focusCardMenu() {
		setTimeout(() => {
			if (!this.state.cardMenuRef) return;
			this.state.cardMenuRef.focus();
		});
	}

	/**
	 * Dispatched Error Notification from file rename validation
	 */
	dispatchFileErrorNotification = message => {
		const type = "error";
		this.props.dispatch(ADD_NOTIFICATIONS, {
			notifications: [
				{
					type,
					message
				}
			]
		});
	};

	/**
	 * This file checks and returns true/false if the file extension has changed
	 * @param newName New name of the file
	 */
	hasExtensionChanged = newName => {
		const { item } = this.props.properties;
		const newExt = this.getExtensionFromFileName(newName);
		const oldExt = this.getExtensionFromFileName(item.name);

		return newExt !== oldExt;
	};

	/**
	 * Update the name of the selected attachment
	 */
	update() {
		const {
			item: { name, sysId }
		} = this.props.properties;
		this.props.dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_UPDATE_ACTION, {
			file_name: name,
			sys_id: sysId
		});
	}

	/**
	 * Utility method to clear the attachment editing state.
	 */
	resetAttachmentState() {
		this.setState({
			isEditing: false
		});
	}

	/**
	 * Utility method to establish a files availability.  For now, this may be verbose.  However, there are 4
	 * sys_attachment states that have been introduced:
	 *
	 * available, not_available, pending, and available_conditionally
	 *
	 * @param item
	 * @returns {boolean}
	 */
	isFileUnavailable(item) {
		return item.state === "not_available";
	}

	/**
	 * Tells if the file is being uploaded
	 *
	 * @param item
	 * @returns {boolean}
	 */
	isFileUploading(item) {
		return item.state === "uploading";
	}

	/**
	 * Return the file extension if found else return ''.
	 */
	getExtensionFromFileName(name) {
		if (name.lastIndexOf(".") === -1) return "";

		return name.substring(name.lastIndexOf(".") + 1).toLowerCase();
	}

	/**
	 * Set the attachment editing to true.
	 */
	enableEditing() {
		this.setState({
			isEditing: true
		});
	}
}

export default AttachmentCard;
