const DropdownPanelItemWithSecondayValue = ({
	item: { value, displayValue, secondaryDisplayValue, icon },
	onClick
}) => {
	return (
		<li
			tabindex="-1"
			id={"key" + value}
			className="sn-component-dropdown-panel-item"
			on-click={onClick}
			role="option"
		>
			<div className="sn-component-line-height-crop">
				<section className="sn-component-dropdown-panel-container">
					<section className="sn-component-dropdown-panel-left">
						<now-avatar image-src={icon} size="sm" />
					</section>
					<section className="sn-component-dropdown-panel-right">
						<div
							className="sn-component-dropdown-panel-item-label"
							on-mouseover={({ target }) => {
								if (target.offsetWidth < target.scrollWidth) {
									target.title = displayValue;
								}
							}}
						>
							{displayValue}
						</div>
						<div
							className="sn-component-dropdown-panel-item-secondary-label"
							on-mouseover={({ target }) => {
								if (target.offsetWidth < target.scrollWidth) {
									target.title = secondaryDisplayValue;
								}
							}}
						>
							{secondaryDisplayValue}
						</div>
					</section>
				</section>
			</div>
		</li>
	);
};

// eslint-disable-next-line no-unused-vars
const DropdownPanelItem = ({ item: { value, displayValue }, onClick }) => {
	return (
		<li
			tabindex="-1"
			id={"key" + value}
			className="sn-component-dropdown-panel-item"
			on-click={onClick}
			role="option"
		>
			<div className="sn-component-line-height-crop">
				<span
					className="sn-component-dropdown-panel-item-label"
					on-mouseover={({ target }) => {
						if (target.offsetWidth < target.scrollWidth) {
							target.title = displayValue;
						}
					}}
				>
					{displayValue}
				</span>
			</div>
		</li>
	);
};

export const DropdownPanel = (state, { dispatch }) => {
	const { items, label } = state.properties;

	const onToggleItemSelected = value => {
		dispatch("DROPDOWN_PANEL#SELECTED_ITEM_SET", { value });
	};

	return (
		<section>
			<label className="sn-component-dropdown-panel-header"> {label} </label>
			<ul
				role="listbox"
				class={{ "sn-component-dropdown-panel-list": true }}
				tabindex="-1"
			>
				{items.map(item => {
					if (item.secondaryDisplayValue || item.icon) {
						return (
							<DropdownPanelItemWithSecondayValue
								item={item}
								onClick={() => onToggleItemSelected(item.value)}
							/>
						);
					} else {
						return (
							<DropdownPanelItem
								item={item}
								onClick={() => onToggleItemSelected(item.value)}
							/>
						);
					}
				})}
			</ul>
		</section>
	);
};
