import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN } from "@devsnc/sn-scripted-modal";
import {
	ATTACHMENT_DOC_VIEWER_CLOSED_OR_DELETED,
	ATTACHMENT_PREVIEW_DISMISS_ACTION,
	ALL_ATTACHMENTS_UPLOADED
} from "./constants";
import { ATTACHMENT_ACTIONS } from "../../constants/attachmentConstants";
import {
	createDragHandlers,
	showAttachmentUploadErrors,
	uploadAttachmentToServer,
	processFiles
} from "./upload/attachmentUploadUtil";
import { getFirstDiv } from "../../../utils/domUtils";

export default {
	actions: {},
	handlers: {
		[ATTACHMENT_DOC_VIEWER_CLOSED_OR_DELETED]: ({
			properties,
			dispatch,
			action
		}) => {
			const { attachmentPreviewItem, table, sysId } = properties;
			if (!(action.payload.viewerHidden || action.payload.deleted)) return;
			dispatch(ATTACHMENT_PREVIEW_DISMISS_ACTION, {
				name: attachmentPreviewItem.name,
				sysId: attachmentPreviewItem.sysId
			});
			if (!action.payload.deleted) return;
			dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY, {
				sysId,
				table
			});
		},
		[COMPONENT_BOOTSTRAPPED]: ({
			updateState,
			properties,
			state,
			dispatch,
			host
		}) => {
			const { canUploadAttachment, nowHoistPrependTo, disableUploadModal } = properties;
			const { showAttachmentUploadOverlay } = state;

			const closeUploadModal = () => {
				updateState({
					showUploadFileModal: false,
					uploadAttachmentListIndex: 0,
					uploadAttachmentList: []
				});
			};

			const showAttachmentErrors = errorList => {
				closeUploadModal();
				showAttachmentUploadErrors(errorList, dispatch);
			};

			const setShowAttachmentUploadOverlay = value => {
				if (value === showAttachmentUploadOverlay) return;
				updateState({
					showAttachmentUploadOverlay: value
				});
			};

			const finishedDroppingFilesCallback = (uploadList, errorList) => {
				if (uploadList.length) {
					if(disableUploadModal) {
						dispatch(ALL_ATTACHMENTS_UPLOADED,{uploadList, errorList});
						return;
					}
					updateState({
						showUploadFileModal: true,
						uploadAttachmentList: uploadList,
						errorAttachmentList: errorList,
						uploadAttachmentListIndex: 0,
						currentFileName: uploadList[0].name
					});
					if (nowHoistPrependTo) dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, true);
				}

				if (!uploadList.length && errorList.length) {
					showAttachmentErrors(errorList);
				}
			};

			const dragHandlers = canUploadAttachment
				? createDragHandlers(
						setShowAttachmentUploadOverlay,
						finishedDroppingFilesCallback
				  )
				: {};

			const dropTarget = nowHoistPrependTo || getFirstDiv(host);
			if (dragHandlers.onDragEnter)
				dropTarget.addEventListener("dragenter", dragHandlers.onDragEnter);
			if (dragHandlers.onDragOver)
				dropTarget.addEventListener("dragover", dragHandlers.onDragOver);
			if (dragHandlers.onDragLeave)
				dropTarget.addEventListener("dragleave", dragHandlers.onDragLeave);
			if (dragHandlers.onDrop)
				dropTarget.addEventListener("drop", dragHandlers.onDrop);

			updateState({
				dragHandlers,
				dropTarget
			});
		},
		[ALL_ATTACHMENTS_UPLOADED]: ({
			dispatch,
			properties: { table, sysId },
			action: {
				payload: { uploadList, errorList }
			}
		}) => {
			_.forEach(uploadList, file => uploadAttachmentToServer(table, sysId, file, dispatch));
			if (errorList.length > 0) showAttachmentUploadErrors(errorList, dispatch);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			dispatch,
			state,
			properties,
			updateState
		}) => {
			const { name, value: uploadFiles, previousValue } = action.payload;

			const { showUploadFileModal } = state;
			const { nowHoistPrependTo, disableUploadModal } = properties;

			if (
				name === "uploadFiles" &&
				uploadFiles !== previousValue &&
				uploadFiles &&
				uploadFiles.length > 0
			)
				processFiles(uploadFiles, (uploadList, errorList) => {
					if (uploadList.length > 0) {
						if(disableUploadModal) {
							dispatch(ALL_ATTACHMENTS_UPLOADED,{uploadList, errorList});
							return;
						}
						updateState({
							showUploadFileModal: true,
							uploadAttachmentList: uploadList,
							errorAttachmentList: errorList,
							currentFileName: uploadList[0].name,
							uploadAttachmentListIndex: 0
						});
						if (nowHoistPrependTo) dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, true);
					}
					else if (errorList.length > 0)
						showAttachmentUploadErrors(errorList, dispatch);
				});
		}
	}
};
