import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;
import { EDIT_TAG_MODAL, USERS, GROUPS } from "./constants";
import { EDIT_TAG_MODAL_QUERIES } from "./editTagModalGraphQLQuery";
import { ACTIONS } from "../constants";
import { TYPEAHEAD } from "../typeahead/constants";
import TagDetails from "./tagDetails";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";

/* Helpers */

export const getPrefix = condition => condition.split("STARTSWITH").pop();

export const getLoadAction = key =>
	key === USERS
		? EDIT_TAG_MODAL.ACTIONS.LOAD_USERS
		: EDIT_TAG_MODAL.ACTIONS.LOAD_USER_GROUPS;

export const getListKey = fieldName =>
	fieldName === "user_list" ? USERS : GROUPS;

/* GraphQL request Handlers */

/* Load tag */

const loadTagEffect = createGraphQLEffect(
	EDIT_TAG_MODAL_QUERIES.GET_TAG_DETAILS,
	{
		variableList: ["tagId"],
		successActionType: EDIT_TAG_MODAL.ACTIONS.LOAD_TAG_DETAILS_SUCCESSFUL
	}
);

const loadTagInterceptor = context => {
	const { coeffects } = context;
	const tagId = _.get(coeffects, "state.properties.tagId", "");
	if (!tagId) return context;

	return {
		...context,
		effects: [
			{
				...loadTagEffect,
				args: [
					...loadTagEffect.args,
					{
						...coeffects,
						action: {
							...coeffects.action,
							payload: { tagId }
						}
					}
				]
			}
		]
	};
};
const transformState = ({ properties, updateState }) => {
	updateState({ opened: properties.opened, tagId: properties.tagId });
};

const loadTagSuccessHandler = ({
	updateState,
	action: {
		payload: {
			data: {
				GlideRecord_Query: {
					label: {
						_results: [tagDetails]
					}
				}
			}
		}
	}
}) => updateState({ tagDetails });

/* Load avatars of users */

const loadAvatarsEffect = createGraphQLEffect(
	EDIT_TAG_MODAL_QUERIES.GET_AVATARS,
	{
		variableList: ["queryConditions"],
		successActionType: EDIT_TAG_MODAL.ACTIONS.LOAD_AVATARS_SUCCESSFUL
	}
);

const initializeAvatarsInterceptor = context => {
	const { effects, coeffects } = context;
	const { action } = coeffects;
	const {
		payload: {
			data: {
				GlideRecord_Query: {
					label: {
						_results: [tagDetails]
					}
				}
			}
		}
	} = action;
	const { user_list: userList } = tagDetails;
	if (!userList.value) return context;

	const loadAvatarAction = {
		...action,
		type: EDIT_TAG_MODAL.ACTIONS.LOAD_AVATARS,
		payload: {
			queryConditions: "sys_idIN" + userList.value
		}
	};
	return {
		...context,
		effects: [
			...effects,
			{
				effect: loadAvatarsEffect.effect,
				args: [
					...loadAvatarsEffect.args,
					{
						...coeffects,
						action: loadAvatarAction
					}
				]
			}
		]
	};
};

const formatAvatars = (avatars = [], userValues) => {
	if (!userValues) return "";

	const userAvatars = avatars.reduce(
		(accumulator, { sys_id, avatar }) => ({
			...accumulator,
			[sys_id.value]: avatar.value
		}),
		{}
	);

	return userValues
		.split(",")
		.map(userId => userAvatars[userId] || "")
		.join(",");
};

const loadAvatarsSuccessHandler = ({
	updateState,
	state,
	action: { payload }
}) => {
	const avatars = _.get(
		payload,
		"data.GlideRecord_Query.sys_user._results",
		[]
	);
	updateState({
		tagDetails: {
			...state.tagDetails,
			user_list: {
				...state.tagDetails.user_list,
				avatars: formatAvatars(avatars, state.tagDetails.user_list.value)
			}
		}
	});
};

/* load users */
const loadUsersHandler = createGraphQLEffect(EDIT_TAG_MODAL_QUERIES.GET_USERS, {
	variableList: ["queryConditions"],
	successActionType: EDIT_TAG_MODAL.ACTIONS.LOAD_USERS_SUCCESSFUL
});

const loadUsersSuccessHandler = ({
	updateState,
	state,
	action: {
		payload: {
			data: {
				GlideRecord_Query: {
					sys_user: { _results: results, _rowCount: totalResults }
				}
			}
		},
		meta: {
			options: {
				variables: { queryConditions }
			}
		}
	}
}) => {
	const prefix = getPrefix(queryConditions);
	const userList = new TagDetails({ ...state }).userList;
	userList.updateAvailableItems(prefix, { results, totalResults });

	// Return if the prefix is outdated, user typed since last request.
	if (userList.filter === prefix) {
		updateState({ [USERS]: userList.properties });
	}
};

/* load user groups */
const loadUserGroupsHandler = createGraphQLEffect(
	EDIT_TAG_MODAL_QUERIES.GET_USER_GROUPS,
	{
		variableList: ["queryConditions"],
		successActionType: EDIT_TAG_MODAL.ACTIONS.LOAD_USER_GROUPS_SUCCESSFUL
	}
);

const loadUserGroupsSuccessHandler = ({
	updateState,
	state,
	action: {
		payload: {
			data: {
				GlideRecord_Query: {
					sys_user_group: { _results: results, _rowCount: totalResults }
				}
			}
		},
		meta: {
			options: {
				variables: { queryConditions }
			}
		}
	}
}) => {
	const prefix = getPrefix(queryConditions);
	const groupList = new TagDetails({ ...state }).groupList;
	groupList.updateAvailableItems(prefix, { results, totalResults });

	// Return if the prefix is outdated, user typed since last request.
	if (groupList.filter === prefix) {
		updateState({ [GROUPS]: groupList.properties });
	}
};

/* update Tag */
const updateTagHandler = createGraphQLEffect(
	EDIT_TAG_MODAL_QUERIES.UPDATE_TAG,
	{
		variableList: ["sys_id", "name", "user_list", "group_list", "viewable_by"],
		successActionType: EDIT_TAG_MODAL.ACTIONS.UPDATE_TAG_SUCCESSFUL
	}
);

const updateTagSuccessHandler = ({
	dispatch,
	action: {
		payload: {
			data: {
				GlideRecord_Mutation: { update_label: updatedTagDetails }
			}
		}
	}
}) => {
	dispatch(EDIT_TAG_MODAL.ACTIONS.TAG_UPDATED, { tag: updatedTagDetails });
};

/* Modal interaction handlers */
const saveButtonHandler = ({ state, dispatch }) => {
	const tagDetails = new TagDetails({ ...state });

	if (!tagDetails.name.value) return;

	dispatch(EDIT_TAG_MODAL.ACTIONS.UPDATE_TAG, tagDetails.value);
	dispatch(EDIT_TAG_MODAL.ACTIONS.OPENED_SET, {
		value: false,
		tagId: state.tagId
	});
};

const cancelButtonHandler = ({ state, dispatch }) => {
	dispatch(EDIT_TAG_MODAL.ACTIONS.OPENED_SET, {
		value: false,
		tagId: state.tagId
	});
};

const fieldChangedHandler = ({
	updateState,
	state,
	action: {
		payload: { name, value, displayValue, avatars }
	}
}) =>
	updateState({
		tagDetails: {
			...state.tagDetails,
			[name]: {
				...state.tagDetails[name],
				value: value,
				displayValue: displayValue || state.tagDetails[name].displayValue,
				avatars: avatars
			}
		},
		[USERS]: {
			...state[USERS],
			filter: "",
			dropdown: false
		},
		[GROUPS]: {
			...state[GROUPS],
			filter: "",
			dropdown: false
		}
	});

const loadItemsRequestFromTypeaheadInterceptor = context => {
	const { coeffects } = context;
	const { action, state } = coeffects;
	const {
		payload: { name: fieldName, prefix }
	} = action;

	const list = new TagDetails({ ...state }).getList(fieldName);
	list.filter = prefix;
	list.dropdown = false;

	if (!prefix || list.loadAvailableItemsFromCache(prefix)) {
		const effect = ({ updateState, state }) =>
			updateState({
				[USERS]: { ...state[USERS], dropdown: false },
				[GROUPS]: { ...state[GROUPS], dropdown: false },
				[list.listKey]: list.properties
			});
		return {
			...context,
			effects: [{ effect, args: [coeffects] }]
		};
	}

	const effect =
		list.listKey === USERS ? loadUsersHandler : loadUserGroupsHandler;
	return {
		...context,
		effects: [
			{
				...effect,
				args: [
					...effect.args,
					{
						...coeffects,
						action: {
							...action,
							type: getLoadAction(list.listKey),
							payload: {
								queryConditions: "active=true^nameSTARTSWITH" + prefix
							}
						}
					}
				]
			}
		]
	};
};

const pillRemovedHandler = ({
	dispatch,
	state,
	action: {
		payload: { name: fieldName, value }
	}
}) => {
	const list = new TagDetails({ ...state }).getList(fieldName);
	const selectedItems = list.selected;
	const newItems = selectedItems.filter(i => i.value !== value);
	list.updateSelectedItems(newItems);
	dispatch(EDIT_TAG_MODAL.ACTIONS.FIELD_CHANGED, {
		name: list.fieldName,
		value: list.value,
		displayValue: list.displayValue,
		avatars: list.avatars
	});
};

const pillAddeddHandler = ({
	dispatch,
	state,
	action: {
		payload: { name: fieldName, value }
	}
}) => {
	const list = new TagDetails({ ...state }).getList(fieldName);
	const availableItems = list.available;
	const selectedItems = list.selected;

	const newItems = [
		...selectedItems,
		...availableItems.filter(i => i.value === value)
	];
	list.updateSelectedItems(newItems);
	dispatch(EDIT_TAG_MODAL.ACTIONS.FIELD_CHANGED, {
		name: list.fieldName,
		value: list.value,
		displayValue: list.displayValue,
		avatars: list.avatars
	});
};

const typeAheadDropdownOpenedHandler = ({ updateState, state, action }) => {
	const {
		payload: { value },
		meta: { componentName: fieldName }
	} = action;
	if (value) return;

	const listKey = getListKey(fieldName);
	updateState({
		[listKey]: { ...state[listKey], dropdown: false, filter: "" }
	});
};

const noopHandler = () => {};

const actions = Object.freeze({
	actions: {
		[EDIT_TAG_MODAL.ACTIONS.LOAD_TAG_DETAILS]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.LOAD_AVATARS_SUCCESSFUL]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.LOAD_TAG_DETAILS_SUCCESSFUL]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.UPDATE_TAG]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.UPDATE_TAG_SUCCESSFUL]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.LOAD_USERS_SUCCESSFUL]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.LOAD_USER_GROUPS_SUCCESSFUL]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.SAVE]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.CANCEL]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.FIELD_CHANGED]: {
			private: true
		},
		[TYPEAHEAD.ACTIONS.LOAD_ITEMS]: {
			private: true
		},
		[TYPEAHEAD.ACTIONS.PILL_REMOVED]: {
			private: true
		},
		[TYPEAHEAD.ACTIONS.PILL_ADDED]: {
			private: true
		},
		[ACTIONS.NOW_PILL_SELECTION_SET]: {
			private: true
		}
	},
	actionHandlers: {
		[EDIT_TAG_MODAL.ACTIONS.LOAD_TAG_DETAILS]: {
			effect: transformState,
			interceptors: [loadTagInterceptor]
		},
		[EDIT_TAG_MODAL.ACTIONS.LOAD_AVATARS_SUCCESSFUL]: loadAvatarsSuccessHandler,
		[EDIT_TAG_MODAL.ACTIONS.LOAD_TAG_DETAILS_SUCCESSFUL]: {
			effect: loadTagSuccessHandler,
			interceptors: [
				{
					after: initializeAvatarsInterceptor
				}
			]
		},
		[EDIT_TAG_MODAL.ACTIONS.UPDATE_TAG]: updateTagHandler,
		[EDIT_TAG_MODAL.ACTIONS.UPDATE_TAG_SUCCESSFUL]: updateTagSuccessHandler,
		[EDIT_TAG_MODAL.ACTIONS.LOAD_USERS_SUCCESSFUL]: loadUsersSuccessHandler,
		[EDIT_TAG_MODAL.ACTIONS
			.LOAD_USER_GROUPS_SUCCESSFUL]: loadUserGroupsSuccessHandler,
		[EDIT_TAG_MODAL.ACTIONS.SAVE]: saveButtonHandler,
		[EDIT_TAG_MODAL.ACTIONS.CANCEL]: cancelButtonHandler,
		[EDIT_TAG_MODAL.ACTIONS.FIELD_CHANGED]: fieldChangedHandler,
		[TYPEAHEAD.ACTIONS.LOAD_ITEMS]: {
			effect: noopHandler,
			interceptors: [
				{
					after: loadItemsRequestFromTypeaheadInterceptor
				}
			]
		},
		[TYPEAHEAD.ACTIONS.PILL_REMOVED]: pillRemovedHandler,
		[TYPEAHEAD.ACTIONS.PILL_ADDED]: pillAddeddHandler,
		[ACTIONS.NOW_PILL_SELECTION_SET]: noopHandler,
		[TYPEAHEAD.ACTIONS
			.DROPDOWN_PANEL_OPENED_SET]: typeAheadDropdownOpenedHandler,
		[COMPONENT_BOOTSTRAPPED]: {
			effect: transformState
		},
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				action: {
					payload: { name, value, previousValue }
				},
				dispatch
			} = context;
			if (name && name === "opened" && !_.isEqual(value, previousValue)) {
				dispatch(EDIT_TAG_MODAL.ACTIONS.LOAD_TAG_DETAILS);
			}
		}
	}
});

export default actions;
