import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-button";
import "@servicenow/now-tooltip";
import "@servicenow/now-legacy-icon";
import { SIDEBAR } from "../constants/constants";
import styles from "./sidebarMenuItem.scss";

const view = (state, { dispatch, updateState }) => {
	const {
		properties: { icon, name, label, selected, tooltip, badge },
		targetRef
	} = state;

	const setTargetRef = ref => {
		if (!targetRef) setTimeout(() => updateState({ targetRef: ref }));
		else if (!ref) setTimeout(() => updateState({ targetRef: null }));
	};

	const tooltipId = `tooltip-${name}`;

	return (
		<div
			hook={{
				destroy: () => setTargetRef(null),
				insert: ({ elm }) => setTargetRef(elm)
			}}
			on-click={() =>
				dispatch(SIDEBAR.INTERNAL_ACTIONS.ACTION_CLICKED, { name })
			}
			class={{ "sidebar-toolbar-menu-item": true, "-selected": selected }}
			tabIndex="0"
			aria-label={tooltip}
		>
			<span className="sidebar-icon-badge-wrapper">
				<now-icon
					className="sidebar-toolbar-menu-item-icon"
					icon={icon}
					size="md"
					weight="bold"
				/>
				{badge && (
					<now-icon
						icon="presence-fill"
						size="sm"
						className="sidebar-icon-presence"
					/>
				)}
			</span>
			<span
				className="sidebar-toolbar-menu-item-label"
				on-mouseover={({ target }) => {
					if (target.offsetWidth < target.scrollWidth) {
						target.title = label;
					}
				}}
			>
				{label}
			</span>
			{tooltip ? (
				<now-tooltip id={tooltipId} target-ref={targetRef} content={tooltip} />
			) : null}
		</div>
	);
};

createCustomElement("sn-form-internal-sidebar-menu-item", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		name: {
			reflect: true
		},
		label: {
			reflect: true
		},
		icon: {
			reflect: true
		},
		selected: {
			reflect: true,
			default: false,
			unstableParse: true
		},
		tooltip: {},
		badge: {}
	},
	styles
});
