import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import _ from "lodash";
import styles from "./sidebarPanel.scss";
import { AttachmentElement } from "../../attachments/AttachmentElement";
import { TemplateElement } from "../../templates/TemplateElement";

const { createElement } = snabbdom;

const getComponentId = (baseInstance, componentName) =>
	`${baseInstance}_${(componentName || "")
		.replace("sn-form-internal-", "")
		.replace("-", "")}`;

const view = state => {
	const {
		properties: {
			componentName,
			formModel,
			actionAttributes = {},
			classicForm,
			recordDisplayValue,
			sidebarPanelExtras
		},
		componentId
	} = state;

	if (!componentName) return null;

	const modelAttribute = formModel || {};

	return createElement(componentName, {
		...actionAttributes,
		model: modelAttribute,
		...formModel,
		"component-id": getComponentId(componentId, componentName),
		classicForm,
		recordDisplayValue,
		...(sidebarPanelExtras || {})
	});
};

createCustomElement("sn-form-internal-sidebar-panel", {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		componentName: {},
		formModel: {
			default: {}
		},
		actionAttributes: {
			default: {}
		},
		classicForm: {},
		recordDisplayValue: {},
		/**
		 * object whose properties will be spread and added to the components being rendered as additional attributes
		 * @type {SidebarPanelExtra}
		 */
		sidebarPanelExtras: {
			default: {}
		}
	}
});
