import keyBindingBehavior from "@servicenow/behavior-key-binding";
import { fitBehavior } from "@servicenow/behavior-fit";
import { TYPEAHEAD } from "./constants";
import { COMPONENT_TAG_TYPE_AHEAD_DROPDOWN_PANEL } from "../constants";
const isTriggeredFromInput = event =>
	event.composedPath()[0].nodeName === "INPUT";
const behaviors = [
	{
		behavior: fitBehavior,
		options: {
			position: [
				"bottom-start top-start",
				"top-start bottom-start",
				"bottom-end top-end",
				"top-end bottom-end"
			],
			constrain: {
				minHeight: 32,
				minWidth: "target",
				maxWidth: 300,
				maxHeight: 300
			},
			canScrollVertical: true
		}
	},
	{
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				open: ["ArrowDown"],
				dismiss: ["Escape", "Tab"],
				remove: ["Backspace"],
				focus: ["Enter", " "]
			},
			handlers: {
				open: function(host, event, dispatch) {
					if (!isTriggeredFromInput(event)) return;
					event.preventDefault();
					const dropDownResults = host.shadowRoot.querySelector(
						COMPONENT_TAG_TYPE_AHEAD_DROPDOWN_PANEL
					).shadowRoot;
					let listbox = dropDownResults.querySelector("ul");
					let nextFocus = dropDownResults.querySelector("li");
					if (nextFocus) {
						nextFocus.scrollIntoView();
						listbox.setAttribute("aria-activedescendant", nextFocus.id);
						nextFocus.focus();
					}
				},
				dismiss: function(host, event, dispatch) {
					if (host.opened) {
						event.stopPropagation();
						dispatch(TYPEAHEAD.ACTIONS.DROPDOWN_PANEL_OPENED_SET, {
							value: false
						});
						host.shadowRoot.querySelector(".typeahead-input").focus();
					}
				},
				remove: function(host, event, dispatch) {
					if (host.filter || host.selectedItems.length === 0) return;
					dispatch(TYPEAHEAD.ACTIONS.PILL_REMOVED, {
						name: host.fieldName,
						value: host.selectedItems[host.selectedItems.length - 1].value
					});
				},
				focus: function(host, event, dispatch) {
					if (host.opened) host.inputRef.focus();
				}
			}
		}
	}
];
export default behaviors;
