import { ACTIONS, LABELS } from "../constants";

const handleClick = (e, dispatch, properties) => {
	const { inputRef, sysId, table, displayValue: title } = properties;
	if (inputRef && inputRef.current) {
		inputRef.current.value = "";
		inputRef.current.focus();
	}
	const tagSysId = e.target.sysId;
	dispatch(ACTIONS.CREATE_TAG_LABEL, { tagSysId, table, sysId, title });
};

export const createListItems = (tags = [], dispatch, properties) => {
	return tags.map(({ sysId, name }, index) => (
		<li
			key={sysId}
			tabindex="-1"
			role="option"
			id={"id" + sysId}
			tabindex={-1}
			className="sn-form-tag-listItem"
			aria-label={name}
			aria-describedby={sysId + "_aria_describedby"}
			sysId={sysId}
			on-click={e => {
				handleClick(e, dispatch, properties);
			}}
		>
			<span className="aria-description" id={sysId + "_aria_describedby"}>
				{LABELS.ADD_TAG_SUGGESTION}
			</span>
			{name}
		</li>
	));
};
