import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import { TYPEAHEAD } from "./constants";
import {
	setFitOpened,
	setFitTarget,
	setFitContent
} from "@servicenow/behavior-fit";
import "./dropdownpanel/dropdownPanel";
import "@servicenow/now-pill";
import { escapeSeismicSpecialSymbols } from "../../../utils/seismicUtils";
const MultiSelectInput = ({
	label,
	dispatch,
	selectedItems,
	value,
	fieldName,
	defaultPillIcon,
	mainComponentId
}) => {
	return (
		<Fragment>
			{label && <label htmlFor={mainComponentId}>{label}</label>}
			<div
				hook={{
					insert: ({ elm }) => setFitTarget(dispatch, elm),
					remove: () => setFitTarget(dispatch, null)
				}}
				on-scroll={(event, { elm }) => setFitTarget(dispatch, elm)}
				on-wheel={(event, { elm }) => setFitTarget(dispatch, elm)}
				className="multi-select-container"
			>
				{selectedItems.map(item => {
					const pillIcon = item.icon ? "" : defaultPillIcon;
					const displayValue = escapeSeismicSpecialSymbols(item.displayValue);
					return (
						<now-pill
							className={"multiselect-pill"}
							size="sm"
							imageSrc={item.icon}
							iconName={pillIcon}
							manageSelected
							label={displayValue}
							canDismiss={true}
							append-to-payload={{ item }}
						/>
					);
				})}
				<input
					tabindex="0"
					id={mainComponentId}
					className="typeahead-input"
					value={value}
					hook={{
						insert: ({ elm }) => {
							dispatch("PROPERTIES_SET", { inputRef: elm });
						},
						remove: () => dispatch("PROPERTIES_SET", { inputRef: null })
					}}
					on-input={evt => {
						dispatch(TYPEAHEAD.ACTIONS.LOAD_ITEMS, {
							name: fieldName,
							prefix: evt.target.value.trim()
						});
					}}
				/>
			</div>
		</Fragment>
	);
};

const getFitStyle = (style, dropdownPanelEl) => {
	if (_.isEmpty(style)) return {};

	const contentEl =
		dropdownPanelEl && dropdownPanelEl.shadowRoot
			? dropdownPanelEl.shadowRoot.querySelector("section")
			: null;
	const height = style.height ? style.height.replace("px", "") * 1 : 0;
	const newHeight = contentEl
		? Math.min(Math.max(contentEl.clientHeight, height), 300)
		: 0;
	return {
		..._.omit(style, ["height", "width"]),
		maxHeight: newHeight ? `${newHeight}px` : style.height
	};
};

export const Typeahead = (state, dispatch) => {
	const {
		opened,
		availableItems = [],
		selectedItems = [],
		filter = "",
		label,
		dropdownLabel,
		fieldName,
		defaultPillIcon,
		dropdownPanelRef
	} = state.properties;
	const { fit } = state.behaviors;
	const fitStyle = getFitStyle(fit.style, dropdownPanelRef);
	return (
		<Fragment>
			<MultiSelectInput
				mainComponentId={state.componentId}
				fieldName={fieldName}
				state={state}
				selectedItems={selectedItems}
				label={label}
				value={filter}
				defaultPillIcon={defaultPillIcon}
				dispatch={dispatch}
			/>
			<sn-form-internal-dropdown-panel
				items={availableItems}
				label={dropdownLabel}
				opened={opened}
				component-name={fieldName}
				on-wheel={(event, { elm }) => {
					event.stopPropagation();
					setFitContent(dispatch, elm);
				}}
				hook={{
					update: (oldNode, newNode) => {
						const wasOpened = oldNode.data.props.opened;
						const opened = newNode.data.props.opened;
						const oldItems = oldNode.data.props.items.map(i => i.id).join();
						const newItems = newNode.data.props.items.map(i => i.id).join();
						if (wasOpened !== opened || (opened && oldItems !== newItems))
							setFitOpened(dispatch, opened);
						if (oldItems !== newItems || wasOpened !== opened)
							setFitContent(dispatch, newNode.elm);
					},
					insert: ({ elm }) => {
						if (elm && elm !== dropdownPanelRef) {
							dispatch("PROPERTIES_SET", { dropdownPanelRef: elm });
							setFitContent(dispatch, elm);
						}
					},
					destroy: () => setFitContent(dispatch, null),
					remove: () => {
						dispatch("PROPERTIES_SET", { dropdownPanelRef: null });
						setFitContent(dispatch, null);
					}
				}}
				style={fitStyle}
			/>
		</Fragment>
	);
};
