import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { createCustomElement, actionTypes } from "@servicenow/ui-core";
const { COMPONENT_DISCONNECTED } = actionTypes;
import {
	renderAttachmentUploadOverlay,
	renderAttachmentUploadModal
} from "./upload/attachmentUploadUtil";
import attachmentUploadModalActions from "./upload/attachmentUploadModalActions";
import attachmentOverlayActions from "./attachmentOverlayActions";
import { renderAttachmentOverlayView } from "./attachmentOverlayUtil";
import styles from "./attachmentOverlay.scss";

const view = (state, { dispatch, updateState }) => {
	const {
		showAttachmentUploadOverlay,
		showUploadFileModal,
		uploadAttachmentList,
		currentFileName,
		uploadAttachmentListIndex,
		uploadFileDataURL,
		properties
	} = state;
	const { recordDisplayValue, canUploadAttachment } = properties;

	const updateAttachmentName = ({ value: currentFileName }) => {
		updateState({ currentFileName });
	};

	const createFileDataURL = file => {
		var reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => {
			if (reader.result !== uploadFileDataURL)
				updateState({
					uploadFileDataURL: reader.result
				});
		};
	};

	return (
		<div>
			{canUploadAttachment && showAttachmentUploadOverlay
				? renderAttachmentUploadOverlay(recordDisplayValue)
				: null}

			{canUploadAttachment && showUploadFileModal
				? renderAttachmentUploadModal(
						showUploadFileModal,
						uploadAttachmentList,
						currentFileName,
						uploadAttachmentListIndex,
						updateAttachmentName,
						createFileDataURL,
						uploadFileDataURL
				  )
				: null}

			{renderAttachmentOverlayView(properties, dispatch, state)}
		</div>
	);
};

const ATTACHMENT_OVERLAY = "sn-form-internal-attachment-overlay";
createCustomElement(ATTACHMENT_OVERLAY, {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/*
		 * whether allow too drag and drop to upload file
		 */
		canUploadAttachment: { default: false },
		/*
		 * whether to allow to download only or rename + delete a file
		 */
		isReadOnly: { default: true },
		/*
		 * Record display value to show in the modal title
		 */
		recordDisplayValue: { default: "" },
		/*
		 * show attachment preview overlay if set to true
		 */
		showAttachmentPreview: { default: false },
		/*
		 * item to preview
		 */
		attachmentPreviewItem: {},
		/*
		 * table of the record
		 */
		table: {},
		/*
		 * sysId of the record
		 */
		sysId: {},
		/*
		 * current state of the doc viewer.
		 */
		docViewerState: {},
		/*
		 * whether to display document viewer or old attachment preview
		 */
		docViewerEnabled: {},
		/*
		 * upload the list of files specified.
		 */
		uploadFiles: { default: [] },
		/**
		 * Disable upload modal and directly upload the files
		 */
		disableUploadModal : { default : false }
	},
	actions: {
		...attachmentOverlayActions.actions,
		...attachmentUploadModalActions.actions
	},
	actionHandlers: {
		...attachmentOverlayActions.handlers,
		...attachmentUploadModalActions.actionHandlers
	},
	[COMPONENT_DISCONNECTED]: ({ state }) => {
		const { dragHandlers, dropTarget } = state;
		if (!dropTarget || !dragHandlers) return;
		if (dragHandlers.onDragEnter)
			dropTarget.removeEventListener("dragenter", dragHandlers.onDragEnter);
		if (dragHandlers.onDragOver)
			dropTarget.removeEventListener("dragover", dragHandlers.onDragOver);
		if (dragHandlers.onDragLeave)
			dropTarget.removeEventListener("dragleave", dragHandlers.onDragLeave);
		if (dragHandlers.onDrop)
			dropTarget.removeEventListeners("drop", dragHandlers.onDrop);
	}
});

export default ATTACHMENT_OVERLAY;
