import react from "@servicenow/ui-renderer-react";
import React from "react";
import { t } from "sn-translate";
import _ from "lodash";
import SearchbarElement from "../searchbar";
import { keyUtils } from "@devsnc/library-form";
import "@servicenow/now-tabs";
import "@servicenow/now-tooltip";
import {
	TEMPLATE_ACTIONS,
	TEMPLATE_MENU_ACTIONS,
	SET_FIELDS_MSG,
	MAX_GRID,
	BASE_GRID,
	TEMPLATE_LIMIT_SYS_PROP,
	TEMPLATE_TAB_ITEMS,
	MORE_ARIA_LABEL,
	TEMPLATE_EVENT_SOURCE
} from "../constants/templateConstants";
import { ADD_NOTIFICATIONS, CLEAR_NOTIFICATIONS } from "../constants/constants";
import { getProperty } from "sn-uxpage-presource";
import "@servicenow/now-dropdown";
import "@servicenow/now-modal";

import { EmptyStateElement } from "../emptyState";

const hasField = (fields, name) =>
	_.find(
		fields,
		!!(field => field.variable_name === name || field.name === name)
	);

export const getPaginationObject = () => {
	return { limit: getProperty(TEMPLATE_LIMIT_SYS_PROP, 100) };
};

const createActionMenu = item => {
	const canDelete = _.get(item, "_row_data.canDelete", false);
	const canWrite = _.get(item, "_row_data.canWrite", false);
	const sysId = _.get(item, "sys_id.value");
	if (!sysId) return [];

	let actions = [];
	if (canWrite)
		actions.push({
			...TEMPLATE_MENU_ACTIONS.EDIT,
			sysId
		});
	if (canDelete)
		actions.push({
			...TEMPLATE_MENU_ACTIONS.DELETE,
			sysId
		});

	return actions;
};

export const constructQueryString = (table, searchQuery, selectedTab = "all") => {
	let qs = "table=" + table + "^active=true^show_on_template_bar=true^user=javascript:gs.getUserID()";
	
	if (selectedTab === "all") {
		qs += "^ORglobal=true";
		qs += "^ORrolesINjavascript:GlideTemplate.getUserRolesForCondition()";
		qs += "^ORgroups=javascript:getMyGroups()";
	}
	
	if (searchQuery) 
		qs += "^nameLIKE" + searchQuery.toLowerCase();
	
	return qs + "^ORDERBYname";
};

class TemplateList extends React.Component {
	constructor(props) {
		super(props);
		this.applyTemplate = this.applyTemplate.bind(this);
		this.getTemplateData = this.getTemplateData.bind(this);
		this.onClickItem = this.onClickItem.bind(this);
		this.refTemplate = this.refTemplate.bind(this);
		this.templateRefs = {}; // a hash of {template_sys_id: nodeRef}
		this.templates = []; // nextTemplates becomes templates at the end of componentDidUpdate()
		this.nextTemplates = []; // every time render() is called, nextTemplates is set with a new array
		this.state = {
			templates: [] // only used for setting this.state to triggerring render. the value is never used
		};
	}

	componentWillMount() {
		if (!this.props.properties.table) return;

		const queryConditions = constructQueryString(this.props.properties.table);
		const paginationVar = getPaginationObject();

		this.props.dispatch(TEMPLATE_ACTIONS.TEMPLATE_FETCH_ACTION, {
			queryConditions,
			paginationVar
		});
	}

	triggerRenderIfNecessary() {
		// if the templates just got rendered are a subset of the templates from the previous render,
		// no need setState to trigger re-render since the templateRefs already exist for these template DOMs
		if (!this.nextTemplates.every(t => this.templates.includes(t))) {
			this.setState({
				templates: this.nextTemplates
			});
		}
	}

	componentDidUpdate() {
		this.triggerRenderIfNecessary();
		this.templates = this.nextTemplates;
	}

	onClickItem(e) {
		if (e.target.tagName === "NOW-DROPDOWN") {
			this.clearAllMessages();
			return;
		}
		if (e.type === "keypress") {
			if (!keyUtils.onSpace(e) && !keyUtils.onEnter(e)) {
				return;
			}
			e.preventDefault();
		}
		this.clearAllMessages();
		const currentTarget = e.currentTarget;
		this.getTemplateData(currentTarget.getAttribute("data-sys-id"));
	}

	clearAllMessages() {
		this.props.dispatch(CLEAR_NOTIFICATIONS, {
			notificationAction: {
				type: "clear"
			}
		});
	}

	getTemplateData(templateSysId) {
		if (this.templateApplied !== templateSysId) {
			const { sysId: targetSysId, encodedRecord } = this.props.properties;
			this.props.dispatch(TEMPLATE_ACTIONS.TEMPLATE_APPLY_ACTION, {
				sysId: templateSysId,
				targetSysId,
				encodedRecord
			});
			this.templateApplied = templateSysId;
		}
	}

	transformUntrustedAndLongData(message) {
		if (message) message = message.replace(/<(?:.|\n)*?>/gm, "");
		if (message && message.length > 254) {
			message = message.substr(0, 254) + "...";
		}

		return message;
	}

	applyTemplate(data) {
		let appliedTemplate = false;
		let messages = [];
		const { fields } = this.props.properties;
		if (!fields) return;

		const {
			templateName,
			data: templateValue,
			conflicts = []
		} = data.GlideLayout_Query.templateDataRetreiver;
		let [conflictedFields, itemsNotPresent] = _.partition(conflicts, entry =>
			hasField(fields, entry.name)
		);
		if (templateValue) {
			templateValue.forEach(entry => {
				const { name, label } = entry;
				if (name === "") {
					appliedTemplate = true;
					return;
				}
				const field = _.find(
					fields,
					field => field.variable_name === name || field.name === name
				);
				if (field) {
					const isFieldReadOnly =
						field.sys_readonly === true || field.readonly === true;
					if (isFieldReadOnly) {
						conflictedFields.push(entry);
					} else {
						this.applyTemplateToField(entry);
						appliedTemplate = true;
						const fieldDisplayMessage = this.getDisplayMessage(entry);
						messages.push(
							t(
								"<b>{0}</b> to <b>{1}</b>",
								label,
								this.transformUntrustedAndLongData(fieldDisplayMessage)
							)
						);
					}
				} else {
					itemsNotPresent.push(entry);
				}
			});
		}

		conflictedFields.forEach(entry => {
			messages.push(`${t("Unable to update field")} <b>${entry.label}</b>`);
		});

		const setFieldsMessage =
			messages.length > 0
				? `<br><br>${SET_FIELDS_MSG}<br>${messages.join("<br>")}`
				: "";
		const missingItemsMessage = t(
			"{0} items not present on this form",
			itemsNotPresent.length
		);
		const itemsNotPresentMessage =
			itemsNotPresent.length > 0
				? `<br><br>${missingItemsMessage}<br><b>${itemsNotPresent
						.map(entry => entry.label)
						.join(", ")}</b>`
				: "";
		let templateAppliedMessage = "";
		let messageType;
		const itemsNotUpdated = conflictedFields.length + itemsNotPresent.length;
		if (appliedTemplate) {
			if (itemsNotUpdated > 0) {
				templateAppliedMessage = t(
					"{0} Template Applied with {1} conflicts",
					templateName,
					itemsNotUpdated
				);
				messageType = "warning";
			} else {
				templateAppliedMessage = t("{0} Template Applied", templateName);
				messageType = "info";
			}
		} else {
			templateAppliedMessage = t("{0} Template Not Applied", templateName);
			messageType = "error";
		}
		const notifyMessage = `${templateAppliedMessage}${itemsNotPresentMessage}${setFieldsMessage}`;
		this.notify(messageType, notifyMessage);
	}

	getDisplayMessage(field) {
		const { displayValue, type, value } = field;
		let displayVal = value ? displayValue : "";
		switch (type) {
			case "field_list":
				displayVal = displayVal.slice(1);
				break;
			case "currency":
				displayVal = displayVal.replace(";", " ");
				break;
		}

		return displayVal ? displayVal : `<i>${t("None")}</i>`;
	}

	applyTemplateToField(field) {
		const { name, value, displayValue, type } = field;
		const { dispatch } = this.props;
		const fieldDisplayValue =
			type === "glide_list" ? displayValue.split(",") : displayValue;
		const fieldValue =
			type === "glide_time" || type === "glide_date_time"
				? displayValue
				: value;
		dispatch("GFORM_INTERNAL#APPLY_TEMPLATE_VALUE", {
			fieldName: name,
			value: fieldValue,
			displayValue: fieldDisplayValue
		});
	}

	notify(type, message) {
		this.props.dispatch(ADD_NOTIFICATIONS, {
			notifications: [
				{
					type,
					message
				}
			]
		});
	}

	isDataSourceEmpty() {
		return !this.props.templateData;
	}

	componentWillReceiveProps(props) {
		const path =
			"applyTemplate.data.GlideLayout_Query.templateDataRetreiver.sysId";
		if (props.applyTemplate) {
			let templateSysId = _.get(props, path);
			if (templateSysId && templateSysId === this.templateApplied) {
				this.applyTemplate(props.applyTemplate.data);
				this.templateApplied = null;
			}
		}
	}

	addSearchFilter(canCreate) {
		const { searchFilter, selectedTab } = this.props;
		const value = _.get(searchFilter, selectedTab, null);
		return (
			<div className="sn-panel-header-search">
				<div className="sn-panel-header-content">
					<SearchbarElement
						placeholder={t("Filter Templates")}
						value={value}
						caseSensitive={true}
					/>
					{canCreate && (
						<now-button-iconic
							icon="plus-fill"
							size="md"
							variant="secondary"
							configAria={{ "aria-label": t("Create template") }}
							tooltip-content={t("Create template")}
							append-to-payload={{
								eventSource: TEMPLATE_EVENT_SOURCE.NEW_TEMPLATE_ICON
							}}
						/>
					)}
				</div>
			</div>
		);
	}

	renderOldEmptyState() {
		const isWorkspace = this.props.classicForm ? " -workspace" : "";
		return (
			<div className={`sn-empty-state${isWorkspace}`}>
				<div className="sn-empty-state-row">
					<div className="sn-empty-state-img -no-template -old" />
				</div>
				<h3 className="sn-empty-state-row -header">
					{t("No templates are available")}
				</h3>
				<div className="sn-empty-state-row">
					{t("There are no available templates for this record type.")}
				</div>
			</div>
		);
	}

	renderEmptyState(canCreate) {
		const isWorkspace = this.props.classicForm ? " -workspace" : "";
		return (
			<div className={`sn-empty-state${isWorkspace}`}>
				<div className="sn-empty-state-row">
					<div className="sn-empty-state-img -no-template" />
				</div>
				<h3 className="sn-empty-state-row -header">
					{t("No Templates Available")}
				</h3>
				{canCreate && (
					<div className="sn-empty-state-row">
						<now-button
							label={t("Create")}
							configAria={{ "aria-label": t("Create template") }}
							size="md"
							variant="primary"
							append-to-payload={{
								eventSource: TEMPLATE_EVENT_SOURCE.NEW_TEMPLATE_BUTTON
							}}
						/>
					</div>
				)}
			</div>
		);
	}

	renderNoMatchFound() {
		const isWorkspace = this.props.classicForm ? " -workspace" : "";
		return (
			<div className={`sn-empty-state${isWorkspace}`}>
				<div className="sn-empty-state-row">
					<div className="sn-empty-state-img -no-match" />
				</div>
				<h3 className="sn-empty-state-row -header">{t("No Match Found")}</h3>
				<div className="sn-empty-state-row">
					{t("We couldn't find what you were looking for.")}
				</div>
			</div>
		);
	}

	refTemplate(node) {
		if (!node) return;
		const sysId = node.parentNode.getAttribute("data-sys-id");
		this.templateRefs[sysId] = node;
	}

	getAriaAttributes(item) {
		return {
			configAria: {
				"aria-label": `${MORE_ARIA_LABEL} ${item.name.value}`,
				"aria-haspopup": "listbox"
			}
		};
	}

	renderMenu = item => {
		const componentIdPrefix = this.props.componentId;
		const sysId = _.get(item, "sys_id.value");
		if (!sysId) return null;

		const actions = createActionMenu(item);
		if (!actions.length) return null;
		const componentId = `${componentIdPrefix}_menu_${sysId}`;

		return (
			<div className="sn-card-content -dropdown">
				<now-dropdown
					bare
					hide-caret
					icon="ellipsis-v-outline"
					items={actions}
					select="none"
					manage-selecteditems
					size="sm"
					variant="secondary"
					tooltip-content={t('Menu for {0}', item.name.value)}
					{...this.getAriaAttributes(item)}
					component-name={componentId}
					append-to-payload={{
						template: item,
						eventSource: TEMPLATE_EVENT_SOURCE.TEMPLATE_MENU_ITEM
					}}
				/>
			</div>
		);
	};

	renderTemplateItem(item) {
		const tooltipId = `tooltip-${item.name.value}`;
		return (
			<div
				key={item.sys_id.value}
				role="button"
				tabIndex="0"
				className="sn-card hover-click"
				data-sys-id={item.sys_id.value}
				onClick={this.onClickItem}
				onKeyPress={this.onClickItem}
			>
				<div className="sn-card-content" ref={this.refTemplate}>
					<span className="sn-card-content-title">
						<strong>{item.name.value}</strong>
					</span>
					<span>{item.short_description.displayValue}</span>
					<now-tooltip
						id={tooltipId}
						target-ref={this.templateRefs[item.sys_id.value]}
						content={item.name.value}
					/>
				</div>
				{this.renderMenu(item)}
			</div>
		);
	}

	renderDeleteModal = (displayDeleteModal) => {
		const tabData = _.get(this.props, "templateData");

		if (!tabData[this.props.selectedTab]) return null;

		const templateGQLData = _.get(
			tabData[this.props.selectedTab],
			"data.GlideRecord_Query.sys_template._results"
		);
		if (!templateGQLData) return null;

		const deletingTemplate = _.get(this.props, "deletingTemplate", false);
		if (!deletingTemplate) return null;

		const name = _.get(deletingTemplate, "name.value", "");
		const modalProps = {
			headerLabel: t("Delete"),
			content: t(
				"Are you sure you want to delete this template {0}?  Changes cannot be undone.",
				name ? `(${name})` : ""
			),
			footerActions: [
				{
					label: t("Cancel"),
					clickActionType: TEMPLATE_ACTIONS.DELETE_CANCELED_ACTION
				},
				{
					label: t("Delete"),
					variant: "primary-negative",
					clickActionType: TEMPLATE_ACTIONS.DELETE_CONFIRMED_ACTION
				}
			],
			appendToPayload: {
				eventSource: TEMPLATE_EVENT_SOURCE.REMOVE_TEMPLATE_MODAL,
				template: deletingTemplate
			}
		};

		return (
			<now-modal
				opened={displayDeleteModal}
				manage-opened
				{...modalProps}
				size="md"
			/>
		);
	};

	render() {
		if (this.isDataSourceEmpty()) return null;

		const {
			properties: { table, sysId, classicForm }
		} = this.props;

		/** Add this part for UIB and workspace versions specifically */
		const isWorkspace = classicForm ? " -workspace" : "";

		if (!classicForm && (!table || !sysId)) {
			return <EmptyStateElement message={t("No data to display")} />;
		}

		const templateGQLData = this.props.templateData[this.props.selectedTab]
			? this.props.templateData[this.props.selectedTab].data.GlideRecord_Query
					.sys_template._results
			: [];

		const canCreate = _.get(
			this.props,
			`templateData[${
				this.props.selectedTab
			}].data.GlideRecord_Query.sys_template._table_metadata.canCreate`,
			false
		);
		const { searchFilter, selectedTab } = this.props;
		const filter =
			searchFilter[selectedTab] || templateGQLData.length > 0
				? this.addSearchFilter(canCreate)
				: null;
		let templateList = null;
		if (templateGQLData.length == 0) {
			templateList = searchFilter[selectedTab]
				? this.renderNoMatchFound()
				: this.renderEmptyState(canCreate);
		} else {
			const fillGrid = () => {
				const totalItems = templateGQLData.length;
				const idealGridSize = Math.ceil(totalItems / BASE_GRID) * BASE_GRID;
				const gridToFill = (idealGridSize % totalItems) + MAX_GRID;
				return _.range(gridToFill).map(() => (
					<div className="sn-card-filler" />
				));
			};

			templateList = (
				<div className={`sn-card-list${isWorkspace}`}>
					{templateGQLData.map(i => this.renderTemplateItem(i))}
					{fillGrid()}
				</div>
			);
		}
		this.nextTemplates = templateGQLData.map(i => i.sys_id.value);

		const displayDeleteModal = _.get(this.props, "displayDeleteModal", false);

		return (
			<div className={`sn-panel${isWorkspace}`}>
				<div className={`sn-panel-header${isWorkspace}`} 
				{...(displayDeleteModal && { ariaHidden: displayDeleteModal })} >
					<h2 className="sn-panel-header-title">{t("Templates")}</h2>
					<now-tabs items={TEMPLATE_TAB_ITEMS} selectedTab={selectedTab} />
					{filter}
				</div>
				<div className={`sn-panel-body${isWorkspace}`}>{templateList}</div>
				{displayDeleteModal ? this.renderDeleteModal(displayDeleteModal) : null}
			</div>
		);
	}
}

export default TemplateList;
