import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./tagDropdown.scss";
import { createListItems } from "./tagDropdownUtils";
import behaviors from "./tagDropdownBehaviors";
import { LABELS, COMPONENT_TAG_DROPDOWN } from "../constants";

const view = (state, { dispatch }) => {
	const {
		properties,
		properties: { opened, tags, activeDescendant }
	} = state;
	const listItems = createListItems(tags, dispatch, properties);
	const resultsPluralState = tags.length === 1 ? LABELS.RESULT : LABELS.RESULTS;
	const ariaDropdownDescription = tags.length
		? `${tags.length} ${resultsPluralState}. ${LABELS.ADD_TAG_SUGGESTION}`
		: "";

	if (!listItems.length) return null;

	return (
		<div className="sn-form-tag-dropdown" hidden={!opened}>
			<span
				id={LABELS.TAG_DROPDOWN_ARIA_DESCRIPTION_ID}
				className="aria-description"
				aria-atomic="true"
				aria-live="polite"
			>
				{ariaDropdownDescription}
			</span>
			<ul
				className="sn-form-tag-list"
				role="listbox"
				aria-controls={LABELS.ADD_TAG_INPUT_ID}
				id={LABELS.TAG_DROPDOWN_ID}
				aria-describedby={LABELS.TAG_DROPDOWN_ARIA_DESCRIPTION_ID}
				aria-activedescendant={activeDescendant}
			>
				{listItems}
			</ul>
		</div>
	);
};

export default createCustomElement(COMPONENT_TAG_DROPDOWN, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		opened: { default: false },
		table: { default: "" },
		sysId: { default: "" },
		inputRef: { default: null },
		tags: { default: [] },
		displayValue: { default: "" },
		activeDescendant: { default: "" }
	},
	styles,
	behaviors
});
