import { createCustomElement } from "@servicenow/ui-core";
import react from "@servicenow/ui-renderer-react";
import _ from "lodash";

import UIActionBar from "./UIActionBar";

const UI_ACTION_BAR_ELEMENT = "now-record-common-uiactionbar";
const DA_WRAPPED_CLICKED = "DA_WRAPPED_CLICKED";

export const daWrapClickHandler = ({ action: { payload }, dispatch }) => {
	const { wrapped_action_name, wrapped_payload } = payload;
	if (wrapped_action_name) dispatch(wrapped_action_name, wrapped_payload);
};

/**
 * Contains buttons and menu items that allow users to interact with and take actions on a page. 
 *
 * ```jsx
 * <now-record-common-uiactionbar
 *		uiActions={...}
 *		width={...}
 *		handleClick={...}
 *		/>
 * ```
 *
 * @seismicElement now-record-common-uiactionbar
 * @summary Set of horizontal buttons mapped to UI actions that fits the size of its container.
 */
createCustomElement(UI_ACTION_BAR_ELEMENT, {
	renderer: {
		type: react,
		view: UIActionBar
	},
	properties: {
		/**
		 * Array of action nodes that map to components that execute when the user clicks them. For example, now-button, now-split-button, or now-dropdown.
		 * @type {(Array.<ActionNode>)}
		 */
		actionNodes: {},
		/**
		 * Minimum number of UI actions to display without overflowing.
		 * @type {{Number}}
		 */
		fixedButtonNumber: {
			default: 0,
			schema: {
				type: 'number'
			}
		},
		/**
		 * Object containing the data needed for all of the components linked to the UI actions.
		 * @type {SidebarFormModel}
		 */
		daModel: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 * Object containing the data needed for dynamic declarative action event mapping.
		 * @type {DaClientActionContract}
		 */
		daClientActionContract: {
			default: {},
			schema: {
				type: 'object'
			},
			selectable: true
		},
		/**
		 * Performs an action asynchronously and executes the callback on completion.
		 * @type {UIActionClickCallback} handleClick - Optional if pass is called when the UI action is clicked;
		 * otherwise, NOW_RECORD_COMMON_UI_ACTION_BAR#CLICKED is dispatched with the selectedAction
		 */
		handleClick: {},
		/**
		 * Width of the parent container. When this values changes, the action bar recalculates which buttons are shown in the dropdown menu.
		 *
		 * @type {(string)}
		 */
		width: {},
		/**
		 * When true, the action bar renders in the workspace form.
		 * @type {boolean}
		 */
		classicForm: {}
	},
	actionHandlers: {
		[DA_WRAPPED_CLICKED]: {
			effect: daWrapClickHandler,
			stopPropagation: true
		},
		REFRESH_REQUESTED: {
			effect: ({ dispatch }) => {
				dispatch('NOW_RECORD_UI_ACTION_BAR#REFRESH_REQUESTED');
			},
			stopPropagation: true
		}
	}
});

export default UI_ACTION_BAR_ELEMENT;
