const SessionMessageFragment = `
sessionQuery: GlideDomain_Query {
	session {
		notifications {
			type
			text
			notificationType
			notificationAttributes {
				attributeName
				attributeValue
			}
			childNotifications {
				type
				text
				notificationType
			}
		}
	}
}
`;

const AttachmentGraphQLQuery = `
query ($table: String!, $sysId: String!) {
	query: GlideAttachmentQuery_Query {
		model: attachments(table: $table, sysId: $sysId) {
			attachments: records{
				name
				userId
				userName
				date
				dateLabel
				type
				state
				size
				sysId
				canEditName
				canDelete
			}
			count
			types
			docViewerEnabled
			extensions
			canCreate
			fileNameLength
		}
	}
	
	${SessionMessageFragment}
}
`;

export { AttachmentGraphQLQuery };
