import { createCustomElement } from "@servicenow/ui-core";
import snabbdom, { Fragment } from "@servicenow/ui-renderer-snabbdom";
import styles from "./editTagModal.scss";
import actions from "./editTagModalActions";
import { EDIT_TAG_MODAL, USERS, GROUPS } from "./constants";
import {
	VIEWABLE_BY,
	COMPONENT_EDIT_TAG_MODAL,
	COMPONENT_TAG_TYPE_AHEAD
} from "../constants";
import "../typeahead/typeahead";
import TagDetails from "./tagDetails";
import behaviors from "./editTagModalBehaviors";
import { escapeSeismicSpecialSymbols } from "../../../utils/seismicUtils";
import _ from "lodash";

const renderModalContent = (tagDetails, opened, dispatch) => {
	const { name, viewableBy, userList, groupList } = tagDetails;

	return (
		<Fragment>
			<div className="sn-component-edit-tag-modal-twocol-container">
				<div className="sn-component-edit-tag-modal-left">
					<sn-record-input-connected
						name={name.fieldName}
						label={name.label}
						initialValue={escapeSeismicSpecialSymbols(name.value)}
						hidden={!(opened && tagDetails.canEdit)}
						onValueChange={(event, name, value) => {
							dispatch(EDIT_TAG_MODAL.ACTIONS.FIELD_CHANGED, { name, value });
						}}
					/>
				</div>
				<div className="sn-component-edit-tag-modal-right">
					<sn-record-choice
						name={viewableBy.fieldName}
						label={viewableBy.label}
						value={viewableBy.value}
						choices={Object.values(VIEWABLE_BY)}
						hidden={!(opened && tagDetails.canEdit)}
						onValueChange={(event, name, value) => {
							dispatch(EDIT_TAG_MODAL.ACTIONS.FIELD_CHANGED, { name, value });
						}}
					/>
				</div>
			</div>
			<div className="sn-component-edit-tag-modal-twocol-container">
				<div className="sn-component-edit-tag-modal-left">
					<COMPONENT_TAG_TYPE_AHEAD
						label={groupList.typeaheadLabel}
						dropdownLabel={groupList.dropdownLabel}
						hidden={!tagDetails.isViewableBySetToUsersAndGroups}
						fieldName={groupList.fieldName}
						filter={escapeSeismicSpecialSymbols(groupList.filter)}
						selectedItems={groupList.selected}
						total={groupList.total}
						availableItems={groupList.nonSelected}
						opened={opened && groupList.dropdownOpened}
						component-name={groupList.fieldName}
					/>
				</div>
				<div className="sn-component-edit-tag-modal-right">
					<COMPONENT_TAG_TYPE_AHEAD
						label={userList.typeaheadLabel}
						dropdownLabel={userList.dropdownLabel}
						hidden={!tagDetails.isViewableBySetToUsersAndGroups}
						defaultPillIcon="user-outline"
						fieldName={userList.fieldName}
						filter={escapeSeismicSpecialSymbols(userList.filter)}
						selectedItems={userList.selected}
						total={userList.total}
						availableItems={userList.nonSelected}
						opened={opened && userList.dropdownOpened}
						component-name={userList.fieldName}
					/>
				</div>
			</div>
		</Fragment>
	);
};

const view = (state, { dispatch }) => {
	const { opened } = state;
	const tagDetails = new TagDetails({ ...state });

	if (!(opened && tagDetails.canEdit)) return null;

	const footerActions = [
		{
			variant: EDIT_TAG_MODAL.BUTTONS.PRIMARY,
			label: EDIT_TAG_MODAL.LABELS.SAVE,
			clickActionType: EDIT_TAG_MODAL.ACTIONS.SAVE,
			disabled: !_.trim(tagDetails.name.value) ? "true" : false
		},
		{
			variant: EDIT_TAG_MODAL.BUTTONS.SECONDARY,
			label: EDIT_TAG_MODAL.LABELS.CANCEL,
			clickActionType: EDIT_TAG_MODAL.ACTIONS.CANCEL
		}
	];

	return (
		<now-modal
			component-name="modal2"
			opened={opened && tagDetails.canEdit}
			manage-opened
			size="md"
			header-label={EDIT_TAG_MODAL.LABELS.TITLE}
			footer-actions={footerActions}
		>
			<div className="sn-component-edit-tag-modal-container">
				{renderModalContent(tagDetails, opened, dispatch)}
			</div>
		</now-modal>
	);
};

export default createCustomElement(COMPONENT_EDIT_TAG_MODAL, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		tagId: {},
		opened: false,
		[USERS]: {
			total: 0,
			dropdown: false,
			filter: "",
			available: []
		},
		[GROUPS]: {
			total: 0,
			dropdown: false,
			filter: "",
			available: []
		},
		tagDetails: {
			name: {},
			viewable_by: {},
			user_list: {},
			group_list: {}
		}
	},
	properties: {
		tagId: {},
		opened: {
			default: false
		}
	},
	...actions,
	styles,
	behaviors
});
