import { LOAD_EXTERNAL_START, LOAD_EXTERNAL_COMPLETED } from './actions';
import { actionTypes } from '@servicenow/ui-core';
import {
	loadComponents,
	createReDispatchActionHandler,
	transformPreviewRecordPayload
} from '@devsnc/library-form';
import { getTagsNotInCache, getWigetTagNames } from './utils/utils';
import {
	RIBBON_ACTIONS,
	RIBBON_OPEN_PHONE,
	RIBBON_PREVIEW_RECORD,
	RIBBON_ADD_NOTIFICATIONS,
	RIBBON_REMOVE_NOTIFICATIONS,
	RIBBON_CLEAR_NOTIFICATIONS,
	RIBBON_ITEM_SELECTED,
	RIBBON_EXECUTED_UI_ACTION,
	RIBBON_WRAPPED_ACTION
} from '../constants/ribbonConstants';

const loadExternalWidgetsEffect = ({
	dispatch,
	state,
	action: {
		payload: { tagNames }
	}
}) => {
	loadComponents(tagNames).finally(() => {
		dispatch(LOAD_EXTERNAL_COMPLETED, { tagNames });
	});
};

const loadExternalWidgetsCompletedEffect = ({
	updateState,
	state,
	action: {
		payload: { tagNames }
	}
}) => {
	updateState({
		widgetsLoading: false,
		tagsLoaded: [...state.tagsLoaded, ...tagNames]
	});
};

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
		const tagNames = getWigetTagNames(properties.widgets);
		if (tagNames.length) {
			dispatch(LOAD_EXTERNAL_START, { tagNames });
		}
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		effect(context) {
			const {
				dispatch,
				action: {
					payload: { name, value }
				},
				state: { tagsLoaded }
			} = context;
			if (name === 'widgets') {
				const tagsNotInCache = getTagsNotInCache(tagsLoaded, value);
				if (tagsNotInCache.length) {
					dispatch(LOAD_EXTERNAL_START, { tagNames: tagsNotInCache });
				}
			}
		}
	},
	[LOAD_EXTERNAL_START]: loadExternalWidgetsEffect,
	[LOAD_EXTERNAL_COMPLETED]: loadExternalWidgetsCompletedEffect,
	[RIBBON_ACTIONS.OPEN_PHONE]: createReDispatchActionHandler(RIBBON_OPEN_PHONE),
	[RIBBON_ACTIONS.PREVIEW_RECORD]: createReDispatchActionHandler(
		RIBBON_PREVIEW_RECORD,
		transformPreviewRecordPayload
	),
	[RIBBON_ACTIONS.ADD_NOTIFICATIONS]: createReDispatchActionHandler(RIBBON_ADD_NOTIFICATIONS),
	[RIBBON_ACTIONS.REMOVE_NOTIFICATIONS]: createReDispatchActionHandler(RIBBON_REMOVE_NOTIFICATIONS),
	[RIBBON_ACTIONS.CLEAR_NOTIFICATIONS]: createReDispatchActionHandler(RIBBON_CLEAR_NOTIFICATIONS),
	[RIBBON_ACTIONS.ITEM_SELECTED]: createReDispatchActionHandler(RIBBON_ITEM_SELECTED),
	[RIBBON_ACTIONS.EXECUTED_UI_ACTION]: createReDispatchActionHandler(RIBBON_EXECUTED_UI_ACTION),
	[RIBBON_ACTIONS.WRAPPED_ACTION]: createReDispatchActionHandler(RIBBON_WRAPPED_ACTION)
};
