import keyBindingBehavior from "@servicenow/behavior-key-binding";
import { ACTIONS, COMPONENT_TAG_DROPDOWN } from "../constants";

const isTriggeredFromInput = event =>
	event.composedPath()[0].nodeName === "INPUT";
const behaviors = [
	{
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				goToResults: ["ArrowDown"],
				closeDropdown: ["Tab"]
			},
			handlers: {
				goToResults: function(host, event, dispatch) {
					if (!isTriggeredFromInput(event)) return;
					const dropDownResults = host.shadowRoot.querySelector(
						COMPONENT_TAG_DROPDOWN
					).shadowRoot;
					let nextFocus = dropDownResults.querySelector("li");
					if (nextFocus) {
						event.preventDefault();
						nextFocus.scrollIntoView();
						dispatch(ACTIONS.ADD_TAG_DESCENDANT_SELECTED, {
							activeDescendant: nextFocus.id
						});
						nextFocus.focus();
					}
				},
				closeDropdown: function(host, event, dispatch) {
					if (!isTriggeredFromInput(event)) return;
					dispatch(ACTIONS.ADD_TAG_DESCENDANT_SELECTED, {
						activeDescendant: ""
					});
					dispatch(ACTIONS.TAG_LIST_CLICKED, { openDropDown: false });
				}
			}
		}
	}
];
export default behaviors;
