import { createHeaderConfigurationDataSource } from "./datasource/configurationDataSource";
import { createDataProvider } from "@devsnc/library-form";

const HEADER_CONFIGURATION_DATA_PROVIDER = "header-configuration-data-provider";
const configurationDataSource = createHeaderConfigurationDataSource();
export const createHeaderConfigurationDataProvider = () =>
	createDataProvider({
		name: HEADER_CONFIGURATION_DATA_PROVIDER,
		dataSources: [configurationDataSource]
	});
