export const ADD_TAG_QUERIES = {
	TAG_PREFIX_GET_QUERY: `query($prefix: String!) {
        GlideViewableTagQuery_Query {
            viewableUserTags(prefix: $prefix) {
                records {
                    name
                    sysId
                    viewableBy
                }
            }
        }
    }`,
	TAG_INSERT_LABEL_ENTRY: `mutation($tagSysId: String!, $table: String!, $sysId: String!, $title: String!) {
        GlideRecord_Mutation {
            insert_label_entry(label: $tagSysId, table: $table, table_key: $sysId, read: "yes", title: $title) {
                sys_id {
                    value
                }
            }
        }
    }`,
	TAG_CREATE_LABEL: `mutation($text: String!) {
        GlideRecord_Mutation {
            insert_label(name: $text) {
                sys_id {
                    value 
                }
            }
        }
    }`
};
