// Common methods used in Header js files

/**
 * Returns true on: null, undefined, and empty string
 * Returns false on: 0, 1, true, false, 'non-empty string'
 *
 * @param {object} Any object
 * @return {boolean}
 * @private
 */
export const _isEmpty = a => {
	return a === undefined || a === null || String(a) === "";
};

/**
 * Validates if any passed in property is missing per the configuration in acceptedProperites
 *
 * @param state The current state object
 * @param acceptedProperties An object of properties based on Seismic createCustomElement properties format
 *                           With an additional (option) field of required
 * @returns {{[p: string]: *}}
 * @private
 */
export const _addMissingRequiredProperty = (state, acceptedProperties) => {
	const { properties } = state;
	let missingRequiredProperty = false;
	Object.keys(acceptedProperties).forEach(function(key) {
		const valueIsEmpty = _isEmpty(properties[key]);
		const required = acceptedProperties[key].required;
		if (required === true && valueIsEmpty) missingRequiredProperty = true;
		else if (typeof required === "object") {
			Object.keys(required).forEach(function(rkey) {
				const conditionTrue = properties[rkey] === required[rkey];
				if (conditionTrue && valueIsEmpty) missingRequiredProperty = true;
			});
		}
	});
	return { ...state, properties: { ...properties, missingRequiredProperty } };
};
