import _ from "lodash";

export const getLabelValuePairContainer = toggleContainerRef => {
	if (!toggleContainerRef || _.isEmpty(toggleContainerRef.firstChild))
		return null;
	const itemsRef = toggleContainerRef.querySelector("now-label-value-stacked");

	if (!itemsRef || !itemsRef.shadowRoot) return null;

	const dl = itemsRef.shadowRoot.querySelector("dl");

	return dl;
};

export const getLabelValuePairs = toggleContainerRef => {
	if (!toggleContainerRef || _.isEmpty(toggleContainerRef.firstChild))
		return null;
	const itemsRef = toggleContainerRef.querySelector("now-label-value-stacked");

	if (!itemsRef || !itemsRef.shadowRoot) return [];

	const labelValuePairs = itemsRef.shadowRoot.querySelectorAll(
		"div.now-label-value-pair"
	);

	if (!labelValuePairs || !labelValuePairs.length) return [];

	return Array.from(labelValuePairs);
};

export const getRects = toggleContainerRef => {
	const labelValuePairs = getLabelValuePairs(toggleContainerRef);
	return labelValuePairs.map(labelValuePair => ({
		node: labelValuePair,
		rect: labelValuePair.getBoundingClientRect()
	}));
};

export const getRowStartIndices = labelValuePairRects => {
	if (!labelValuePairRects || !labelValuePairRects.length) return [];
	const leftPoint = labelValuePairRects[0].rect.left;
	const leftPointIndices = [];
	labelValuePairRects.forEach((lvp, index) => {
		const isRowStart = lvp.rect.left === leftPoint;
		if (isRowStart) leftPointIndices.push(index);
	});
	return leftPointIndices;
};
