import _ from 'lodash';

export const toAttributes = attrs => {
	return Object.keys(attrs).reduce((acc, key) => {
		const newKey = _.kebabCase(key);
		acc[newKey] = attrs[key];
		return acc;
	}, {});
};

export const getWigetTagNames = widgets => widgets.map(widget => _.get(widget, 'tag.value'));

export const getTagsNotInCache = (tagsLoaded, widgets) => {
	if(!Array.isArray(widgets))
		return [];
	const tagNames = getWigetTagNames(widgets);
	const newTags = tagNames.filter(tag => {
		return tagsLoaded.indexOf(tag) === -1;
	});
	return newTags;
};
