export const TemplateGQLRead = `
query ($queryConditions: String, $paginationVar: Glide_PaginationInputType) {
	GlideRecord_Query {
	  __typename
	  sys_template(queryConditions: $queryConditions, pagination: $paginationVar) {
		_table_metadata {
		  canCreate
		}
		_rowCount
		_results {
		  _row_data {
			canWrite
			canDelete
		  }
		  name {
			value
		  }
		  template {
			value
		  }
		  short_description {
			displayValue
		  }
		  sys_id {
			value
		  }
		  table {
			value
		  }
		}
	  }
	}
  }  
`;

export const TemplateDataGQLRead =  `
query ($sysId: String, $targetSysId: String, $encodedRecord: String)
{
	GlideLayout_Query {
		templateDataRetreiver (sysId: $sysId, targetSysId: $targetSysId, encodedRecord: $encodedRecord) {
			sysId
			templateName
			data {
		        name
		        type
		        label
		        value
		        displayValue
			}
			conflicts {
				name
				type
				label
				value
				displayValue
			}
		}
	}
}
`;
