import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-button";
import { SIDEBAR } from "../constants/constants";
import styles from "./sidebarAction.scss";
import "@servicenow/now-tooltip";

const view = (state, { dispatch, updateState }) => {
	const {
		properties: { icon, name, selected, tooltip, badge },
		targetRef
	} = state;
	const variant = selected
		? "sidebar-action-button is-selected"
		: "sidebar-action-button";

	const setTargetRef = ref => {
		if (!targetRef) setTimeout(() => updateState({ targetRef: ref }));
		else if (!ref) setTimeout(() => updateState({ targetRef: null }));
	};

	return (
		<div>
			<button
				type="button"
				className={variant}
				on-click={event => {
					event.preventDefault();
					event.stopPropagation();
					if (targetRef) targetRef.focus();
					dispatch(SIDEBAR.INTERNAL_ACTIONS.ACTION_CLICKED, { name });
				}}
				hook={{
					destroy: () => setTargetRef(null),
					insert: ({ elm }) => setTargetRef(elm)
				}}
				aria-label={tooltip}
			>
				{" "}
				<span className="sidebar-icon-badge-wrapper">
					<now-icon icon={icon} size="md" />
					{badge && (
						<now-icon
							icon="presence-fill"
							size="sm"
							className="sidebar-icon-presence"
						/>
					)}
				</span>
			</button>
			<now-tooltip target-ref={targetRef} content={tooltip} />
		</div>
	);
};

createCustomElement("sn-form-internal-sidebar-action", {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		badge: {},
		name: {},
		icon: {},
		selected: {
			unstableParse: true
		},
		tooltip: {}
	}
});
