import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { _isEmpty } from "./HeaderCommon.js";
import { createHeaderDataProvider } from "./headerDataProvider";
import { default as HEADER_COMPONENT } from "./header";
import _ from "lodash";

const headerDataProvider = createHeaderDataProvider();

// Create view for renderer
const view = state => {
	const attrs = _.pick(state.properties, [
		"primaryValue",
		"secondaryItems",
		"table",
		"sysId",
		"recordDisplayValue",
		"recordTags"
	]);
	return <HEADER_COMPONENT {...attrs} />;
};

const HEADER_CONNECT_COMPONENT = "now-record-common-header-connected";

// Create header component
createCustomElement(HEADER_CONNECT_COMPONENT, {
	properties: headerDataProvider.properties,
	renderer: {
		type: snabbdom,
		view
	},
	actions: headerDataProvider.actions,
	actionHandlers: headerDataProvider.actionHandlers
});

export default HEADER_CONNECT_COMPONENT;
