import { createCustomElement } from "@servicenow/ui-core";
import react from "@servicenow/ui-renderer-react";
import Searchbar from "./Searchbar";
import style from "./Searchbar.scss";
import { SEARCH_INPUT_ENTERED } from "../constants/searchbarConstants.js";

const COMPONENT_NAME = "sn-form-internal-searchbar";

/**
 * Searchbar component used to search for content within form components.
 * The component dispatches an event SEARCH_INPUT#ENTERED
 * which can be used to update the content of a component based on the input received.
 *
 * ```jsx
 * <sn-form-internal-searchbar
 *		placeholder={...}
 *		caseSensitive={...}
 * />
 * ```
 *
 * @seismicElement sn-form-internal-searchbar
 * @summary Searchbar component (For internal use only)
 *
 */
createCustomElement(COMPONENT_NAME, {
	renderer: {
		type: react,
		view: Searchbar
	},

	properties: {
		/**
		 * Flag to indicate whether the search result should be case sensitive
		 * @type {boolean}
		 */
		caseSensitive: {},
		/**
		 * Placeholder text displayed in the search field
		 * @type {string}
		 */
		placeholder: {},

		/**
		 * The initial value for the search input
		 * @type {string}
		 */
		value: {
			default: ""
		}
	},

	dispatches: {
		/**
		 * Action dispatched when the searchbar receives an input
		 * Components can use this action to update the content based on the input received
		 * @type {{ filter: string }}
		 */
		[SEARCH_INPUT_ENTERED]: {}
	},

	styles: style
});

export default COMPONENT_NAME;
