import { createDataProviderDataSource, getValue } from "@devsnc/library-form";
import { CONTEXTUAL_LINK_NAMES } from "../../../constants/contextualLinks";
import { UI_ACTION_TYPES, UX_ACTION_TYPES } from "../UIActionBarConst";
import { transformer, contractGenerator } from "@devsnc/sn-declarative-actions";

export const UX_ACTION_QUERY_KEY = "GlideUxActionLayout_Query";
export const GET_ACTIONS_KEY = "getActionLayout";
export const UI_ACTION_FORM_ACTIONS = "formActions";
export const MESSAGES = "messages";
export const ACTION_LAYOUT_ITEMS = "actionLayoutItems";
export const QUERY_FRAGMENT = `
${UX_ACTION_QUERY_KEY} {
    ${GET_ACTIONS_KEY}(table: $table, sysId: $sysId, view: $view, query: $query, actionConfigId: $actionConfigId){
        ${MESSAGES} {
            name
            value
        }
        ${ACTION_LAYOUT_ITEMS} {
            ... on  GlideUxActionLayout_UxActionGroupType {
                name
                label
                color
                shouldBeInOverflow
                type
                actions        
                {
                    ... on GlideUxActionLayout_UxUiActionType {
                        name
                        label
                        color
                        shouldBeInOverflow
                        order
                        sysId
                        clientScript
                        hint
                        hasClientScript
                    }
                    ... on GlideUxActionLayout_UxDeclarativeActionType {
                        name
                        label
                        color
                        shouldBeInOverflow
                        assignmentId
                        tooltip
                        actionPayload
                        actionDispatch
                        actionType
                        confirmationMessage
                        confirmationRequired
                        modelConditions {
                            field
                            operator
                            value
                            newQuery
                            or
                        }
                        payloadMap {
                            name
                            value
                        }
                    }
                }
            }

            ... on GlideUxActionLayout_UxUiActionType {
                name
                label
                color
                shouldBeInOverflow
                sysId
                clientScript
                hint
                hasClientScript
            }

            ... on GlideUxActionLayout_UxDeclarativeActionType {
                name
                label
                color
                shouldBeInOverflow
                assignmentId
                tooltip
                actionPayload
                actionDispatch
                actionType
                confirmationMessage
                confirmationRequired
                modelConditions {
                    field
                    operator
                    value
                    newQuery
                    or
                }
                payloadMap {
                    name
                    value
                }
            }
        }
    }
}`;

const transformMessages = (messages = []) =>
	messages.reduce(
		(loadedMessages, message) =>
			message.name
				? {
						...loadedMessages,
						[message.name]: message.value
				  }
				: loadedMessages,
		{}
	);

const transformAction = (action, actionsMap) => {
	const id = action.sysId || action.assignmentId;
	return {
		id,
		label: action.label || action.title,
		hint: action.hint || action.tooltip,
		uxActionType: action.sysId
			? UX_ACTION_TYPES.UI_ACTION
			: UX_ACTION_TYPES.DECLARATIVE_ACTION,
		action: actionsMap[id]
	};
};

const transformActions = (actions, actionsMap) => {
	return actions.map(action => transformAction(action, actionsMap));
};

const transformActionNodes = (nodes, actionIds, formActions) => {
	const actionsMap = formActions.reduce((map, action) => {
		return {
			...map,
			[action.id]: action
		};
	}, {});
	let nodeId = 1;
	return nodes.reduce((groups, node) => {
		const actions = (node.actions || [node]).filter(action =>
			actionIds.has(action.sysId || action.assignmentId)
		);
		if (!actions.length) return groups;

		const hasMultipleChildren = actions.length > 1;
		return [
			...groups,
			{
				id: nodeId++,
				label: node.type === UI_ACTION_TYPES.MENU ? node.label : "",
				overflow: node.shouldBeInOverflow,
				color: node.color,
				type: hasMultipleChildren ? node.type : UI_ACTION_TYPES.BUTTON,
				children: transformActions(actions, actionsMap)
			}
		];
	}, []);
};

export const transformDAActionsToUXFContract = (daActions, properties, state) => {
	const {
		record: { table, trueTable, sysId, view, query}
	} = state;
	const { actionConfigId } = properties;
	const model = {
		modelType: 'FORM',
		table: trueTable || table,
		sysId,
		view,
		query,
		actionConfigId
	}
	return contractGenerator(daActions, model) || {};
};

const getActionsFromNodes = (nodes, formModel, formActions) => {
	const actionIds = new Set();
	const uiActionIds = new Set();
	const uiActions = [];
	const declarativeActions = [];
	const transformNode = node => {
		const id = node.sysId || node.assignmentId;
		if (actionIds.has(id)) return;

		actionIds.add(id);
		const uxActionType = node.sysId
			? UX_ACTION_TYPES.UI_ACTION
			: UX_ACTION_TYPES.DECLARATIVE_ACTION;
		const action = {
			...node,
			id,
			uxActionType
		};
		if (uxActionType == UX_ACTION_TYPES.UI_ACTION) {
			uiActions.push(action);
			uiActionIds.add(id);
		} else declarativeActions.push(action);
	};

	nodes.forEach(node => {
		if (node.actions) {
			for (const action of node.actions) {
				transformNode(action);
			}
		} else {
			transformNode(node);
		}
	});

	const daActions = transformer(declarativeActions, formModel);
	const evaluatedActionIds = new Set([...uiActionIds, ...daActions.map(da => da.assignmentId)]);
	return {
		actionIds: evaluatedActionIds,
		formActions: [...uiActions, ...daActions],
		daActions
	};
};

const createTransform = getValueOrDefault => (
	data,
	state,
	properties,
	formModel
) => {
	const {
		isFormReadonly,
		properties: { table }
	} = state;
	const { record: { trueTable = table } = {} } = state;

	const actionLayoutItems = getValueOrDefault(ACTION_LAYOUT_ITEMS, [])(data);

	const { formActions = [], actionIds = new Set(), daActions = [] } = isFormReadonly
		? {}
		: getActionsFromNodes(actionLayoutItems, formModel);

	const actionNodes = isFormReadonly
		? []
		: transformActionNodes(actionLayoutItems, actionIds, formActions);

	const encodedQuery = encodeURIComponent(`active=true`);
	const configurationItem = {
		order: CONTEXTUAL_LINK_NAMES.uxActions.order,
		label: CONTEXTUAL_LINK_NAMES.uxActions.name,
		url: encodeURIComponent(
			`sys_ux_form_action_layout_item_list.do?sysparm_query=${encodedQuery}`
		)
	};

	const daClientActionContract = transformDAActionsToUXFContract(daActions, properties, state);

	const messages = transformMessages(getValueOrDefault(MESSAGES, [])(data));

	return {
		formActions,
		actionNodes,
		daClientActionContract,
		configurationItem,
		messages
	};
};

export const createUxActionDataSource = (queryPrefix = "") => {
	const baseKey = `${queryPrefix}${UX_ACTION_QUERY_KEY}.${GET_ACTIONS_KEY}`;
	const getValueOrDefault = getValue(baseKey);
	const transform = createTransform(getValueOrDefault);

	return createDataProviderDataSource({
		query: QUERY_FRAGMENT,
		variables: ['table', 'view', 'sysId', 'query', 'actionConfigId'],
		transform,
		props: {
			table: { default: '' },
			view: { default: '' },
			sysId: { default: '' },
			query: { default: '' },
			actionConfigId: { default: '' }
		},
		selectableProps: {
			formActions: {
				default: []
			},
			configurationItem: {},
			messages: {
				default: []
			},
			actionNodes: {
				default: []
			},
			daClientActionContract: {
				default: {}
			}
		}
	});
};
