import React from "react";
import react from "@servicenow/ui-renderer-react";
import _ from "lodash";
import { findDOMNode } from "react-dom";
import PropTypes from "prop-types";
import { t } from "sn-translate";
import { UI_ACTION_TYPES, UX_ACTION_TYPES ,
	UI_ACTION_BAR_INTERNAL_ACTIONS,
	UI_ACTION_BAR_EVENT_SOURCE,
	UI_ACTION_BAR_ACTIONS
} from "./UIActionBarConst";
import "@servicenow/now-dropdown";
import "@servicenow/now-button";
import "@servicenow/now-split-button";

import { EmptyStateElement } from "../emptyState";
import { transformer } from "@devsnc/sn-declarative-actions";

const MORE_TEXT = t("More Actions");
const MORE_ARIA_LABEL = t("Show hidden actions");
const DROPDOWN_NAME = "ui_action_more_dropdown";
const WORKSPACE_FORM_LAYOUT_ACTION_DISABLE_FORM =
	"WORKSPACE_FORM_LAYOUT#DISABLE_FORM";

const UI_ACTION_DEFAULT = 3;

const initialState = (actionNodes = []) => {
	const [buttonNodes, overflowNodes] = _.partition(
		actionNodes,
		node => !node.overflow
	);
	return { overflowNodes, isDisabled: false, buttonNodes };
};

const colorToVariant = {
	primary: "primary",
	unstyled: "secondary",
	destructive: "primary-negative"
};

const validClickTypes = {
	[UI_ACTION_BAR_INTERNAL_ACTIONS.BUTTON_CLICK]: "",
	[UI_ACTION_BAR_INTERNAL_ACTIONS.MENU_CLICK]: "",
	[UI_ACTION_BAR_INTERNAL_ACTIONS.SPLIT_BUTTON_ACTION_CLICKED]: "",
	[UI_ACTION_BAR_INTERNAL_ACTIONS.SPLIT_BUTTON_CLICKED]: ""
};

const isActionableClick = type => type in validClickTypes;

class UIActionBarInternal extends React.Component {
	constructor(props) {
		super(props);

		this.buttonRefs = [];
		this.buttonRects = [];
		this.rectsCalculated = false
		this.overflowRef = null;
		this.resize = this.resize.bind(this);
		this.refActionContainer = this.refActionContainer.bind(this);
		this.refFixedButtonsContainer = this.refFixedButtonsContainer.bind(this);
		this.refActionButtons = this.refActionButtons.bind(this);
		this.setOverflowButtonRef = this.setOverflowButtonRef.bind(this);
		this.getComponentMargin = this.getComponentMargin.bind(this);
		this.state = initialState(props.properties.actionNodes);
	}

	refActionContainer(node) {
		if (!node) {
			return;
		}
		this.actionContainer = node;
	}

	refFixedButtonsContainer(node) {
		if (!node) {
			return;
		}
		this.fixedButtonsContainer = node;
	}

	refActionButtons(node) {
		if (!node) {
			return;
		}
		const index = parseInt(node.getAttribute("data-index"), 10);
		this.buttonRefs[index] = node;
	}

	componentDidMount() {
		const { actionNodes } = this.props.properties;
		if (actionNodes) {
			this.setActionBarVisibility("hidden");
			setTimeout(() => this.getRenderWidth());
		}
	}

	focusOnLastClickedAction() {
		if (!this.state.lastClickedAction) return;

		const buttonNodeIndex = this.state.buttonNodes.findIndex(buttonNode =>
			_.isEqual(buttonNode.children[0], this.state.lastClickedAction.action)
		);
		const focusIndex = buttonNodeIndex > 0 ? buttonNodeIndex : 0;

		// focus on the same ui action, or if action is removed, focus on the first action
		return this.onceElementHasFocus(this.buttonRefs[focusIndex], 10, 100);
	}

	componentDidUpdate(prevProps, prevState) {
		if (
			this.state.lastClickedAction &&
			prevState.lastClickedAction !== this.state.lastClickedAction
		) {
			this.focusOnLastClickedAction();
		}

		if (this.props.properties.actionNodes !== prevProps.properties.actionNodes)
			setTimeout(() => this.getRenderWidth());
		else if (this.props.properties.width !== prevProps.properties.width)
			setTimeout(() => this.getRenderWidth());
	}

	componentWillReceiveProps(changedProps) {
		const {
			properties: { actionNodes, classicForm },
			buttonClicked,
			behaviors: {
				resize: {
					size: { width }
				}
			},
		} = changedProps;

		if (!classicForm && width && width !== this.state.resizeWidth) {
			this.setState({resizeWidth: width}, () => this.getRenderWidth());
		}

		if (this.state.lastClickedAction && this.state.actionNodesLoaded) {
			this.focusOnLastClickedAction().then(() =>
				this.setState({ lastClickedAction: null, actionNodesLoaded: false })
			);
		}

		if (buttonClicked && buttonClicked != this.props.buttonClicked) {
			const { type, payload } = changedProps.buttonClicked;
			if (isActionableClick(type)) {
				const { item, actionFromPayload } = payload;
				const action = item || actionFromPayload;
				if (action != null) this.submitAction(action);
			}
		}

		if (actionNodes && actionNodes !== this.props.properties.actionNodes) {
			this.setState(initialState(actionNodes));

			// to signal when to focus on the last clicked button
			if (this.state.lastClickedAction) {
				this.setState({
					actionNodesLoaded: true
				});
			}
		}
	}

	getComponentMargin(component) {
		try {
			const style = window.getComputedStyle(component);
			return parseInt(style.getPropertyValue("margin-inline-start"));
		} catch (e) {
			console.log(e);
			return 0;
		}
	}

	getRenderWidth() {
		if (!this.actionContainer || !this.actionContainer.childNodes) {
			return;
		}

		const { buttonNodes, overflowNodes } = initialState(this.props.properties.actionNodes);
		if (buttonNodes.length <= UI_ACTION_DEFAULT && overflowNodes.length == 0) {
			this.setActionBarVisibility("visible");
			return;
		}

		if (this.shouldGetRects()) {
			let marginLeft;
			this.buttonRects = this.buttonRefs.map(node => {
				if (!marginLeft) marginLeft = this.getComponentMargin(node);
				return Object.assign(node.getBoundingClientRect(), {
					marginLeft
				});
			});
			this.rectsCalculated = true;
			this.fixedButtonsContainerRect = this.fixedButtonsContainer.getBoundingClientRect();
		}
		this.resize();
	}

	// FF only return correct rect on the later rendering.
	// DEF0092342: refs with the right widths are returned on later rendering
	shouldGetRects() {
		if (this.rectsCalculated)
			return false;

		if (this.buttonRects.length !== this.buttonRefs.length &&
			this.buttonRefs.length > 0) {
				const rect = this.buttonRefs[0].getBoundingClientRect();
				return rect && rect.width > 0;
			}

		return this.buttonRects.length > 1 &&
			this.buttonRects[0].x === this.buttonRects[1].x;
	}

	resize() {
		const { actionNodes, fixedButtonNumber } = this.props.properties;
		const { buttonNodes, overflowNodes } = initialState(actionNodes);

		const fixedButtonWidth = this.fixedButtonsContainerRect
			? this.fixedButtonsContainerRect.width
			: 0;

		let containerWidth = this.props.properties.width;
		if (!containerWidth) {
			const containerRect = this.actionContainer.parentNode.getBoundingClientRect();
			containerWidth = containerRect.width;
		}

		if (!containerWidth) {
			this.setActionBarVisibility("visible");
			return false;
		}

		const maxWidth = containerWidth - fixedButtonWidth;
		let sumWidth = 0;
		this.buttonRects.forEach(rec => {
			const width = rec.width;
			sumWidth += width + (rec.marginLeft || 0);

			if ((sumWidth > maxWidth) && (buttonNodes.length > fixedButtonNumber)) {
				const button = buttonNodes.pop();
				if (button) overflowNodes.unshift(button);
			}
		});

		new Promise(resolve => {
			if (this.state.buttonNodes.length !== buttonNodes.length) {
				this.setState(
					{
						buttonNodes,
						overflowNodes
					},
					resolve
				);
			} else {
				resolve();
			}
		}).finally(() => {
			this.setActionBarVisibility("visible");
		});
		return true;
	}

	setActionBarVisibility(visibility = "visible") {
		if (this.actionContainer)
			this.actionContainer.style.visibility = visibility;
	}

	setDisable(disabled) {
		const { dispatch } = this.props;

		this.setState({ isDisabled: disabled });
		dispatch(WORKSPACE_FORM_LAYOUT_ACTION_DISABLE_FORM, {
			disabled: disabled
		});
	}

	onceElementHasFocus(element, frequency = 10, tries = 100) {
		const hasFocus = element => {
			if (element) element.focus();
			if (!element.shadowRoot.activeElement) return false;

			return true;
		};
		return new Promise((resolve, reject) => {
			let attempts = 0;
			const interval = setInterval(() => {
				attempts++;
				if (hasFocus(element)) {
					clearInterval(interval);
					resolve(element);
				}
				if (attempts >= tries) {
					clearInterval(interval);
					reject(
						new Error(
							`waitForRender executed the maximum number of attempts, ${tries}`
						)
					);
				}
			}, frequency);
		});
	}

	handleActionClick(action) {
		const {
			dispatch,
			properties: { handleClick, daModel }
		} = this.props;

		if (handleClick) {
			this.setDisable(true);
			Promise.resolve(handleClick(action))
				.then(
					() => {
						console.log("Action succeeded!");
					},
					() => {
						console.log("Action failed!");
					}
				)
				.finally(() => {
					this.setDisable(false);
				});
		} else {
			if (action.uxActionType === UX_ACTION_TYPES.DECLARATIVE_ACTION) {
				const daActions = transformer([action.action], daModel);
				if (daActions.length > 0)
					dispatch(UI_ACTION_BAR_ACTIONS.DECLARATIVE_ACTION, {
						action: daActions[0],
						model: daModel
					});
			} else {
				dispatch(UI_ACTION_BAR_ACTIONS.ACTION_CLICK, {
					actionSysId: action.id
				});
			}
		}
	}

	submitAction(action) {
		this.handleActionClick(action);

		var lastClickedAction = this.state.buttonNodes.find(button =>
			button.children.includes(action)
		);
		if (!lastClickedAction) {
			const dropdownMenuEl = this.fixedButtonsContainer.querySelector(
				"now-dropdown"
			);
			this.onceElementHasFocus(dropdownMenuEl, 10, 100);
		} else {
			lastClickedAction = { action: lastClickedAction.children[0] };
			this.setState({ lastClickedAction });
		}
	}

	returnFocusToOverflowButton() {
		if (this.overflowRef) {
			this.overflowRef.focus();
		}
	}

	getBaseNodeAttributes(
		key,
		label,
		tooltipContent,
		variant,
		disabled,
		appendPayload,
		id = ""
	) {
		return {
			"data-index": key,
			ref: this.refActionButtons,
			key,
			label,
			tooltipContent,
			componentName: `${this.state.componentName || ""}_node${key}_${id}`,
			variant,
			...(disabled && { disabled }),
			"config-aria": {
				"aria-label": label
			},
			"append-to-payload": {
				eventSource: UI_ACTION_BAR_EVENT_SOURCE,
				...(appendPayload || {})
			}
		};
	}

	renderButtonNode(action, index, variant, disabled) {
		return (
			<now-button
				{...this.getBaseNodeAttributes(
					index,
					action.label,
					action.hint,
					variant,
					disabled,
					{ item: action },
					action.id
				)}
			/>
		);
	}

	renderSplitButtonNode(
		actionForPayload,
		dropActions,
		index,
		variant,
		disabled
	) {
		const { label } = actionForPayload;
		return (
			<now-split-button
				items={dropActions}
				{...this.getBaseNodeAttributes(index, label, label, variant, disabled, {
					actionFromPayload: actionForPayload
				})}
			/>
		);
	}

	renderMenuButtonNode(label, dropActions, index, variant, disabled) {
		return (
			<now-dropdown
				items={dropActions}
				manage-selected-items
				select={"none"}
				placeholder={label}
				{...this.getBaseNodeAttributes(index, label, label, variant, disabled)}
			/>
		);
	}

	renderButtonNodes(buttons, disabled) {
		if (!buttons || !buttons.length) return null;
		if (this.buttonRefs.length) this.buttonRefs = [];
		return buttons.map((node, index) => {
			const { children } = node;
			if (!children || children.length == 0) return null;
			const action = children[0];
			const color = colorToVariant[node.color || ""];
			if (node.type === UI_ACTION_TYPES.BUTTON || children.length == 1)
				return this.renderButtonNode(action, index, color, disabled);
			else if (node.type === UI_ACTION_TYPES.SPLIT_BUTTON)
				return this.renderSplitButtonNode(
					action,
					children.slice(1),
					index,
					color,
					disabled
				);
			else
				return this.renderMenuButtonNode(
					node.label,
					children,
					index,
					color,
					disabled
				);
		});
	}

	setOverflowButtonRef(node) {
		this.overflowRef = findDOMNode(node);
	}

	renderOverflow(disabled) {
		const { overflowNodes } = this.state;
		if (!overflowNodes || !overflowNodes.length) return null;
		return (
			<now-dropdown
				manage-selected-items
				append-to-payload={{
					eventSource: UI_ACTION_BAR_EVENT_SOURCE
				}}
				config-aria={{
					"aria-label": MORE_ARIA_LABEL
				}}
				{...{
					select: "none",
					className: "uiaction-bar-dropdown",
					size: "md",
					hideCaret: true,
					icon: "ellipsis-h-outline",
					items: overflowNodes,
					tooltipContent: MORE_TEXT,
					componentName: DROPDOWN_NAME,
					...(disabled && { disabled })
				}}
				ref={node => {
					if (node && node.shadowRoot)
						this.setOverflowButtonRef(
							node.shadowRoot.getElementById("trigger")
						);
				}}
			/>
		);
	}

	renderActionBar() {
		const { overflowNodes, isDisabled, buttonNodes } = this.state;

		return (
			<div className="uiaction-bar-wrapper">
				<div className="uiaction-bar" ref={this.refActionContainer}>
					<div className="uiaction-bar-btn-container">
						{buttonNodes.length > 0 &&
							this.renderButtonNodes(buttonNodes, isDisabled)}
					</div>
					<div
						className="uiaction-bar-btn-container uiaction-bar-btn"
						ref={this.refFixedButtonsContainer}
					>
						{overflowNodes.length > 0 && this.renderOverflow(isDisabled)}
					</div>
				</div>
			</div>
		);
	}

	render() {
		const { actionNodes, classicForm } = this.props.properties;
		if (!classicForm && !actionNodes)
			return <EmptyStateElement message={t("No data to display")} />;

		return this.renderActionBar();
	}
}

UIActionBarInternal.propTypes = {
	actionNodes: PropTypes.arrayOf(
		PropTypes.shape({
			label: PropTypes.string.isRequired,
			id: PropTypes.number,
			overflow: PropTypes.bool,
			type: PropTypes.string,
			color: PropTypes.string,
			children: PropTypes.arrayOf(
				PropTypes.shape({
					id: PropTypes.string,
					label: PropTypes.string,
					hint: PropTypes.string
				})
			)
		})
	),
	fixedButtonNumber: PropTypes.number,
	handleClick: PropTypes.func, // optional if pass will be called when action is click; otherwise, NOW_RECORD_COMMON_UI_ACTION_BAR#CLICKED will be dispatched with selectedAction
	width: PropTypes.number,
	classicForm: PropTypes.bool
};

export default UIActionBarInternal;
