import { TYPEAHEAD } from "./constants";
const itemSelectedHandler = ({
	dispatch,
	state: {
		properties: { fieldName, inputRef }
	},
	action: {
		payload: { value }
	}
}) => {
	dispatch(TYPEAHEAD.ACTIONS.PILL_ADDED, {
		name: fieldName,
		value: value
	});
	window.setTimeout(() => {
		inputRef.focus();
		inputRef.scrollIntoView;
	}, 20);
};

const pillDismissHandler = ({
	dispatch,
	state: {
		properties: { selectedItems, inputRef, fieldName }
	},
	action: {
		payload: {
			item: { value }
		}
	}
}) => {
	dispatch(TYPEAHEAD.ACTIONS.PILL_REMOVED, { name: fieldName, value: value });
	if (selectedItems.length === 1) {
		window.setTimeout(() => {
			inputRef.focus();
			inputRef.scrollIntoView;
		}, 20);
	}
};

const DROPDOWN_PANEL_SELECTED_ITEM = "DROPDOWN_PANEL#SELECTED_ITEM_SET";
const DROPDOWN_PANEL_DISMISS_PILL = "NOW_PILL#DISMISSED";

export default {
	actions: {
		[DROPDOWN_PANEL_SELECTED_ITEM]: {
			private: true
		},
		[DROPDOWN_PANEL_DISMISS_PILL]: {
			private: true
		}
	},
	actionHandlers: {
		[DROPDOWN_PANEL_SELECTED_ITEM]: itemSelectedHandler,
		[DROPDOWN_PANEL_DISMISS_PILL]: pillDismissHandler
	}
};
