import { createActionToStateHandler } from "@devsnc/library-form";
import {
	UI_ACTION_BAR_INTERNAL_ACTIONS,
	UI_ACTION_BAR_EVENT_SOURCE
} from "./UIActionBarConst";
import { executeEffectIfValidEventSource } from "@devsnc/library-form";

const updateStateIfValidEventSource = executeEffectIfValidEventSource(
	UI_ACTION_BAR_EVENT_SOURCE,
	{ effect: createActionToStateHandler("buttonClicked") }
);

export default {
	actions: {
		[UI_ACTION_BAR_INTERNAL_ACTIONS.BUTTON_CLICK]: {
			private: true
		},
		[UI_ACTION_BAR_INTERNAL_ACTIONS.MENU_CLICK]: {
			private: true
		}
	},
	actionHandlers: {
		// TODO: this is a temporary solution, we will need to find a better solution to handler size change
		[UI_ACTION_BAR_INTERNAL_ACTIONS.BUTTON_CLICK]: updateStateIfValidEventSource,
		[UI_ACTION_BAR_INTERNAL_ACTIONS.MENU_CLICK]: updateStateIfValidEventSource,
		[UI_ACTION_BAR_INTERNAL_ACTIONS.SPLIT_BUTTON_CLICKED]: updateStateIfValidEventSource,
		[UI_ACTION_BAR_INTERNAL_ACTIONS.SPLIT_BUTTON_ACTION_CLICKED]: updateStateIfValidEventSource
	}
};
