import { createCustomElement, actionTypes } from "@servicenow/ui-core";
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_DOM_TREE_READY} = actionTypes;
import snabbdom, { Fragment } from "@servicenow/ui-renderer-snabbdom";
import resizeBehavior from "@servicenow/behavior-resize";
import "@servicenow/now-label-value";
import "@servicenow/now-heading";
import "@servicenow/now-button";
import { t } from "sn-translate";
import _ from "lodash";
import { itemsSchema } from "./schemas/displayValueSchema";
import { EmptyStateElement } from "../emptyState";
import { LayoutHandler, isRTL } from "./layoutHandler";
import CONSTS from "./displayValueBlockConstants";
import styles from "./displayValueBlock.scss";
import { getMaxLineHeight } from "./lineHeightCalculator";
import classNames from "classnames/dedupe";

// support for dynamic layoutType will go here
const layoutTypes = {
	distributed: {
		align: "horizontal-equal",
		truncated: true,
		wrapped: true,
		itemMinWidth: CONSTS.LAYOUT.DISTRIBUTED.MIN_WIDTH
	},
	dynamic: {
		align: "horizontal",
		truncated: true,
		wrapped: true
	}
};

//styling to apply to value div when mutltiline text and document is LTR
const maxLinesPerValueStyleLTR = `style="white-space:normal;word-break:break-all;hyphens:auto;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-box-orient:vertical;`;
//styling to apply to value div when mutltiline and document is in RTL mode
const maxLinesPerValueStyleRTL = `style="white-space:normal;word-break:break-all;hyphens:auto;`;

const acceptedProperties = {
	/**
	 * Title of the component displayed at top-left in a header tag.
	 * @type {string}
	 */
	title: {
		schema: {
			type: "string"
		},
		default: ""
	},
	/**
	 * Array of label-value items. Labels can have one or more values. Values
	 * can be rendered as a string, text link, or highlighted value.
	 *
	 * Label-value items are configured with the following keys:
	 * - `label`: String. Text displayed as the label. Required.
	 * - `value`: String or Object. Single value. Required.
	 *
	 * Each `value` can be a string for a simple value or an object for more
	 * complex values. Object values are configured using the following keys:
	 *
	 * To render the value as a string:
	 * - `type`: String. Enter 'string' to render value as a string.
	 * - `value`: String. Current value.
	 * - `previous`: String. Previous value (crossed out).
	 *
	 * To render the value as a highlighted-value component (see now-highlighted-value documentation for details):
	 * - `type`: String. Enter `'highlighted-value'` to render the value with the now-highlighted-value component.
	 * - `label`: String. Current value. Required.
	 * - `status`: String. Enter `'low'`. Sets the background/icon color.
	 * - `showIcon`: Boolean. Enter `false`. When true, shows a solid circle icon next to the label.
	 *
	 * To render the value as a text-link component (see now-text-link documentation for details):
	 * - `type`: String. Enter `'text-link'` to render the value with the now-text-link component.
	 * - `label`:String. Current value. Required.
	 * - `href`: String. URL to open when a user clicks the link. Required.
	 * - `variant`: String. Enter `'primary'`. Sets the link color.
	 * - `underlined`: Boolean. Enter `false`. When true, the link is underlined.
	 * - `opensWindow`: Boolean. Enter `false`. When true, opens the link in a new window or tab.
	 *
	 * @type {{label: string, values: (string|Object|Object[])}[]}
	 */
	items: { default: [], schema: itemsSchema },
	/**
	 * Sets the layout type.
	 * @type {('distributed'|'dynamic')}
	 */
	layoutType: {
		default: CONSTS.LAYOUT.DISTRIBUTED.NAME,
		schema: {
			type: "string",
			enum: ["distributed", "dynamic"]
		}
	},
	/**
	 * Character to display between label-value pairs.
	 * @type {('' | '|' | '•' | '·' | '-')}
	 */
	delimiter: {
		default: "",
		schema: {
			type: "string",
			enum: ["", "|", "•", "·", "-"]
		}
	},
	/**
	 * When true, labels are hidden.
	 * @type {boolean}
	 */
	hideLabels: {
		schema: {
			type: "boolean"
		},
		default: false
	},
	/**
	 * When true, displays label and values side-by-side.
	 * @type {boolean}
	 */
	displayInline: {
		schema: {
			type: "boolean"
		},
		default: false
	},
	/**
	 * Number of lines for a text type value before it is truncated.
	 * To prevent truncating, pass `0`,
	 * @type {number}
	 */
	maxLinesPerValue: {
		schema: {
			type: "number"
		},
		default: 1
	},
	/**
	 * Number of rows to render on the screen. If the number of actual rows
	 * is greater than the value of this property, excess rows will be hidden.
	 * @type {number}
	 */
	rowsBeforeToggleShown: {
		schema: {
			type: "number"
		},
		default: 2
	}
};

const transformItems = state => {
	const {
		properties: { items, hideLabels, maxLinesPerValue, displayInline }
	} = state;

	if (!items || !items.length) return state;

	const maxLinesPerValueStyle = isRTL()
		? `${maxLinesPerValueStyleRTL};${
				maxLinesPerValue > 0
					? "max-height:" + getMaxLineHeight(maxLinesPerValue) + "px;"
					: ""
		  }"`
		: `${maxLinesPerValueStyleLTR}-webkit-line-clamp:${maxLinesPerValue};"`;
	const transformedItems = items.map(item => {
		const shouldConvertToHtml =
			item.value.type === "string" && !item.value.previous;
		return {
			...item,
			label: !hideLabels ? item.label + (displayInline ? ":" : "") : "",
			value: {
				...item.value,
				type: shouldConvertToHtml ? "html" : item.value.type,
				value: shouldConvertToHtml
					? `<div title="${
							item.value.value
					  }" class="needs-clamping" ${maxLinesPerValueStyle}>${
							item.value.value
					  }</div>`
					: item.value.value
			}
		};
	});

	return {
		...state,
		items: transformedItems
	};
};

const view = (state, { updateState }) => {
	const {
		properties: { title, layoutType },
		behaviors: { resize = {} },
		width,
		items,
		showToggle,
		toggleOpen,
		initialLayoutCalculationComplete
	} = state;

	if (!items || !items.length) {
		const message = t("No data to display");
		return <EmptyStateElement message={message} />;
	}

	if (resize.size.width !== width) {
		LayoutHandler.onWidthChange(state);
		const toggleState = LayoutHandler.calculateToggleState(state);
		updateState({
			...(toggleState || {}),
			width: resize.size.width,
			shouldRender: true,
			initialLayoutCalculationComplete: true
		});
	}

	const labelValueStackedProps = {
		...layoutTypes[layoutType],
		items: [...items]
	};

	return (
		<div
			className={classNames({ "--hidden": !initialLayoutCalculationComplete })}
		>
			{title !== "" ? (
				<now-heading
					label={title}
					level="1"
					variant="header-tertiary"
					hasNoMargin={true}
				/>
			) : (
				<Fragment />
			)}
			<div className="wrapper-div">
				<now-label-value-stacked {...labelValueStackedProps} />
			</div>

			{showToggle ? (
				<now-button-bare
					hidePadding
					size="sm"
					label={toggleOpen ? t("Show less") : t("Show more")}
					iconEnd={toggleOpen ? "chevron-up-fill" : "chevron-down-fill"}
				/>
			) : (
				<Fragment />
			)}
		</div>
	);
};

/**
 * Display read-only information to users when they view a portal page or application.
 *
 * ```jsx
 * <now-display-value-block
 *      title={...}
 *      items={...}
 *      layoutType={...}
 *      delimeter={...}
 *      hideLabels={...}
 *      displayInline={...}
 *      maxLinesPerValue={...}
 * />
 * ```
 *
 * @seismicElement now-display-value-block
 * @summary Displays view-only record content to a user in portal pages and applications, using a configurable accordion.   
 */
createCustomElement(CONSTS.COMPONENT_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		...acceptedProperties
	},
	styles,
	initialState: {
		showToggle: false,
		toggleOpen: false,
		toggleHeight: 0,
		width: null,
		toggleContainerRef: null,
		initialLayoutCalculationComplete: false
	},
	transformState(state) {
		return transformItems(state);
	},
	behaviors: [
		{
			behavior: resizeBehavior,
			options: { timer: { type: "debounce", wait: 150 } }
		}
	],
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: {
			effect: ({
				state,
				updateState,
				action: {
					payload: { value, previousValue }
				}
			}) => {
				if (_.isEqual(value, previousValue)) return;
				const layoutState = LayoutHandler.recalculateLayout(state);
				updateState({ ...layoutState, shouldRender: true });
			}
		},
		[CONSTS.ACTIONS.PRIVATE.LINK_CLICKED]: {
			effect: ({
				action: {
					payload: { item }
				},
				dispatch
			}) => {
				dispatch(CONSTS.ACTIONS.PUBLIC.LINK_CLICKED, item);
			},
			stopPropagation: true
		},

		[CONSTS.ACTIONS.PRIVATE.TOGGLE_CLICKED]: {
			effect: ({ state, updateState }) => {
				const { toggleOpen } = state;
				LayoutHandler.onToggleChange(state);
				updateState({
					toggleOpen: !toggleOpen
				});
			},
			stopPropagation: true
		},
		[COMPONENT_DOM_TREE_READY]: {
			effect: ({ state, updateState, host }) => {
				const wrapperDiv = host.shadowRoot.querySelector(".wrapper-div")
				LayoutHandler.applyStyles(state, wrapperDiv);
				updateState({
					toggleContainerRef: wrapperDiv,
					shouldRender: false
				});
			}
		}
	}
});

export default CONSTS.COMPONENT_NAME;
