import React from "react";
import react from "@servicenow/ui-renderer-react";
import "@devsnc/sn-declarative-actions";
import UIActionBarInternalElement from "./UIActionBarInternalElement";
function UIActionBar(props) {
	const {
		actionNodes,
		fixedButtonNumber,
		handleClick,
		width,
		daModel = {},
		classicForm
	} = props.properties;

	const attributes = {
		actionNodes,
		fixedButtonNumber,
		handleClick,
		width,
		daModel,
		classicForm
	};
	if (classicForm) return <UIActionBarInternalElement {...attributes} />;

	return (
		<sn-declarative-mediator should-wrap-action={!classicForm}>
			<UIActionBarInternalElement {...attributes} />
		</sn-declarative-mediator>
	);
}
export default UIActionBar;
