import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { getProperty } from "sn-uxpage-presource";
import _ from "lodash";
import { t } from "sn-translate";
import "@servicenow/now-modal";
import {
	UPLOAD_FILE_ERROR,
	ALLOWED_MIME_TYPE,
	ALLOWED_DRAG_TYPE,
	ATTACHMENT_UPLOAD_SAVE,
	FILE_UPLOAD,
	UPLOAD_MODAL_EVENT_SOURCE
} from "../constants";
import { escapeSeismicSpecialSymbols } from "../../../../utils/seismicUtils";
import "@devsnc/sn-record-input-connected";
import { ADD_NOTIFICATIONS } from "../../../constants/constants";

/**
 * Return the file extension if found else return ''.
 * @param name String name of the file
 */
const getExtensionFromFileName = name => {
	if (name.lastIndexOf(".") === -1) return "";

	return name.substring(name.lastIndexOf(".") + 1).toLowerCase();
};

/**
 * Types are extensions set in glide.attachment.extensions property
 * if set
 * 		only allowed specified extensions
 * else
 * 		allows everything
 * @param file File object
 * @param types Array of allowed extensions
 */
const isInvalidFileType = (file, types) =>
	!_.includes(types, getExtensionFromFileName(file.name));

const isAllowedDragType = e =>
	e.dataTransfer && _.includes(e.dataTransfer.types, ALLOWED_DRAG_TYPE);

/**
 * To convert the file size from Bytes to KB/ MB
 * @param sizeInBytes
 */
const getDisplaySize = (sizeInBytes) => {
	let kilobytes = sizeInBytes / 1024;
	if (kilobytes < 1)
		kilobytes = 1;
	if (kilobytes > 1024) {
		let reportSize = kilobytes / 1024;
		return reportSize.toFixed(2) + "MB";
	}
	return kilobytes.toFixed(2) + "K";;
}

/**
 * Process the files to be uploaded. which are either set as property or dropped on the form.
 * @param files FileList object of Files dropped
 * @param updateLists method that updates uploadList and errorList
 */
const processFiles = (files, updateLists) => {
	const extentionsProperty = getProperty("glide.attachment.extensions", "");
	const extensions = extentionsProperty ? extentionsProperty.split(",") : [];
	const maxSizeMb = getProperty("com.glide.attachment.max_size", 1024);
	const maxSizeBytes = maxSizeMb * 1024 * 1024;

	let uploadList = [];
	let errorList = [];
	_.forEach(files, file => {
		if (extentionsProperty && isInvalidFileType(file, extensions))
			errorList.push(t(`{0} ${UPLOAD_FILE_ERROR.UNSOPPORTED_FILE}`, file.name)) 
		else if (file.size === 0)
			errorList.push(t(UPLOAD_FILE_ERROR.EMPTY_FILE));
		else if (file.size > maxSizeBytes )
			errorList.push(t(`{0} is {1}. The maximum file size is {2}.`, file.name, getDisplaySize(file.size), getDisplaySize(maxSizeBytes)))
		else 
			uploadList.push(file);
	});
	updateLists(uploadList, errorList);
};

const createDragHandlers = (setAttachmentOverlay, updateLists) => {
	let attachmentDragEventCounter = 0;

	/**
	 * Remove the attachment overlay for upload
	 */
	const removeAttachmentOverlay = () => {
		attachmentDragEventCounter = 0;
		setAttachmentOverlay(false);
	};

	const handleDragEnter = e => {
		e.preventDefault();
		e.stopPropagation();

		if (!isAllowedDragType(e)) return;

		attachmentDragEventCounter++;
		setAttachmentOverlay(true);
	};

	const handleDragLeave = e => {
		e.preventDefault();
		e.stopPropagation();

		if (!isAllowedDragType(e)) return;

		attachmentDragEventCounter--;
		if (attachmentDragEventCounter === 0) setAttachmentOverlay(false);
	};

	const handleDragOver = e => {
		e.preventDefault();
		e.stopPropagation();
	};

	const handleDrop = e => {
		e.preventDefault();
		e.stopPropagation();

		if (!isAllowedDragType(e)) return;
		removeAttachmentOverlay();
		const files = e.dataTransfer.files;
		if (!files.length) return;

		processFiles(files, updateLists);
	};

	return {
		onDragEnter: handleDragEnter,
		onDragOver: handleDragOver,
		onDragLeave: handleDragLeave,
		onDrop: handleDrop
	};
};

const renderAttachmentUploadOverlay = displayValue => {
	const attachmentOverlayMessage = displayValue
		? t("Drop a file to add it as an attachment to ") + displayValue
		: t("Drop a file to add it as an attachment");
	return (
		<div className="attachment-overlay" id="attachment-overlay">
			<div className="attachment-overlay--body">
				<div className="attachment-overlay--content">
					<div className="upload-art" />
					<div className="title">{t("Upload a file")}</div>
					<span>{attachmentOverlayMessage}</span>
				</div>
			</div>
		</div>
	);
};

/**
 * Uploads a new attachment (REST httpEffect)
 * @param table
 * @param sysId
 * @param file
 * @param dispatch
 */
const uploadAttachmentToServer = (table, sysId, file, dispatch) => {
	const data = new FormData();
	data.append("sysparm_table", table);
	data.append("sysparm_sys_id", sysId);
	data.append("sysparm_encryption_context", "");
	data.append("sysparm_nostack", "yes");
	data.append("attachments_modified", "true");
	data.append("file", file);

	const params = {
		action: "add",
		table: table,
		sys_id: sysId,
		load_attachment_record: "true"
	};

	dispatch(ATTACHMENT_UPLOAD_SAVE.ACTIONS.START, {
		data,
		...params
	});
};

/**
 * This function will dispatch Notification for all the files which cannot
 * be uploaded.
 *
 * @param errorList Array of error messages due to which files cannot be uploaded
 * @param dispatch
 */
const showAttachmentUploadErrors = (errorList, dispatch) => {
	if (_.isEmpty(errorList)) return;

	const notifications = errorList.map(error => ({
		type: "error",
		message: error
	}));

	dispatch(ADD_NOTIFICATIONS, {
		notifications
	});
};

const getModalTitle = (list, index) => {
	let title = t("Upload a file");
	const listLength = list.length - 1;

	if (listLength !== index)
		title = t("Upload a file (and {0} more...)", listLength - index);

	return title;
};

const getCancelLabel = (list, index) => {
	let label = t("Cancel");
	const listLength = list.length - 1;

	if (listLength !== index) label = t("Cancel All");

	return label;
};

const getFileFromFormData = ({
	meta: {
		request: { data }
	}
}) => {
	for (const entry of data.entries()) if (entry[0] === "file") return entry[1];
};

const isImageType = type =>
	_.includes(ALLOWED_MIME_TYPE, type.substring(0, type.indexOf("/")));

const renderAttachmentUploadModal = (
	showModal,
	fileList,
	currentFileName,
	currentFileIndex,
	updateName,
	createFileDataURL,
	uploadFileDataURL
) => {
	const fileToUpload = fileList[currentFileIndex];
	let canShowPreview = false;
	const cancelLabel = getCancelLabel(fileList, currentFileIndex);
	const modalTitle = getModalTitle(fileList, currentFileIndex);

	if (isImageType(fileToUpload.type)) {
		createFileDataURL(fileToUpload);
		canShowPreview = true;
	}

	const isUploadDisabled = fileToUpload.name === "" || currentFileName === "";
	return (
		<now-modal
			opened={showModal}
			manageOpened
			size="md"
			append-to-payload={{
				file: fileToUpload,
				eventSource: UPLOAD_MODAL_EVENT_SOURCE
			}}
			header-label={modalTitle}
			footer-actions={[
				{
					id: "upload",
					variant: "primary",
					label: t("Upload"),
					disabled: isUploadDisabled ? true : false
				},
				{
					id: "cancel",
					variant: "secondary",
					label: cancelLabel
				}
			]}
		>
			<sn-record-input-connected
				label={t("Title")}
				initialValue={escapeSeismicSpecialSymbols(currentFileName)}
				onValueChange={updateName}
				onStagedValueChange={updateName}
			/>
			{canShowPreview ? (
				<div className="sn-attachment-upload-preview">
					<img
						className="image"
						src={uploadFileDataURL}
						alt={fileToUpload.name}
					/>
				</div>
			) : null}
		</now-modal>
	);
};
const transformLoadingItem = (item, details) => {
	const { sys_id: sysId, canDelete, canWrite: canEditName, sys_created_by: userId, sys_created_by_display: userName,
		sys_updated_on: date, sys_updated_on_display: dateLabel, state } = details;
		return {
			...item,
			sysId, date, dateLabel, canDelete, canEditName, userId, userName, state, size: item.size.toString()
		};
}
export {
	createDragHandlers,
	renderAttachmentUploadOverlay,
	uploadAttachmentToServer,
	showAttachmentUploadErrors,
	renderAttachmentUploadModal,
	getExtensionFromFileName,
	getFileFromFormData,
	processFiles,
	transformLoadingItem
};
