import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_DOM_TREE_READY } = actionTypes;
const DISPATCH_CUSTOM_EVENT = "DISPATCH_CUSTOM_EVENT";
const getLifeCycleEventListenerRenderer = (baseRenderer, lifeCycleEvent, wait = 500) => {
	return {
		onStateChange: async (el,getTemplate, arg3, arg4) => {
			let lifeCycleEventTriggeredResolve = null;
			const lifeCycleEventTriggeredPromise = new Promise((resolve) => {
				lifeCycleEventTriggeredResolve = resolve;
			})
			const timeout = setTimeout(() => {
				lifeCycleEventTriggeredResolve()
			}, wait)
			const listener = async ({ detail: {type }}) => {
				if (type === lifeCycleEvent) {
					clearTimeout(timeout);
					el.removeEventListener(DISPATCH_CUSTOM_EVENT, listener);
					lifeCycleEventTriggeredResolve();
				}
			};
			
			el.addEventListener(DISPATCH_CUSTOM_EVENT, listener, false);
			await baseRenderer.onStateChange(el, getTemplate, arg3, arg4);
			return lifeCycleEventTriggeredPromise;
		},
		onDisconnect: baseRenderer.onDisconnect
	}
}

export const afterDomTreeReadyRenderer =
	(baseRenderer, wait = 5000) => getLifeCycleEventListenerRenderer(baseRenderer, COMPONENT_DOM_TREE_READY, wait);

