import attachmentPreviewActions from "./preview/attachmentPreviewActions";
import attachmentDeleteActions from "./delete/attachmentDeleteActions";
import attachmentUploadActions from "./upload/attachmentUploadActions";

export const createAttachmentActionHandlers = (
	enableUpload = true,
	additionalActions = { actions: {}, actionsHandlers: {} }
) => {
	return {
		actions: {
			...attachmentPreviewActions.actions,
			...attachmentDeleteActions.actions,
			...(enableUpload ? attachmentUploadActions.actions : {}),
			...additionalActions.actions
		},
		actionHandlers: {
			...attachmentPreviewActions.actionHandlers,
			...attachmentDeleteActions.actionHandlers,
			...(enableUpload ? attachmentUploadActions.actionHandlers : {}),
			...additionalActions.actionsHandlers
		}
	};
};
