import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./tagList.scss";
import {
	VIEWABLE_BY,
	LABELS,
	COMPONENT_ADD_TAG,
	COMPONENT_TAG_LIST
} from "../constants";
import { tagListActions } from "./tagListActions";
import {
	tagListSection,
	createTagSections,
	createTitle,
	handleClick,
	clearInputOnReopen
} from "./tagListUtils";
import "../addTag/addTag";
import _ from "lodash";

const view = (state, { dispatch, updateState }) => {
	const {
		properties: { taglist, table, sysId, displayValue },
		openDropdown
	} = state;

	const { tagListMe, tagListGroups, tagListEveryone } = createTagSections(
		taglist
	);
	const title = createTitle(table, displayValue);
	return (
		<div className="sn-tag-component">
			<div
				className="sn-tag-component-container"
				on-click={e => {
					handleClick(e, dispatch);
				}}
			>
				<div className="sn-tag-component--headercontainer">
					<span className="sn-tag-component--header">{LABELS.TAGS}</span>
				</div>
				<COMPONENT_ADD_TAG
					dropdown={openDropdown}
					table={table}
					sysId={sysId}
					displayValue={title}
					existingTags={taglist}
				/>
				<div className="sn-tag-component-sections">
					{tagListSection(tagListMe, VIEWABLE_BY.ME.displayValue)}
					{tagListSection(
						tagListGroups,
						VIEWABLE_BY.GROUPS_AND_USERS.displayValue
					)}
					{tagListSection(tagListEveryone, VIEWABLE_BY.EVERYONE.displayValue)}
				</div>
			</div>
		</div>
	);
};

export default createCustomElement(COMPONENT_TAG_LIST, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		openDropdown: false
	},
	properties: {
		taglist: { default: [] },
		table: { default: "" },
		sysId: { default: "" },
		displayValue: { default: "" },
		pillToFocus: { default: "" }
	},
	actions: tagListActions.actions,
	actionHandlers: {
		...tagListActions.actionHandlers
	},
	styles
});
