import _ from "lodash";
import { transformer } from "@devsnc/sn-declarative-actions";

const ATTACHMENT_ELEMENT = "now-record-common-attachments-connected";

// get default action will always return the first action as a default action if opened is true.
const getDefaultAction = (opened, actions = []) => {
	if (opened && actions[0]) return actions[0].name;

	return "";
};

const getActionByName = (actionName, actions = []) =>
	actions.find(action => action.name === actionName);

const getActionByComponentName = (actionComponentName, actions = []) =>
	actions.find(action => action.actionComponent === actionComponentName);

const getSelectedOrDefaultAction = (opened, selectedAction, actions = []) => {
	if (selectedAction) {
		// if selectedAction is set, check to make sure it's existing in current actions, otherwise default to selected action.
		const action = getActionByName(selectedAction, actions);
		return !_.isEmpty(action) ? action.name : getDefaultAction(opened, actions);
	}

	return selectedAction || getDefaultAction(opened, actions);
};

export const isAttachmentAvailable = actions =>
	!!getActionByComponentName(ATTACHMENT_ELEMENT, actions);

export const sidebarTransformer = (state, properties) => {
	if (_.isEmpty(properties)) return { ...state };
	const { actions = [], formModel, opened } = properties;
	const { selectedAction } = state;

	const transformedActions = transformer(actions, formModel);
	const newSelectedAction = getSelectedOrDefaultAction(
		opened,
		selectedAction,
		transformedActions
	);
	const attachmentAction = getActionByComponentName(
		ATTACHMENT_ELEMENT,
		actions
	);

	return {
		...state,
		transformedActions,
		selectedAction: newSelectedAction,
		renderedPanels: {
			...(state.renderedPanels || {}),
			...(newSelectedAction ? { [newSelectedAction]: true } : {}),
			...(!!attachmentAction ? { [attachmentAction.name]: true } : {}) //if there's an attachment in the side panel, we want to load it right away because we need to setup drag and drop
		}
	};
};

export const toggleSelectedOrDefaultAction = (state, properties) => {
	const { opened } = properties;
	const { transformedActions, selectedAction: currentSelectedAction } = state;

	const selectedAction = getSelectedOrDefaultAction(
		opened,
		currentSelectedAction,
		transformedActions
	);

	return {
		...state,
		selectedAction
	};
};

export const isAttachmentInSidebar = (formModel, actions) => {
	// needs to transform raw actions in case it's filtered out from client condition.
	const transformedActions = transformer(actions, formModel);
	return isAttachmentAvailable(transformedActions);
};
