import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@seismic/snabbdom-renderer';
import styles from './styles.scss';
import view from './view';
import { actionHandlers } from './actionHandlers';
import {
	RIBBON_OPEN_PHONE,
	RIBBON_PREVIEW_RECORD,
	RIBBON_ADD_NOTIFICATIONS,
	RIBBON_REMOVE_NOTIFICATIONS,
	RIBBON_CLEAR_NOTIFICATIONS,
	RIBBON_ITEM_SELECTED,
	RIBBON_EXECUTED_UI_ACTION,
	RIBBON_WRAPPED_ACTION
} from '../constants/ribbonConstants';

const RIBBON_ELEMENT = 'sn-record-ribbon';

const valueSchema = {
	type: 'object',
	properties: {
		value: { type: 'string' },
		displayValue: { type: 'string' }
	}
};

/**
 * The ribbon displays widgets for requestors and fulfillers who are viewing and updating records and record pages.
 *
 * ```jsx
 * <sn-record-ribbon
 *		table={...}
 *		sysId={...}
 *		widgets={...}
 *		active={...}
 *		canCollapse={...}
 *		widgets={...}
 *		collapsed={...}
 *		/>
 * ```
 *
 * @seismicElement sn-record-ribbon
 * @summary Ribbon is a container that holds one or more ribbon widget sub-components.
 */

createCustomElement(RIBBON_ELEMENT, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		widgetsLoading: true,
		tagsLoaded: []
	},
	properties: {
		/**
		 * Table of the record.
		 * @type {string}
		 */
		table: {
			schema: {
				type: 'string'
			},
			default: ''
		},

		/**
		 * Sys ID of the record.
		 * @type {string}
		 */
		sysId: {
			schema: {
				type: 'string'
			},
			default: ''
		},

		/**
		 * Widget objects passed to the ribbon.
		 * @type {array}
		 */
		widgets: {
			schema: {
				type: 'array',
				items: [
					{
						type: 'object',
						properties: {
							table: {
								...valueSchema
							},
							name: {
								...valueSchema
							},
							attrs: {
								type: 'object',
								properties: {
									variables: {
										type: 'object',
										properties: {
											variableName: { type: 'string' },
											variableValue: { type: 'string' }
										}
									}
								}
							},
							component: {
								...valueSchema
							},
							tag: {
								...valueSchema
							},
							width: {
								...valueSchema
							},
							order: {
								...valueSchema
							}
						},
						additionalProperties: true
					}
				]
			},
			default: []
		},

		/**
		 * When true, the record is active.
		 * @type {boolean}
		 */
		active: {
			schema: {
				type: 'boolean'
			},
			default: false
		},

		/**
		 * When true, the component is visible.
		 * @type {boolean}
		 */
		isVisible: {
			schema: {
				type: 'boolean'
			},
			default: false
		},

		/**
		 * When true, the component is collapsed.
		 * @type {boolean}
		 */
		collapsed: {
			schema: {
				type: 'boolean'
			},
			default: true
		},

		/**
		 * When true, the component is collapsible.
		 * @type {boolean}
		 */
		canCollapse: {
			schema: {
				type: 'boolean'
			},
			default: true
		},

		/**
		 * When true, the component is rendered in a workspace form.
		 * @type {boolean}
		 */
		classicForm: {
			schema: {
				type: 'boolean'
			},
			default: false
		},
		/**
		 * When true, an Openframe is configured.
		 * @type {boolean}
		 */
		isOpenframeConfigValid: {
			schema: {
				type: 'boolean'
			},
			default: false
		}
	},
	dispatches: {
		/**
		 * Dispatched to open the phone feature.
		 * @type {OpenPhone}
		 */
		[RIBBON_OPEN_PHONE]: {},
		/**
		 * Dispatched to preview a record.
		 * @type {PreviewRecordPayload}
		 */
		[RIBBON_PREVIEW_RECORD]: {},
		/**
		 * Dispatched to show notification messages.
		 * @type {RibbonAddNotificationsPayload}
		 */
		[RIBBON_ADD_NOTIFICATIONS]: {},
		/**
		 * Dispatched to remove notification message.
		 * @type {RibbonRemoveNotificationsPayload}
		 */
		[RIBBON_REMOVE_NOTIFICATIONS]: {},
		/**
		 * Dispatched to clear all notification messages.
		 */
		[RIBBON_CLEAR_NOTIFICATIONS]: {},
		/**
		 * Dispatched when an item is selected.
		 * @type {ItemSelectedPayload}
		 */
		[RIBBON_ITEM_SELECTED]: {},
		/**
		 * Dispatched to execute a UI action.
		 * @type {UIAction}
		 */
		[RIBBON_EXECUTED_UI_ACTION]: {},
		/**
		 * Dispatched to wrap an intended action in a payload.
		 * @type {WrappedAction}
		 */
		[RIBBON_WRAPPED_ACTION]: {}
	},
	actionHandlers,
	styles
});

export default RIBBON_ELEMENT;
