import keyBindingBehavior from "@servicenow/behavior-key-binding";
import { TYPEAHEAD } from "../constants";
const findFirstLi = host => host.shadowRoot.querySelector("li");
const findLastLi = host => host.shadowRoot.querySelector("li:last-of-type");
const behaviors = [
	{
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				moveFocus: ["Home", "ArrowUp", "ArrowDown", "End", "Tab"],
				dismiss: ["Escape", " ", "Enter"],
				select: [" ", "Enter"]
			},
			handlers: {
				moveFocus: function(host, event, dispatch) {
					const currentFocus = host.shadowRoot.querySelector(":focus");
					let nextFocus;

					switch (event.key) {
						case "Tab": {
							nextFocus = host.parentNode.querySelector("input");
							nextFocus.focus();
							dispatch(TYPEAHEAD.ACTIONS.DROPDOWN_PANEL_OPENED_SET, {
								value: false
							});
							event.stopPropagation();
							return;
						}
						case "Home": {
							nextFocus = findFirstLi(host);
							break;
						}
						case "ArrowDown": {
							const nextLi = currentFocus.nextElementSibling;
							nextFocus = nextLi ? nextLi : findFirstLi(host);
							break;
						}
						case "ArrowUp": {
							const previousLi = currentFocus.previousElementSibling;
							nextFocus = previousLi ? previousLi : findLastLi(host);
							break;
						}
						case "End": {
							nextFocus = findLastLi(host);
							break;
						}
					}
					event.preventDefault();
					const listbox = host.shadowRoot.querySelector("ul");
					if (nextFocus && nextFocus !== currentFocus) {
						nextFocus.scrollIntoView();
						listbox.setAttribute("aria-activedescendant", nextFocus.id);
						nextFocus.focus();
					}
				},
				dismiss: function(host, event, dispatch) {
					event.stopPropagation();
					dispatch(TYPEAHEAD.ACTIONS.DROPDOWN_PANEL_OPENED_SET, {
						value: false
					});
					const parent = host.getRootNode();
					if (parent) {
						const input = parent.querySelector("input");
						if (input) input.focus();
					}
				},
				select: function(host, event) {
					event.preventDefault();
					const listbox = host.shadowRoot.querySelector("ul");
					const currentFocus = host.shadowRoot.querySelector(
						"#" + listbox.getAttribute("aria-activedescendant")
					);
					if (currentFocus) {
						currentFocus.click();
					}
				}
			}
		}
	}
];
export default behaviors;
