import { ACTIONS, GQL_RESULTS } from "../constants";
import { actionTypes } from "@servicenow/ui-core";
import { EDIT_TAG_MODAL } from "../editTagModal/constants";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { TAG_QUERIES } from "./tagGraphQLQuery";
import _ from "lodash";
import {
	executeEffectIfValidEventSource,
	createEffectsInterceptors
} from "@devsnc/library-form";
import { SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN } from "@devsnc/sn-scripted-modal";
import { getSelectNode } from "../../../utils/domUtils";
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const getTagStateFromQuery = (queryResult) => {
	if (!queryResult) return;

	const {
		canCreateGlobalTags,
		enabled: tagEnabled,
		records = []
	} = queryResult;
	const tags = records.map(
		({ sysId, name, viewableBy, canEdit, labelEntry }) => ({
			sysId,
			name,
			viewableBy,
			canEdit,
			labelEntry
		})
	);
	return {
		tagEnabled,
		canCreateGlobalTags,
		tags,
		getTagsFromProps: false
	};
};

const fetchViewableTags = createGraphQLEffect(TAG_QUERIES.TAGS_GET_QUERY, {
	variableList: ["table", "sysId"],
	successActionType: ACTIONS.TAG_LOAD_SUCCESS,
	errorActionType: ACTIONS.TAG_GRAPHQL_ERROR
});

const loadTagsInterceptor = context => {
	const { coeffects } = context;
	const { state, action } = coeffects;
	const {
		properties: { table, sysId },
	} = state;
	return {
		...context,
		effects: [
			{
				effect: fetchViewableTags.effect,
				args: [
					...fetchViewableTags.args,
					{
						...coeffects,
						action: {
							...action,
							type: ACTIONS.TAGS_LOAD,
							payload: { table, sysId }
						}
					}
				]
			}
		]
	};
};

const deleteTagLabelEntry = createGraphQLEffect(
	TAG_QUERIES.TAG_DELETE_LABEL_ENTRY,
	{
		variableList: ["sysId"],
		successActionType: ACTIONS.TAG_DELETE_LABEL_SUCCESS,
		errorActionType: ACTIONS.TAG_GRAPHQL_ERROR
	}
);

const createSetProperFocusOnOpenCloseEffect = () => {
	return {
		effect: ({ state, updateState }) => {
			if (state.editMode) return;
			// when opening we want to focus the add tag input element
			// verify the taglist is opened and not in editMode
			// have to check the opposite for opened because we just dispatched the state
			// change for opened and it value change hasnt taken effect yet
			if (!state.opened) {
				setTimeout(() => {
					updateState(() => {
						const popoverPanelContent = document.body.querySelector(
							"seismic-hoist"
						);
						if (popoverPanelContent) {
							const inputEl = popoverPanelContent.shadowRoot
								.querySelector("sn-form-internal-add-tag")
								.shadowRoot.querySelector("input");

							if (inputEl) inputEl.focus();
						}

						return { shouldUpdate: false };
					});
				}, 250);
			}
		}
	};
};

const focusPill = ({ updateState }) => {
	setTimeout(() => {
		updateState(({ state }) => {
			const popoverPanelContent = document.body.querySelector("seismic-hoist");
			const pill = popoverPanelContent.shadowRoot.querySelector(
				`now-pill[data-id='${state.editTagId}']`
			);
			if (pill) pill.shadowRoot.querySelector("button").focus();
			return { shouldUpdate: false };
		});
	}, 250);
};

export default {
	actions: {
		[ACTIONS.TAGS_LOAD]: {
			private: true
		},
		[ACTIONS.TAG_LOAD_SUCCESS]: {
			private: true
		},
		[ACTIONS.TAG_GRAPHQL_ERROR]: {
			private: true
		},
		[ACTIONS.TAG_ICON_CLICKED]: {
			private: true
		},
		[ACTIONS.NOW_PILL_DISMISSED]: {
			private: true
		},
		[ACTIONS.TAG_DELETE]: {
			private: true
		},
		[ACTIONS.TAG_DELETE_LABEL_SUCCESS]: {
			private: true
		},
		[ACTIONS.NOW_PILL_SELECTION_SET]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.OPENED_SET]: {
			private: true
		},
		[EDIT_TAG_MODAL.ACTIONS.TAG_UPDATED]: {
			private: true
		},
		[ACTIONS.SET_TAG_STATE]: {
			private: true
		}
	},
	actionHandlers: {
		[ACTIONS.BOOTSTRAP_TAG]: {
			effect: () => {},
			interceptors: [loadTagsInterceptor],
			stopPropagation: true
		},
		[ACTIONS.TAGS_LOAD]: fetchViewableTags,
		[ACTIONS.TAG_LOAD_SUCCESS]: ({ updateState, state, action }) => {
			const queryResult = _.get(
				action,
				GQL_RESULTS.TAG_FETCH_VIEWABLE_RECORD_TAGS
			);
			const updatedState = getTagStateFromQuery(queryResult);
			if (updatedState)
				updateState(updatedState);
		},

		[ACTIONS.TAG_GRAPHQL_ERROR]: ({ action }) => {
			console.warn(`ERROR: ${action}`);
		},
		[ACTIONS.TAG_ICON_CLICKED]: executeEffectIfValidEventSource(
			ACTIONS.EVENT_SOURCE,
			{
				effect: ({ updateState, state }) => {
					updateState({ tagIconJustClicked: false, opened: !state.opened });
				}
			},
			[createSetProperFocusOnOpenCloseEffect()]
		),
		[ACTIONS.NOW_PILL_DISMISSED]: ({ action, dispatch }) => {
			const sysId = action.meta.componentName;
			dispatch(ACTIONS.TAG_DELETE, { sysId });
		},
		[ACTIONS.TAG_DELETE]: deleteTagLabelEntry,
		[ACTIONS.TAG_DELETE_LABEL_SUCCESS]: ({ dispatch, action, state }) => {
			if (_.get(action, GQL_RESULTS.TAG_DELETED_COUNT, 0) === 1) {
				const {
					properties: { table, sysId }
				} = state;
				dispatch(ACTIONS.TAGS_LOAD, { table, sysId });
			}
		},
		[ACTIONS.TAG_UPDATE_REFLECT]: ({
			state: {
				properties: { table, sysId }
			},
			dispatch
		}) => {
			dispatch(ACTIONS.TAGS_LOAD, { table, sysId });
		},
		[ACTIONS.NOW_PILL_SELECTION_SET]: ({
			updateState,
			action: {
				payload: { tagId: editTagId, canEdit }
			},
			dispatch,
			state: { hoistOverlayToContainer }
		}) => {
			updateState({ editMode: canEdit, editTagId, opened: !canEdit });
			if (hoistOverlayToContainer) dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, true);
		},
		[EDIT_TAG_MODAL.ACTIONS.OPENED_SET]: createEffectsInterceptors([
			({
				updateState,
				action: {
					payload: { value: editDialogOpened }
				},
				dispatch,
				state: { hoistOverlayToContainer }
			}) => {
				updateState({ editMode: editDialogOpened, opened: !editDialogOpened });
				if (hoistOverlayToContainer) dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, editDialogOpened);
			},
			focusPill
		]),
		[EDIT_TAG_MODAL.ACTIONS.TAG_UPDATED]: ({
			updateState,
			state,
			action: {
				payload: { tag }
			}
		}) => {
			const {
				name: { value: name },
				viewable_by: { value: viewableBy }
			} = tag;
			const { tags, editTagId } = state;

			updateState({
				editMode: false,
				opened: true,
				tags: tags.map(tag =>
					tag.sysId === editTagId ? { ...tag, name, viewableBy } : { ...tag }
				)
			});
		},
		[ACTIONS.SET_TAG_STATE]: ({ updateState, action: { payload } }) => {
			updateState({ ...payload });
		},
		[COMPONENT_PROPERTY_CHANGED]: coeffects => {
			const {
				action: {
					payload: { name, value, previousValue }
				},
				dispatch,
				updateState,
				properties: { table, sysId }
			} = coeffects;
			if (
				table &&
				sysId &&
				(name === "table" || name === "sysId") &&
				!_.isEqual(value, previousValue)
			) {
				dispatch(ACTIONS.TAGS_LOAD, { table, sysId });
			}

			if (name === "recordTags" && !_.isEqual(value, previousValue)) {
				const newState = getTagStateFromQuery(value);
				if (newState)
					updateState(newState);
			}

		},
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch, host, properties, state, updateState }) => {
			const { opened, overlayContainer, sysId, table, recordTags } = properties;
			const tagsFromProps = state.getTagsFromProps ? recordTags : undefined;

			let updatedState = {};

			if (table && sysId && !tagsFromProps) {
				dispatch(ACTIONS.BOOTSTRAP_TAG, { table, sysId });
			} else {
				updatedState = getTagStateFromQuery(recordTags);
			}

			const containerNode = overlayContainer
				? getSelectNode(host.shadowRoot, overlayContainer)
				: null;

			updateState({
				...updatedState,
				hoistOverlayToContainer: containerNode,
				opened
			});
		},
		[COMPONENT_DISCONNECTED]: ({ updateState }) => {
			updateState({
				hoistOverlayToContainer: undefined
			});
		},
	}
};
