import snabbdom, { Fragment } from "@servicenow/ui-renderer-snabbdom";
import { t } from "sn-translate";
import _ from "lodash";
import { focusTrapHandler } from "../../utils/focusTrap";
import { attachmentActionHandler } from "./preview/attachmentPreviewUtil";
import {
	ATTACHMENT_PREVIEW_DISMISS_ACTION,
	ALLOWED_MIME_TYPE
} from "./constants";
import {
	ATTACHMENT_DOWNLOAD_ACTION,
	ATTACHMENT_DELETE_ACTION
} from "../../constants/attachmentConstants";
import classNames from "classnames/dedupe";
import { loadComponent } from "@devsnc/library-form";
import "@servicenow/now-loader";

const BUTTON_TYPES = {
	DOWNLOAD: "download",
	DELETE: "delete",
	CLOSE: "close"
};
const DOC_VIEWER_STATUS = {
	LOADING: "loading",
	READY: "ready",
	FAILED: "failed"
};
const TAG_SN_DOCUMENT_VIEWER = "sn-document-viewer";

export function loadDocViewer(currentDocViewerState, updateState) {
	if (currentDocViewerState) return;
	const isDefined = !!customElements.get(TAG_SN_DOCUMENT_VIEWER);
	if (isDefined) {
		updateState({
			docViewerState: DOC_VIEWER_STATUS.READY,
			shouldRender: false
		});
		return;
	}
	loadComponent(TAG_SN_DOCUMENT_VIEWER).then((loadedComponents) => {
		if (loadedComponents.length === 0)
			updateState({
				docViewerState: DOC_VIEWER_STATUS.FAILED
			});
	});
	updateState({
		docViewerState: DOC_VIEWER_STATUS.LOADING,
		shouldRender: false
	});
	customElements.whenDefined(TAG_SN_DOCUMENT_VIEWER).then(() => {
		updateState({
			docViewerState: DOC_VIEWER_STATUS.READY
		});
	});
}
/**
 * This utility function returns true if mime type of the attachment is allowed for preview,
 * and false if not.
 * @param type mime type of tha attachment currently in preview
 */
const isAllowedMime = type => {
	return type
		? ALLOWED_MIME_TYPE.includes(type.substring(0, type.indexOf("/")))
		: false;
};

const renderActionButton = ({
	tooltip,
	type,
	callback,
	additionalClass = "",
	buttonType,
	componentIdPrefix
}) => {
	const ariaConfig = {
		"aria-label": tooltip
	};
	return (
		<now-button
			className={`action-button ${additionalClass}`}
			variant="inherit"
			bare
			component-id={`${componentIdPrefix}_overlaybtn_${buttonType}`}
			config-aria={ariaConfig}
			icon-name={type}
			onclick={callback}
			tooltip-content={tooltip}
			ref={input => {
				if (input && buttonType === BUTTON_TYPES.DOWNLOAD)
					setTimeout(() => {
						const button = input.shadowRoot
							? input.shadowRoot.querySelector("button")
							: null;
						if (!button) return;
						button.focus();
						const event = new FocusEvent("focus", {
							view: window,
							bubbles: true,
							cancelable: true,
							composed: true
						});
						button.dispatchEvent(event);
					}, 100);
			}}
		/>
	);
};

const renderActionButtonType = (dispatch, item, type, componentIdPrefix) => {
	const createCallback = actionName =>
		_.partial(attachmentActionHandler, dispatch, actionName, item);

	switch (type) {
		case "download":
			return renderActionButton({
				tooltip: t("Download {0}", item.name),
				type: "download-outline",
				callback: createCallback(ATTACHMENT_DOWNLOAD_ACTION),
				buttonType: BUTTON_TYPES.DOWNLOAD,
				componentIdPrefix
			});
		case "delete":
			return renderActionButton({
				tooltip: t("Delete {0}", item.name),
				type: "trash-outline",
				callback: createCallback(ATTACHMENT_DELETE_ACTION),
				buttonType: BUTTON_TYPES.DELETE,
				componentIdPrefix
			});
		case "preview":
			return renderActionButton({
				tooltip: t("Close {0} preview", item.name),
				type: "close-outline",
				callback: createCallback(ATTACHMENT_PREVIEW_DISMISS_ACTION),
				additionalClass: "--dismiss",
				buttonType: BUTTON_TYPES.CLOSE,
				componentIdPrefix
			});
	}
};

const handleEsc = dispatch => {
	return e => {
		if (e.key === "Escape") dispatch(ATTACHMENT_PREVIEW_DISMISS_ACTION);
	};
};

/**
 * Return the file extension if found else return ''.
 */
const getExtensionFromFileName = name => {
	if (!name || name.lastIndexOf(".") === -1) return "";

	return name.substring(name.lastIndexOf(".") + 1).toLowerCase();
};

/**
 * This utility function returns back the no preview message
 * @param name name of the attachment
 */
const getNoPreviewMessage = name => {
	return t(
		"We're sorry, {0} files are currently not supported.",
		name ? getExtensionFromFileName(name).toUpperCase() : "these"
	);
};

/**
 * Renders the preview not available message block
 * @param item object with attachment meta
 */
const renderPreviewNotAvailable = item => {
	const attachmentOverlayMessage = getNoPreviewMessage(item.name);
	return (
		<div className="no-preview">
			<div className="no-preview-icon">
				<sn-file-icon
					className="sn-icon"
					size="2x"
					mime-type={item.type}
					extension={getExtensionFromFileName(item.name)}
				/>
			</div>
			<div className="title">{t("Preview not available")}</div>
			<span>{attachmentOverlayMessage}</span>
		</div>
	);
};

/**
 * This function will open the attachment preview overlay
 *
 * @param item object with meta data about the attachment
 * @param dispatch dispatch function
 * @param dismissPreview callback to dismiss the attachment preview
 */
export const renderAttachmentPreviewOverlay = (
	item,
	dispatch,
	isReadOnly,
	isHoisted,
	componentIdPrefix,
	docViewerEnabled,
	docViewerState
) => {
	if (!item.name) return null;
	const deleteHidden = isReadOnly || !item.canDelete || item.readonly;
	return (
		<div
			className={classNames("attachment-overlay preview", {
				"no-hoist": !isHoisted
			})}
			tabIndex="-1"
			on-keyup={handleEsc(dispatch)}
			role="dialog"
		>
			{docViewerEnabled && docViewerState === DOC_VIEWER_STATUS.READY ? (
				<sn-document-viewer
					sys-id={item.sysId}
					delete-hidden={`${deleteHidden}`}
				/>
			) : docViewerEnabled && docViewerState === DOC_VIEWER_STATUS.LOADING ?
				<div className="attachment-preview--body">
					<now-loader size="lg"></now-loader>
				</div>
				:
				(
				<Fragment>
					<div className="attachment-preview--header">
						<span className="title">{item.name}</span>
						<div
							on-focus={focusTrapHandler}
							className="actions">
							{renderActionButtonType(
								dispatch,
								item,
								BUTTON_TYPES.DOWNLOAD,
								componentIdPrefix
							)}
							{!deleteHidden
								? renderActionButtonType(
										dispatch,
										item,
										BUTTON_TYPES.DELETE,
										componentIdPrefix
								  )
								: null}
							{renderActionButtonType(
								dispatch,
								item,
								"preview",
								componentIdPrefix
							)}
						</div>
					</div>
					<div className="attachment-preview--body">
						{isAllowedMime(item.type) ? (
							<img
								className="preview-image"
								src={`/${item.sysId}.iix`}
								alt={item.name}
							/>
						) : null}
						{!isAllowedMime(item.type) ? renderPreviewNotAvailable(item) : null}
					</div>
				</Fragment>
			)}
		</div>
	);
};

export const renderAttachmentOverlayView = (properties, dispatch, state) => {
	const {
		attachmentPreviewItem,
		showAttachmentPreview,
		isReadOnly,
		nowHoistPrependTo,
		docViewerState,
		docViewerEnabled
	} = properties;
	const { componentId } = state;
	return (
		<Fragment>
			{showAttachmentPreview && attachmentPreviewItem
				? renderAttachmentPreviewOverlay(
						attachmentPreviewItem,
						dispatch,
						isReadOnly,
						!!nowHoistPrependTo,
						componentId,
						docViewerEnabled,
						docViewerState
				  )
				: null}
		</Fragment>
	);
};
