import react from "@servicenow/ui-renderer-react";
import { createCustomElement, actionTypes } from "@servicenow/ui-core";
import { SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN } from "@devsnc/sn-scripted-modal";
import { createHttpEffect } from "@servicenow/ui-effect-http";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { AttachmentGraphQLQuery as read } from "./datasources/AttachmentGraphQLQuery.js";
import { AttachmentGraphQLUpdate as update } from "./datasources/AttachmentGraphQLUpdate.js";
import { default as Attachment } from "./Attachment";
import {
	ATTACHMENT_ACTIONS,
	ATTACHMENT_DELETE_ACTION,
	ATTACHMENT_DELETE_CONFIRM_ACTION,
	ATTACHMENT_UPLOAD_BUTTON_CLICKED,
	NOW_MODAL_OPENED_SET_ACTION,
	ATTACHMENT_MODAL_CLOSED_ACTION,
	ATTACHMENT_ADD_NOTIFICATIONS,
	ATTACHMENT_UPLOAD_SUCCEEDED
} from "../constants/attachmentConstants.js";
import { ADD_NOTIFICATIONS } from "../constants/constants";
import { SEARCH_INPUT_ENTERED } from "../constants/searchbarConstants.js";
import { createAttachmentActionHandlers } from "./overlay/attachmentActions";
import { ATTACHMENT_DELETE } from "./overlay/constants";
import { getSelectNode } from "../../utils/domUtils";
import { loadDocViewer } from "./overlay/attachmentOverlayUtil";

import _ from "lodash";
import { t } from "sn-translate";
import styles from "./Attachment.scss";
const {
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED,
	COMPONENT_BOOTSTRAPPED
} = actionTypes;
const queryAttachmentEntries = createGraphQLEffect(read, {
	variableList: ["sysId", "table"],
	successActionType: ATTACHMENT_ACTIONS.ATTACHMENT_READ_SUCCESS,
	errorActionType: ATTACHMENT_ACTIONS.ATTACHMENT_READ_ERROR
});

const updateAttachmentEntry = createGraphQLEffect(update, {
	variableList: ["sys_id", "file_name"],
	successActionType: ATTACHMENT_ACTIONS.ATTACHMENT_REFRESH_REQUESTED,
	errorActionType: ATTACHMENT_ACTIONS.ATTACHMENT_DELETE_OR_UPDATE_ERROR
});

const getAttachmentListFromData = (attachments, searchString) => {
	const list = !searchString
		? [...attachments]
		: attachments.filter(item =>
				item.name.toLowerCase().includes(searchString)
		  );

	return { list, count: list.length };
};

const focusPreviousCardOrBrowseButton = (state, sysId) => {
	if (!state || !state.fileInputRef || !state.fileInputRef.parentElement) return;
	const attachmentList = state.fileInputRef.parentElement.querySelector("sn-form-internal-attachments-list");
	if (attachmentList && attachmentList.shadowRoot) {
		const cards = attachmentList.shadowRoot.querySelectorAll("now-record-common-attachment-card");
		const attachmentIndexToBeDeleted = state.list.findIndex(attachment => attachment.sysId === sysId);
		const cardIndexToFocus = attachmentIndexToBeDeleted > 0 ? attachmentIndexToBeDeleted - 1 : 0;
		if (cards && cards.length > 1) setTimeout(() => { cards[cardIndexToFocus].shadowRoot.querySelector("button").focus() }, 200);
		else setTimeout(() => { 
			const browseButton = attachmentList.shadowRoot.querySelector("now-button");
			if (browseButton) browseButton.focus();
		}, 1000);
	}
}

const readSuccessHandler = {
	effect: ({ state, updateState, action, dispatch }) => {
		const data = {
			attachments: _.get(action, "payload.data.query.model.attachments", []),
			maxFileNameLength: _.get(
				action,
				"payload.data.query.model.fileNameLength",
				0
			),
			docViewerEnabled: _.get(
				action,
				"payload.data.query.model.docViewerEnabled",
				false
			),
			canCreate: _.get(action, "payload.data.query.model.canCreate", true),
			extensionString: _.get(action, "payload.data.query.model.extensions", ""),
			count: _.get(action, "payload.data.query.model.count", 0)
		};
		const { loadingItems, docViewerState } = state;
		const { docViewerEnabled, attachments } = data;
		if (docViewerEnabled && attachments.length > 0)
			loadDocViewer(docViewerState, updateState);

		const sysIds = data.attachments.map(attachment => attachment.sysId);
		const cleanedUpItems = loadingItems.filter(
			item => !sysIds.includes(item.sysId)
		);

		updateState({
			attachmentsInitialized: true,
			docViewerEnabled,
			data,
			...getAttachmentListFromData(data.attachments, state.searchString),
			loadingItems: cleanedUpItems
		});
	}
};

const handlerError = (dispatch, actionName, errorMessage) => {
	if (_.isEmpty(errorMessage)) return;
	const message = actionName
		? t(`Error executing ${actionName}: ${errorMessage}`)
		: t(errorMessage);
	dispatch(ADD_NOTIFICATIONS, {
		notifications: [
			{
				type: "error",
				message
			}
		]
	});
};

export const addNotificationsEffect = ({
	action,
	properties: { classicForm },
	dispatch
}) => {
	if (classicForm) return;

	const {
		payload: { notifications }
	} = action;
	dispatch(ATTACHMENT_ADD_NOTIFICATIONS, {
		items: notifications
	});
	action.stopPropagation();
};

export const uploadAttachmentSucceededEffect = ({
	action: { payload, stopPropagation },
	dispatch
}) => {
	dispatch(ATTACHMENT_UPLOAD_SUCCEEDED, payload);
	stopPropagation();
};

const AttachmentElement = "now-record-common-attachments-connected";

const attachmentActions = createAttachmentActionHandlers(true);

/**
 * Component used to render the attachments associated with a record. It displays a list of the attachments
 * relating to the record and a header containing the component title, a searchbar, and a browse button.
 *
 * ```jsx
 * <now-record-common-attachments-connected
 *		table={...}
 *		sysId={...}
 *		isAttachmentUploadDisabled={...}
 *		isReadOnly={...}
 *		classicForm={...}
 *		mode={...}
 * />
 * ```
 *
 * @seismicElement now-record-common-attachments-connected
 * @summary Allows users to add, preview, download, or remove attachments from a related record. 
 *
 */
createCustomElement(AttachmentElement, {
	renderer: {
		type: react,
		view: Attachment
	},
	initialState: {
		modal: {
			display: false,
			type: "",
			data: null
		},
		searchString: "",
		showAttachmentUploadOverlay: false,
		showAttachmentPreview: false,
		lastTriggerCard: null,
		uploadAttachmentList: [],
		errorAttachmentList: [],
		uploadAttachmentListIndex: 0,
		showUploadFileModal: false,
		uploadFileDataURL: "",
		loadingItems: []
	},

	properties: {
		/**
		 * When true, the component is rendered in a classic form.
		 * @type {boolean}
		 */
		classicForm: {
			default: false,
			schema: {
				type: "boolean"
			}
		},
		/**
		 * When true, a user cannot upload an attachment.
		 * @type {boolean}
		 */
		isAttachmentUploadDisabled: {
			default: false,
			schema: {
				type: "boolean"
			}
		},
		/**
		 * When true, the attachments are read only.
		 * @type {boolean}
		 */
		isReadOnly: {
			default: false,
			schema: {
				type: "boolean"
			}
		},
		/**
		 * When true, document viewer should load.
		 * @type {boolean}
		 */
		loadDocViewerIfNecessary: {
			default: false,
			schema: {
				type: "boolean"
			}
		},
		/**
		 * The layout in which to render the component.
		 * Options include `full` and `compact`.
		 * @type {string}
		 */
		mode: { default: "full" },
		/**
		 * The Sys ID of the record to display attachments for.
		 * @type {string}
		 */
		sysId: {},
		/**
		 * The table of the record to display attachments for.
		 * @type {string}
		 */
		table: {},
		/**
		 * The record display value to show in the upload overlay.
		 */
		recordDisplayValue: { default: "" },
		/**
		 * Overlay container ID. The overlay container contains the preview overlay and upload overlay.
		 * If not specified, the topmost container in this component is used.
		 */
		overlayContainer: { default: "" },
		/**
		 * The attachment item to show in the preview.
		 * (Internal props so we can use preview overlay in attachment component from the container for activity stream)
		 * @private
		 */
		attachmentPreviewItem: {},
		/**
		 * Whether to render attachment preview or not
		 * (Internal props so we can use preview overlay in attachment component from the container for activity stream)
		 * @private
		 */
		showAttachmentPreview: {},
		/**
		 * Set this prop to timestamp to force re-fetch data
		 * @private
		 */
		refreshRequested: {},
		/**
		 * Disable upload modal and directly upload the files
		 * @type {boolean}
		 */
		disableUploadModal : { default : false }
	},
	dispatches: {
		/**
		 * Dispatched to show notification messages.
		 * @type {AddNotificationsPayload}
		 */
		[ADD_NOTIFICATIONS]: {},
		/**
		 * Dispatched to show notification messages when an attachment component is inside a record page in UI Builder.
		 * @type {AttachmentAddNotificationsPayload}
		 */
		[ATTACHMENT_ADD_NOTIFICATIONS]: {},
		/**
		 * Dispatched when upload is succesful.
		 */
		[ATTACHMENT_UPLOAD_SUCCEEDED] : {}
	},
	actions: {
		[ATTACHMENT_MODAL_CLOSED_ACTION]: {
			private: true
		},
		[NOW_MODAL_OPENED_SET_ACTION]: {
			private: true
		},
		[ATTACHMENT_DELETE_CONFIRM_ACTION]: {
			private: true
		},
		[ATTACHMENT_DELETE_ACTION]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_READ_SUCCESS]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_READ_ERROR]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_UPDATE_ACTION]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_UPLOAD_ERROR]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_DELETE_OR_UPDATE_ERROR]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_REFRESH_REQUESTED]: {
			private: true
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_UPLOAD_SUCCESS]: {
			private: true
		},
		[ATTACHMENT_UPLOAD_BUTTON_CLICKED]: {
			private: true
		},
		[SEARCH_INPUT_ENTERED]: {
			private: true
		},
		...attachmentActions.actions
	},

	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ host, properties, updateState }) => {
			const { overlayContainer } = properties;
			const containerNode = overlayContainer
				? getSelectNode(host.shadowRoot, overlayContainer)
				: null;
			updateState({
				hoistOverlayToContainer: containerNode,
				shouldRender: false
			});
		},
		[ATTACHMENT_MODAL_CLOSED_ACTION]: ({ updateState, state: { lastTriggerCard }, dispatch }) => {
			if (lastTriggerCard) lastTriggerCard.focus();
			updateState({
				path: "modal.display",
				operation: "set",
				value: false
			});
			dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, false);
		},
		[NOW_MODAL_OPENED_SET_ACTION]: ({ updateState, dispatch }) => {
			updateState({
				path: "modal.display",
				operation: "set",
				value: false
			});
			dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, false);
		},
		[ATTACHMENT_DELETE_CONFIRM_ACTION]: ({
			action,
			dispatch,
			updateState,
			state
		}) => {
			const { data } = action.payload.action;
			if (!data) return;
			const { action: actionType, sysId, name } = data;
			if (actionType === "delete") {
				dispatch(ATTACHMENT_DELETE.ACTIONS.START, {
					sys_id: sysId,
					name
				});
			}
			updateState({
				modal: {
					display: false
				},
				showAttachmentPreview: false,
				attachmentPreviewItem: null
			});
			focusPreviousCardOrBrowseButton(state, sysId)
			dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, false);
		},
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				action: {
					payload: { name, value, previousValue }
				},
				state: {docViewerEnabled, docViewerState},
				properties,
				dispatch,
				updateState
			} = context;
			if (
				(name === "table" || name === "sysId" || name === "refreshRequested") &&
				(value || "") !== (previousValue || "")
			) {
				const { table, sysId } = properties;
				const payload = {
					table: name === "table" ? value : table,
					sysId: name === "sysId" ? value : sysId
				};
				dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY, payload);
			} else if (
				(name === "showAttachmentPreview" ||
					name === "attachmentPreviewItem") &&
				(value || "") !== (previousValue || "")
			) {
				updateState({
					[name]: value
				});
			} else if (name === "loadDocViewerIfNecessary" && value && docViewerEnabled) {
				loadDocViewer(docViewerState, updateState);
			}
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_UPDATE_ACTION]: updateAttachmentEntry,
		/**
		 * Delete the attachment
		 * @param sysId sys_id of the attachment
		 */
		[ATTACHMENT_DELETE_ACTION]: ({ action, dispatch, updateState, state: { hoistOverlayToContainer} }) => {
			const { sysId, name } = action.payload;
			updateState({
				modal: {
					display: true,
					type: "delete_confirmation",
					data: {
						sysId,
						name,
						action: "delete"
					}
				}
			});
			if (hoistOverlayToContainer) dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, true);
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_DELETE_OR_UPDATE_ERROR]: ({
			dispatch,
			action
		}) => {
			const message = _.get(action, "payload.data.message", "");
			const actionName = _.get(action, "payload.name", "");
			handlerError(dispatch, actionName, message);
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY]: queryAttachmentEntries,
		[ATTACHMENT_ACTIONS.ATTACHMENT_READ_ERROR]: ({ dispatch, action }) => {
			const message = _.get(action, "payload.data.error.message", "");
			handlerError(dispatch, "", message);
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_UPLOAD_ERROR]: ({ dispatch, action }) => {
			const message = _.get(action, "payload.data.message", "");
			const actionName = _.get(
				action,
				"payload.options.params.executingFileName",
				""
			);
			handlerError(dispatch, actionName, message);
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_REFRESH_REQUESTED]: ({
			state,
			dispatch,
			action
		}) => {
			const { table, sysId } = state.properties;
			const error = _.get(action, "payload.errors", []);
			if (_.isEmpty(error))
				dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY, {
					sysId: sysId,
					table: table
				});
			else {
				const executingFileName = action.meta.options.variables.file_name;
				const message = error[0].message;
				if (_.isEmpty(message)) return;
				dispatch(ADD_NOTIFICATIONS, {
					notifications: [
						{
							type: "error",
							message: t(`Error executing ${executingFileName}: ${message}`)
						}
					]
				});
			}
		},
		[ATTACHMENT_ACTIONS.ATTACHMENT_READ_SUCCESS]: {
			...readSuccessHandler,
			interceptors: [
				{
					after: context => {
						const { state, dispatch, action } = context.coeffects;
						const count = _.get(action, "payload.data.query.model.count", 0);
						const value = count > 0 ? count.toString() : null;
						dispatch("NOW_RECORD_FORM#ACTION_STATUS_UPDATED", {
							component: AttachmentElement,
							type: "badge",
							value
						});
						const sessionNotifications = _.get(
							action,
							"payload.data.sessionQuery.session.notifications",
							[]
						);
						if (!sessionNotifications.length) return context;

						const notifications = sessionNotifications.reduce(
							(allNotifications, { type, text }) => {
								if (!text)
									return allNotifications;

								return [
									...allNotifications,
									{
										type,
										message: text
									}
								];
							}, []
						);
						if (notifications.length)
							dispatch(ADD_NOTIFICATIONS, {
								notifications
							});
						return context;
					}
				}
			]
		},
		[ATTACHMENT_UPLOAD_BUTTON_CLICKED]: ({ state }) => {
			// This approach is temporary and should be updated when migrating to Seismic 18.
			// As part of Seismic 18, all action handlers will pass in the host as a coeffect.
			// This will allow us to query select the file input node without having to store
			// the dom ref in the state
			state.fileInputRef.click();
		},
		[COMPONENT_DISCONNECTED]: ({ updateState }) => {
			updateState({
				fileInputRef: null,
				hoistOverlayToContainer: undefined
			});
		},
		[SEARCH_INPUT_ENTERED]: ({
			state: { data },
			action: { payload },
			updateState
		}) => {
			updateState({
				searchString: payload.filter,
				...getAttachmentListFromData(data.attachments, payload.filter)
			});
		},
		[ADD_NOTIFICATIONS]: addNotificationsEffect,
		[ATTACHMENT_ACTIONS.ATTACHMENT_UPLOAD_SUCCESS]: uploadAttachmentSucceededEffect,
		...attachmentActions.actionHandlers
	},
	styles
});

export default AttachmentElement;
