import { createCustomElement } from "@servicenow/ui-core";
import snabbdom, { Fragment } from "@servicenow/ui-renderer-snabbdom";
import styles from "./Header.scss";
import "./HeaderLabelValueSet.js";
import { getProperty } from "sn-uxpage-presource";
import "../tags/tag/tag";
import { t } from "sn-translate";
import classNames from "classnames/dedupe";
import "@servicenow/now-image";
import {
	PREVIEW_RECORD
} from "@devsnc/library-form";
import { EmptyStateElement } from "../emptyState";
import { HEADER_ACTIONS_PREVIEW_RECORD } from "./headerConst";

const TAGS_SYS_PROPERTIES = {
	GLIDE_UI_SHOW_FORM_TAGS_BAR: "glide.ui.show_form_tags_bar"
};

const tagSysProperty = getProperty(
	TAGS_SYS_PROPERTIES.GLIDE_UI_SHOW_FORM_TAGS_BAR,
	true
);

const tagSysPropertyWithDefault = (tagSysProperty !== null) ? tagSysProperty : true;

const acceptedProperties = {
	/**
	 * Value to show at the top of the header.
	 * @type {string}
	 */
	primaryValue: {
		default: "",
		required: true
	},
	/**
	 * Array of items to show labels and values for under the primary value.
	 * @type {(Array.<SecondaryItem>)}
	 */
	secondaryItems: {},
	/**
	 * Tags to load into the record tag component.
	 * @type {string[]}
	 */
	recordTags: {},
	/**
	 * Table for the record to display.
	 * @type {string}
	 */
	table: {
		default: ""
	},
	/**
	 * Sys ID of the record to show values for.
	 * @type {string}
	 */
	sysId: {
		default: ""
	},
	/**
	 * ID of the workspace. For example, agent, csm, or vsm.
	 * @type {string}
	 */
	workspaceConfigId: {
		default: ""
	},
	/**
	 * @deprecated
	 * @type {string}
	 */
	recordDisplayValue: {
		default: ""
	},
	/**
	 * When true, the component renders in the workspace form.
	 * @type {boolean}
	 */
	classicForm: {},
	/**
	 * Header item that describes the image details.
	 * @type {any<HeaderItem>}
	 */
	headerImageItem: {},
	/**
	 * Value to show as a subheading under the header.
	 * @type {string}
	 */
	subHeadingValue: {
		default: ""
	},
	/**
	 * When true, hides the tags icon.
	 * @type {boolean}
	 */
	hideTagsValue: {
		schema: {
			type: "boolean"
		},
		default: false
	},
	/**
	 * String of the selector for the container.
	 * @type {string}
	 */
	overlayContainer: {
		schema: {
			type: "string"
		},
		default: ""
	}
};

// Create view for renderer
export const view = state => {
	const {
		primaryValue,
		secondaryItems,
		table,
		sysId,
		recordDisplayValue,
		recordTags,
		classicForm,
		headerImageItem,
		subHeadingValue,
		hideTagsValue,
		overlayContainer
	} = state.properties;

	if (!primaryValue && !secondaryItems)
		return !classicForm ? (
			<EmptyStateElement message={t("No data to display")} />
		) : (
			<div />
		);

	const renderSubheadingDisplay = () => {
		return subHeadingValue ? (
			<div className="sn-heading-row subheading">
				<p>{subHeadingValue}</p>
			</div>
		) : (
			<Fragment />
		);
	};

	const renderHeaderImageDisplay = () => {
		return !headerImageItem || !headerImageItem.headerImage ? (
			<Fragment />
		) : (
			<now-image
				src={headerImageItem.headerImage}
				alt={headerImageItem.fieldLabel}
				className="headerImageConstraints"
				fit="scale-down"
			/>
		);
	};

	const containerClassNames = {
		"sn-heading-container": true,
		headerImage: headerImageItem !== null,
		subHeading: !!subHeadingValue
	};

	return (
		<div className={`${classNames(containerClassNames)}`}>
			<div className="sn-heading-row">
				{renderHeaderImageDisplay()}
				<div className="sn-heading-content">
					<div className="sn-heading-row">
						<h1 className="sn-heading">{primaryValue}</h1>
						{tagSysPropertyWithDefault && !hideTagsValue ? (
							<sn-record-tags-connected
								table={table}
								sysId={sysId}
								displayValue={recordDisplayValue}
								recordTags={recordTags}
								classicForm={classicForm}
								overlayContainer={overlayContainer}
								headerSubComponent={true}
							/>
						) : (
							<Fragment />
						)}
					</div>
					{renderSubheadingDisplay()}
					<div className="sn-heading-row">
						<sn-form-internal-header-label-value-set items={secondaryItems} />
					</div>
				</div>
			</div>
		</div>
	);
};

const HEADER_COMPONENT = "now-record-common-header";

/**
 * Main container for a record or record page. Displays a primary item at the top such as a name and description,
 * and a number of field label-value pairs.
 *
 * ```jsx
 * <now-record-common-header
 *		table={...}
 *		sysId={...}
 *		primaryValue={...}
 *		secondaryItems={...}
 *		recordTags={...}
 *		subHeadingValue={...}
 *		headerImageItem={...}
 *		hideTagsValue={...}
 *		/>
 * ```
 *
 * @seismicElement now-record-common-header
 * @summary Main container for a record or record page that displays an overview of relevant information about a record. 
 */
createCustomElement(HEADER_COMPONENT, {
	properties: acceptedProperties,
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	dispatches: {
		/**
		 * Dispatched when a secondaryItem of type reference is clicked.
		 * @type {PreviewRecordPayload}
		 */
		[PREVIEW_RECORD]: {},
		/**
		 * Dispatched when a secondaryItem of type reference is clicked and the header is inside a record page in UI Builder.
		 * @type {PreviewRecordPayload}
		 */
		[HEADER_ACTIONS_PREVIEW_RECORD]: {}
	},
	actionHandlers: {
		[PREVIEW_RECORD]: {
			effect: ({ action, dispatch, properties: { classicForm } }) => {
				if (classicForm) return;

				dispatch(HEADER_ACTIONS_PREVIEW_RECORD, action.payload);
				action.stopPropagation();
			}
		}
	}
});

export default HEADER_COMPONENT;
