import keyBindingBehavior from "@servicenow/behavior-key-binding";
import { EDIT_TAG_MODAL } from "./constants";
const behaviors = [
	{
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				dismiss: ["Escape"]
			},
			handlers: {
				dismiss: function(host, event, dispatch) {
					if (host.opened) {
						event.stopPropagation();
						dispatch(EDIT_TAG_MODAL.ACTIONS.OPENED_SET, { value: false });
					}
				}
			}
		}
	}
];
export default behaviors;
