import { createCustomElement } from "@servicenow/ui-core";
import react from "@servicenow/ui-renderer-react";
import _ from "lodash";
import resizeBehavior from '@servicenow/behavior-resize';

import actions from "./UIActionBarAction";
import UIActionBarInternal from "./UIActionBarInternal";
import styles from "./UIActionBar.scss";

const UI_ACTION_BAR_INTERNAL_ELEMENT = "sn-form-internal-uiactionbar";

/**
 * Set of horizontal buttons mapped to ui actions that fits the size of its container
 * Clicking on a button will execute the UIAction
 *
 * ```jsx
 * <sn-form-internal-uiactionbar
 *		action-nodes={...}
 *		width={...}
 *		handleClick={...}
 *		/>
 * ```
 *
 * @seismicElement sn-form-internal-uiactionbar
 * @summary Set of horizontal buttons mapped to ui actions that fits the size of its container
 */
createCustomElement(UI_ACTION_BAR_INTERNAL_ELEMENT, {
	renderer: {
		type: react,
		view: UIActionBarInternal
	},
	properties: {
		/**
		 * Array of action nodes which will be mapped to clickable nds components for execution (now-button | now-split-button | now-dropdown)
		 * @type {(Array.<ActionNode>)}
		 */
		actionNodes: {},
		/**
		 * Minimum number of UI actions to be displayed without overflow.
		 * @type {{Number}}
		 */
		fixedButtonNumber: { 			
			default: 0,
			schema: {
				type: 'number'
			} 
		},
		/**
		 * this is an object which contains the data needed for all of the components represented by all of the actions to load
		 * @type {SidebarFormModel}
		 */
		daModel: {
			default: {},
			schema: {
				type: "object"
			}
		},
		/**
		 * Does something asynchronously and executes the callback on completion.
		 * @type {UIActionClickCallback} handleClick - optional if pass will be called when the UIAction is clicked;
		 * otherwise, NOW_RECORD_COMMON_UI_ACTION_BAR#CLICKED will be dispatched with selectedAction
		 */
		handleClick: {},
		/**
		 * Width of the parent container, when it changes it recalculates what buttons are shown in dropdown menu
		 *
		 * @type {(string)}
		 */
		width: {},
		/**
		 * whether or not this is rendered in the workspace Form
		 * @type {boolean}
		 */
		classicForm: {}
	},
	behaviors: [{ behavior: resizeBehavior }],
	styles,
	...actions,
	dispatches: {
		/**
		 * SysId of UI Action that was clicked
		 *
		 * @type {(string)}
		 */
		"NOW_RECORD_UI_ACTION_BAR#CLICKED": {}
	}
});

export default UI_ACTION_BAR_INTERNAL_ELEMENT;
