import react from "@servicenow/ui-renderer-react";
import React from "react";
import PropTypes from "prop-types";
import { t } from "sn-translate";
import _ from "lodash";
import "@servicenow/now-button";
import { SEARCH_INPUT_ENTERED } from "../constants/searchbarConstants.js";

const HIDDEN_STYLE = { display: "none" };

class Searchbar extends React.PureComponent {
	constructor(props) {
		super(props);
		this.search = _.debounce(this.search, 500);
	}

	componentWillReceiveProps(props) {
		if (
			props.properties.caseSensitive ||
			props.properties.value !== this.searchInput.value.toLowerCase()
		)
			this.searchInput.value = props.properties.value;
	}

	search = () => {
		if (!this.searchInput) {
			console.warn("Something was wrong. No reference to input field.");
			return;
		}
		const { caseSensitive } = this.props.properties;
		const filter = caseSensitive
			? this.searchInput.value
			: this.searchInput.value.toLowerCase();
		this.closeBox.style.display = filter ? "flex" : "none";
		this.executeSearch(filter);
	};

	clearSearch = () => {
		if (!this.searchInput) {
			console.warn("Something was wrong. No reference to input field.");
			return;
		}

		this.searchInput.value = "";
		this.search();
	};

	executeSearch(filter = "") {
		this.props.dispatch(SEARCH_INPUT_ENTERED, { filter });
	}

	inputRef = node => {
		this.searchInput = node;
		if (this.searchInput) this.searchInput.value = this.props.properties.value;
	};

	closeBoxRef = node => {
		this.closeBox = node;
		if (this.closeBox)
			this.closeBox.style.display = this.props.properties.value
				? "flex"
				: "none";
	};

	wrapperRef = node => {
		this.wrapperEl = node;
	};

	onFocus = () => {
		this.wrapperEl.classList.add("is-focus");
	};

	onBlur = () => {
		this.wrapperEl.classList.remove("is-focus");
	};

	render() {
		const { placeholder } = this.props.properties;

		return (
			<div className="sn-control" ref={this.wrapperRef}>
				<div className="sn-control-field">
					<div className="sn-control-icon">
						<now-icon icon="magnifyingGlassOutline" size="md" />
					</div>
					<input
						className="sn-input"
						type="text"
						aria-label={placeholder}
						placeholder={placeholder}
						onChange={this.search}
						onFocus={this.onFocus}
						onBlur={this.onBlur}
						ref={this.inputRef}
					/>
					<div
						className="sn-control-controls"
						style={HIDDEN_STYLE}
						ref={this.closeBoxRef}
					>
						<now-button
							icon-name="closeOutline"
							icon-set="solid"
							variant="tertiary"
							size="sm"
							bare
							onclick={this.clearSearch}
							tooltip-content={t("Clear Search")}
						/>
					</div>
				</div>
			</div>
		);
	}
}

Searchbar.propTypes = {
	placeholder: PropTypes.string,
	search: PropTypes.func,
	caseSensitive: PropTypes.bool
};

Searchbar.defaultProps = {
	caseSensitive: false
};

export default Searchbar;
