import { get, isEmpty } from "lodash";
import { toAttributes } from "./utils/utils";
import { EmptyStateElement } from "../emptyState";
import { t } from "sn-translate";

import "@servicenow/now-uxf-page";

const constructWidgets = (widgets, defaultAttributes, isVisible) => {
	return widgets
		.filter(widget => get(widget, "tag.value"))
		.map(widget => {
			const CustomTag = get(widget, "tag.value");
			const name = get(widget, "name.displayValue");
			const width = get(widget, "width.value", 3);
			const compId =
				name +
				"-" +
				defaultAttributes.table +
				"-" +
				defaultAttributes.sysid +
				Math.random();

			let compAttributes = (get(widget, "attrs.variables") || []).reduce(
				(attributes, { variableName, variableValue }) => {
					return {
						...attributes,
						[variableName]: variableValue
					};
				},
				{ name, ...defaultAttributes }
			);

			compAttributes = toAttributes(compAttributes);

			let columns = 1;
			if (width > 3) {
				columns = 3;
			} else if (width > 1) {
				columns = 2;
			}

			return (
				<div key={"col-" + name} className={`sn-ribbon-column -col-${width}`}>
					<CustomTag
						{...compAttributes}
						component-id={compId}
						columns={columns}
						isVisible={isVisible}
					/>
				</div>
			);
		});
};

const view = state => {
	const {
		properties: {
			table,
			sysId,
			widgets,
			active,
			isVisible,
			collapsed,
			canCollapse,
			classicForm,
			isOpenframeConfigValid
		},
		widgetsLoading
	} = state;

	if (!sysId || !table)
		return classicForm ? (
			<div />
		) : (
			<EmptyStateElement message={t("No data to display")} />
		);

	if (widgetsLoading || isEmpty(widgets)) return <div class="empty-ribbon" />;

	const defaultAttributes = {
		sysid: sysId,
		table,
		active,
		...(classicForm ? {} : { isOpenframeConfigValid })
	};

	const htmls = constructWidgets(widgets, defaultAttributes, isVisible);

	return (
		<div className="ribbon-container">
			<now-uxf-page-container
				bare
				can-collapse={canCollapse}
				collapsed={collapsed}
			>
				{htmls.length && (
					<div className="sn-ribbon">
						<div className="sn-ribbon-row">{htmls}</div>
					</div>
				)}
			</now-uxf-page-container>
		</div>
	);
};

export default view;
