export const view = state => {
	const {
		properties: { message, detail }
	} = state;

	return (
		<div className="sn-error-state">
			<div className="sn-error-state-row">{icon}</div>
			<div className="sn-error-state-row -header">
				{message ? <p>{message}</p> : <Fragment />}
			</div>
			<div className="sn-error-state-row">
				{detail ? <p>{detail}</p> : <Fragment />}
			</div>
		</div>
	);
};

const EMPTY_STATE_COMPONENT = "sn-internal-form-empty-state";

createCustomElement(EMPTY_STATE_COMPONENT, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		message: {},
		detail: {}
	},
	styles
});

export default EMPTY_STATE_COMPONENT;
