import { createCustomElement } from "@servicenow/ui-core";
import { createRef } from "@servicenow/ui-renderer-snabbdom";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./addTag.scss";
import actions from "./addTagActions";
import {
	handleOnFocus,
	handleNonEnterKeys,
	handleEnterKey
} from "./addTagUtils";
import "../tagDropdown/tagDropdown";
import {
	LABELS,
	COMPONENT_ADD_TAG,
	COMPONENT_TAG_DROPDOWN
} from "../constants";
import behaviors from "./addTagBehaviors";

const view = (state, { dispatch }) => {
	const {
		inputField,
		tags,
		userText,
		activeDescendant,
		properties: { dropdown, table, sysId, displayValue }
	} = state;
	const inputRef = createRef();
	const handleKeyUp = _.debounce(
		handleNonEnterKeys(dispatch, inputRef, userText),
		500
	);
	const handleKeyPress = handleEnterKey(dispatch, state, inputRef);
	const handleFocus = handleOnFocus(dispatch);

	return (
		<div className="sn-form-tag-filter">
			<span>{LABELS.ADD_TAG}</span>
			<input
				id={LABELS.ADD_TAG_INPUT_ID}
				autocomplete="off"
				className="sn-form-tag-filter-text"
				type="text"
				ref={inputRef}
				on-keyup={handleKeyUp}
				on-keypress={handleKeyPress}
				on-focus={handleFocus}
				role="combobox"
				aria-haspopup="true"
				aria-label={LABELS.ADD_TAG}
				aria-owns={LABELS.TAG_DROPDOWN_ID}
				aria-expanded={inputField && dropdown && tags.length ? "true" : "false"}
				aria-describedby={LABELS.ADD_TAG_ARIA_DESCRIPTION_ID}
				aria-autocomplete="both"
				aria-activedescendant={activeDescendant}
			/>
			<span
				id={LABELS.ADD_TAG_ARIA_DESCRIPTION_ID}
				className="aria-description"
			>
				{LABELS.ADD_TAG_AUTOCOMPLETE_INSTRUCTIONS}
			</span>
			<div className="sn-form-tag-autocomplete">
				<COMPONENT_TAG_DROPDOWN
					opened={inputField && dropdown}
					tags={tags}
					table={table}
					sysId={sysId}
					inputRef={inputRef}
					displayValue={displayValue}
					activeDescendant={activeDescendant}
				/>
			</div>
		</div>
	);
};

export default createCustomElement(COMPONENT_ADD_TAG, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		inputField: false,
		userText: "",
		tags: [],
		activeDescendant: ""
	},
	properties: {
		dropdown: { default: true },
		table: { default: "" },
		sysId: { reflect: false },
		displayValue: { default: "" },
		existingTags: { default: [] }
	},
	styles,
	...actions,
	behaviors
});
