import { getRowStartIndices } from "./labelValueProvider";

const calcToggleHeight = (rowsBeforeToggleShown, labelValuePairRects) => {
	const rowStartIndices = getRowStartIndices(labelValuePairRects);
	if (
		!(rowStartIndices && rowStartIndices.length) ||
		rowsBeforeToggleShown > rowStartIndices.length
	)
		return 0;

	let maxHeightPerRow = [];

	for (let i = 0; i < rowStartIndices[rowsBeforeToggleShown]; i++) {
		if (rowStartIndices.includes(i)) {
			maxHeightPerRow.push({
				index: i,
				rect: labelValuePairRects[i].rect
			});
		} else {
			const lastIndex = maxHeightPerRow.length - 1;
			if (
				labelValuePairRects[i].rect.height >
				maxHeightPerRow[lastIndex].rect.height
			) {
				maxHeightPerRow.splice(lastIndex, 1, {
					index: i,
					rect: labelValuePairRects[i].rect
				});
			}
		}
	}
	return Math.ceil(
		maxHeightPerRow[maxHeightPerRow.length - 1].rect.bottom -
			maxHeightPerRow[0].rect.top
	);
};

const applyToggleHeight = (toggleOpen, toggleHeight, toggleContainer) => {
	if (!toggleHeight) return;
	if (toggleOpen) {
		toggleContainer.style.removeProperty("max-height");
		return;
	}
	toggleContainer.style.setProperty("max-height", toggleHeight + 10 + "px");
};

export const toggleHeightCalculator = {
	applyToggleHeight,
	calcToggleHeight
};
