import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./resizeBar.scss";

import classNames from "classnames/dedupe";

const RESIZABLE_BAR = "sn-form-internal-resize-bar";

const view = ({ properties }) => {
	const { active, resizing, color } = properties;
	return active ? (
		<div
			className={classNames("column-resize-bar", {
				hidden: !active,
				resizing,
				dark: color === "dark"
			})}
		>
			<div className="column-resize-bar-handle" />
		</div>
	) : null;
};

createCustomElement(RESIZABLE_BAR, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		active: {
			default: true
		},
		resizing: {
			default: false
		},
		color: {
			default: "light"
		}
	},
	styles
});

export default RESIZABLE_BAR;
