import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { createCustomElement } from "@servicenow/ui-core";
// eslint-disable-next-line no-unused-vars
import styles from "./_typeahead.scss";
import actions from "./actions";
import behaviors from "./behaviors";
import { Typeahead } from "./views";
import { COMPONENT_TAG_TYPE_AHEAD } from "../constants";

/** @seismicElement now-dropdown */
createCustomElement(COMPONENT_TAG_TYPE_AHEAD, {
	properties: {
		/**
		 * Label to be displayed above the input field.
		 * @type {string}
		 */
		label: {},

		/**
		 * Label to be displayed as dropdown header
		 * @type {string}
		 */
		dropdownLabel: {},

		/** Array of available items
		 * @type {{value: (string|number), displayValue: string, secondaryDisplayValue: string, icon: string}[]}
		 */
		availableItems: { default: [], cast: "array" },

		/** Array of selected items
		 * @type {{value: (string|number), displayValue: string, secondaryDisplayValue: string, icon: string}[]}
		 */
		selectedItems: { default: [], cast: "array" },

		/**
		 * Manage the property to control when the typeahead dropdown is open.
		 * @type {boolean}
		 */
		opened: { default: false, manageable: true },

		/**
		 * Input value of the filter
		 * @type {string}
		 */
		filter: { default: "", manageable: true },

		/**
		 * Name of the element. Will be passed in dispatch events.
		 * @type {string}
		 */
		fieldName: { default: "typeahead-element" },

		/**
		 * Default pill icon to render if image url is not set or not found.
		 * @type {string}
		 */
		defaultPillIcon: {},

		/**
		 * Total available items
		 * @type {number}
		 */
		total: {},
		inputRef: {},
		dropdownPanelRef: {}
	},
	renderer: {
		type: snabbdom,
		view: Typeahead
	},
	...actions,
	behaviors,
	styles
});
