import _ from "lodash";
import {
	ATTACHMENT_UPLOAD_MODAL_FOOTER_ACTION,
	ATTACHMENT_MODAL_FOOTER_BUTTON,
	UPLOAD_MODAL_EVENT_SOURCE
} from "../constants";
import { NOW_MODAL_OPENED_SET_ACTION } from "../../../constants/attachmentConstants";
import {
	showAttachmentUploadErrors,
	uploadAttachmentToServer,
	getExtensionFromFileName
} from "./attachmentUploadUtil";
import { executeEffectIfValidEventSource } from "@devsnc/library-form";
import { SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN } from "@devsnc/sn-scripted-modal";
import { t } from "sn-translate";

const dismissModal = (updateState, dispatch) => {
	updateState({
		showUploadFileModal: false,
		uploadAttachmentListIndex: 0,
		uploadAttachmentList: []
	});
	dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, false);
};

const uploadNext = (state, updateState, dispatch) => {
	const {
		errorAttachmentList,
		uploadAttachmentList,
		uploadAttachmentListIndex
	} = state;
	const nextFileIndex = uploadAttachmentListIndex + 1;
	if (nextFileIndex <= uploadAttachmentList.length - 1) {
		updateState({
			uploadAttachmentListIndex: nextFileIndex,
			currentFileName: uploadAttachmentList[nextFileIndex].name
		});
	} else {
		dismissModal(updateState, dispatch);
		showAttachmentUploadErrors(errorAttachmentList, dispatch);
	}
};

const uploadFile = (action, state, dispatch) => {
	if (!action.payload.file) return;

	const {
		errorAttachmentList,
		currentFileName,
		properties: { table, sysId }
	} = state;
	const originalFileName = action.payload.file.name;
	if (
		getExtensionFromFileName(currentFileName) !==
		getExtensionFromFileName(originalFileName)
	)
		errorAttachmentList.push(
			t(
				"Renaming attachment {0} to new name {1} is not allowed.",
				originalFileName,
				currentFileName
			)
		);
	else {
		const fileToUpload =
			currentFileName === originalFileName
				? action.payload.file
				: new File([action.payload.file], currentFileName, {
						type: action.payload.file.type
				  });

		uploadAttachmentToServer(table, sysId, fileToUpload, dispatch);
	}
};

const updateStateOnModalOpenedEffect = {
	effect: ({ updateState, state, dispatch }) => {
		uploadNext(state, updateState, dispatch);
	}
};

const updateStateOnUploadEffect = {
	effect: ({ updateState, state, action, dispatch }) => {
		const actionButtonId = _.get(action, "payload.action.id", "");
		if (actionButtonId === ATTACHMENT_MODAL_FOOTER_BUTTON.UPLOAD) {
			uploadFile(action, state, dispatch);
			uploadNext(state, updateState, dispatch);
		} else if (actionButtonId === ATTACHMENT_MODAL_FOOTER_BUTTON.CANCEL) {
			const { errorAttachmentList } = state;
			dismissModal(updateState, dispatch);
			showAttachmentUploadErrors(errorAttachmentList, dispatch);
		}
	}
};

export default {
	actions: {
		[ATTACHMENT_UPLOAD_MODAL_FOOTER_ACTION]: {
			private: true
		},
		[NOW_MODAL_OPENED_SET_ACTION]: {
			private: true
		}
	},
	actionHandlers: {
		[ATTACHMENT_UPLOAD_MODAL_FOOTER_ACTION]: executeEffectIfValidEventSource(
			UPLOAD_MODAL_EVENT_SOURCE,
			updateStateOnUploadEffect
		),
		[NOW_MODAL_OPENED_SET_ACTION]: executeEffectIfValidEventSource(
			UPLOAD_MODAL_EVENT_SOURCE,
			updateStateOnModalOpenedEffect
		)
	}
};
