import {
	getLabelValuePairContainer,
	getLabelValuePairs,
	getRects,
	getRowStartIndices
} from "./labelValueProvider";
import { lineClamp } from "./lineClamper";
import CONSTS from "./displayValueBlockConstants";
import {
	removeDelimiterNodesFromContainer,
	handleDelimiter
} from "./delimiter";
import { toggleHeightCalculator } from "./toggleHeightCalculator";

const handleDisplayInline = (state, toggleContainer) => {
	const {
		properties: { displayInline, layoutType }
	} = state;

	const labelValuePairNodes = getLabelValuePairs(toggleContainer);
	labelValuePairNodes.forEach(labelValuePair => {
		const dt = labelValuePair.querySelector("dt");
		const dd = labelValuePair.querySelector("dd");

		if (displayInline) {
			// temporary workaround until NDS add the property to displayInline for now-label-value-stacked
			labelValuePair.style.setProperty("display", "flex");
			labelValuePair.style.setProperty("align-items", "baseline");
			dt.style.setProperty("font-size", "1rem");
			dd.style.setProperty("margin-inline-start", ".25rem");

			const isHighlightedOrLink = labelValuePair.querySelector(
				"now-highlighted-value, now-text-link"
			);
			if (
				isHighlightedOrLink &&
				layoutType === CONSTS.LAYOUT.DISTRIBUTED.NAME
			) {
				labelValuePair
					.querySelector("dd")
					.style.setProperty("overflow", "hidden");
			}
		} else {
			// this is used to reset displayInline when changed from true to false
			labelValuePair.style.removeProperty("display");
			labelValuePair.style.removeProperty("align-items");
			dt.style.removeProperty("font-size");
			dd.style.removeProperty("margin-inline-start");

			const isHighlightedOrLink = labelValuePair.querySelector(
				"now-highlighted-value, now-text-link"
			);
			if (
				isHighlightedOrLink &&
				layoutType === CONSTS.LAYOUT.DISTRIBUTED.NAME
			) {
				labelValuePair.querySelector("dd").style.removeProperty("overflow");
			}
		}
	});
};

const handleDistributedLayout = toggleContainer => {
	removeDelimiterNodesFromContainer(toggleContainer);
};

const handleDynamicLayout = (state, toggleContainer) => {
	const labelValuePairNodes = getLabelValuePairs(toggleContainer);
	labelValuePairNodes.forEach(labelValuePair => {
		const dt = labelValuePair.querySelector("dt");
		const dd = labelValuePair.querySelector("dd");

		labelValuePair.style.removeProperty("min-width");
		dt.style.setProperty("max-width", CONSTS.LAYOUT.DYNAMIC.MAX_WIDTH);
		dd.style.setProperty("max-width", CONSTS.LAYOUT.DYNAMIC.MAX_WIDTH);
	});

	handleDelimiter(state, toggleContainer);
};

const onToggleChange = state => {
	const { toggleOpen, toggleHeight, toggleContainerRef } = state;

	toggleHeightCalculator.applyToggleHeight(
		!toggleOpen,
		toggleHeight,
		toggleContainerRef
	);
};

const handleLineClamping = (dd, maxLinesPerValue) => {
	const divToClamp = dd.querySelector(".needs-clamping");
	if (divToClamp) lineClamp(divToClamp, maxLinesPerValue);
};

const applyStyles = (state, host) => {
	const {
		toggleContainerRef,
		properties: { layoutType }
	} = state;
	const toggleContainer = toggleContainerRef || host;
	const dl = getLabelValuePairContainer(toggleContainer);

	if (!dl) return;

	handleDisplayInline(state, toggleContainer);

	if (layoutType === CONSTS.LAYOUT.DYNAMIC.NAME)
		handleDynamicLayout(state, toggleContainer);
	else handleDistributedLayout(toggleContainer);
};

const onWidthChange = state => {
	const {
		properties: { maxLinesPerValue, rowsBeforeToggleShown },
		toggleContainerRef
	} = state;
	const dl = getLabelValuePairContainer(toggleContainerRef);

	if (!dl) return;

	const labelValuePairNodes = getLabelValuePairs(toggleContainerRef);

	labelValuePairNodes.forEach(labelValuePair => {
		const dd = labelValuePair.querySelector("dd");

		if (isRTL()) handleLineClamping(dd, maxLinesPerValue);
	});
};

const calculateToggleState = state => {
	const {
		properties: { enableToggle, rowsBeforeToggleShown },
		behaviors: {
			resize: {
				size: { width }
			}
		},
		toggleContainerRef,
		toggleOpen,
		showToggle
	} = state;

	const dl = getLabelValuePairContainer(toggleContainerRef);

	if (!dl) return;

	if (!(rowsBeforeToggleShown > 0)) return { width };

	const labelValuePairRects = getRects(toggleContainerRef);
	const rowStartIndices = getRowStartIndices(labelValuePairRects);
	const rowCount = rowStartIndices.length;

	if (rowCount <= rowsBeforeToggleShown) {
		toggleContainerRef.style.removeProperty("max-height");
		if (showToggle)
			return {
				width,
				showToggle: false
			};
		return;
	}

	const toggleHeight = toggleHeightCalculator.calcToggleHeight(
		rowsBeforeToggleShown,
		labelValuePairRects
	);

	toggleHeightCalculator.applyToggleHeight(
		toggleOpen,
		toggleHeight,
		toggleContainerRef
	);

	return {
		width,
		showToggle: true,
		toggleHeight
	};
};

const recalculateLayout = state => {
	applyStyles(state);
	onWidthChange(state);
	return calculateToggleState(state);
};

export const isRTL = () => document.dir === "rtl";

export const LayoutHandler = {
	applyStyles,
	onWidthChange,
	recalculateLayout,
	onToggleChange,
	calculateToggleState
};
