const focusableTagsHashSet = {
	BUTTON: 1,
	INPUT: 1,
	SELECT: 1,
	TEXTAREA: 1
};

const isVisible = elem => {
	return !!(
		elem.offsetWidth ||
		elem.offsetHeight ||
		elem.getClientRects().length
	);
};

const isFocusable = elem => {
	if (typeof elem.getAttribute !== "function") return false;
	if ((elem.getAttribute("class") || "") === "focus-trap-bound") return false; //ignore
	if (elem.hasAttribute("disabled")) return false;
	const tabIndex = elem.getAttribute("tabindex") || "";
	if (tabIndex !== "") return parseInt(tabIndex) >= 0;
	if (!isVisible(elem)) return false;
	return (
		focusableTagsHashSet[elem.tagName] === 1 ||
		((elem.getAttribute("href") || "") !== "" &&
			elem.tagName !== "LINK" &&
			elem.tagName !== "BASE")
	);
};

const getContentBody = elem => {
	if (!elem) return elem;
	if (elem.tagName === "IFRAME")
		return elem.contentDocument || elem.contentWindow.document;
	if (elem.shadowRoot) return elem.shadowRoot;
	return elem;
};

const getFirstAndLastFocusableElement = doc => {
	if (!doc) return null;

	const children = doc.childNodes || [];
	if (children.length === 0) return null;

	let first = 0;
	let last = children.length - 1;
	let foundFirst = null;
	let foundLast = null;

	while (first <= last && !(foundFirst && foundLast)) {
		if (isFocusable(children[first])) foundFirst = children[first];
		if (isFocusable(children[last])) foundLast = children[last];

		if (!foundFirst && children[first].tagName) {
			const inner = getFirstAndLastFocusableElement(
				getContentBody(children[first])
			);
			if (inner !== null) foundFirst = inner.first;
		}

		if (!foundLast && children[last].tagName) {
			const inner = getFirstAndLastFocusableElement(
				getContentBody(children[last])
			);
			if (inner !== null) foundLast = inner.last;
		}

		if (!foundFirst) first++;
		if (!foundLast) last--;
	}

	return foundFirst && foundLast
		? { first: foundFirst, last: foundLast }
		: null;
};

const createNewEvent = (eventName, params) => {
	return typeof window.CustomEvent === "function"
		? new CustomEvent(eventName, params)
		: () => {
				params = params || {
					bubbles: false,
					cancelable: false,
					detail: { ...params }
				};
				const evt = document.createEvent(eventName);
				evt.initCustomEvent(
					eventName,
					params.bubbles,
					params.cancelable,
					params
				);
				return evt;
		  };
};

const resendEvent = (toElem, originalEvent) => {
	const { shiftKey } = originalEvent;
	toElem.dispatchEvent(createNewEvent("focus-cycle", { detail: { shiftKey } }));
};

const createCycleElem = () => {
	let elem = document.createElement("i");
	elem.setAttribute("class", "focus-trap-bound");
	elem.setAttribute("tabIndex", "0");
	elem.setAttribute("style", "opacity: 0; width: 0; height: 0");
	return elem;
};

const addFocusCycleEventListener = (
	container,
	elem,
	isFirstChild,
	oppositeElem
) => {
	elem.addEventListener("keyup", e => {
		const TAB_KEY = 9;
		if (
			e.which === TAB_KEY &&
			((isFirstChild && e.shiftKey) || (!isFirstChild && !e.shiftKey))
		) {
			oppositeElem.focus();
			resendEvent(oppositeElem, e);
		}
	});

	elem.addEventListener("focus-cycle", e => {
		const { shiftKey } = e.detail;
		const { first, last } = getFirstAndLastFocusableElement(
			getContentBody(container)
		);
		if (isFirstChild) {
			if (!shiftKey && first !== null) first.focus();
		} else {
			if (shiftKey && last !== null) last.focus();
		}
	});
};

/**
 * Use to attach focus trap handler to a container
 */
export const attachFocusTrap = container => {
	if (!container.getAttribute("data-focus-trapped")) {
		container.setAttribute("data-focus-trapped", "true");
		const firstElem = createCycleElem();
		container.insertBefore(firstElem, container.childNodes[0]);
		const lastElem = createCycleElem();
		container.appendChild(lastElem);
		addFocusCycleEventListener(container, firstElem, true, lastElem);
		addFocusCycleEventListener(container, lastElem, false, firstElem);
	}
};

/**
 * Use as onFocus handler where we need focus trap
 * @param event
 * @return void
 */
export const focusTrapHandler = event => {
	const container = event.currentTarget;
	if (container) attachFocusTrap(container);
};
