export const getActiveElement = el => {
	return el.shadowRoot && el.shadowRoot.activeElement
		? getActiveElement(el.shadowRoot.activeElement)
		: el;
};

export const getSelectNode = (doc, selector) => {
	if (!doc || !selector || !doc.querySelector) return null;
	const host = doc.host;
	const selectedNode = doc.querySelector(selector);
	if (!host || selectedNode) return selectedNode;
	return getSelectNode(host.getRootNode(), selector);
};

export const getFirstDiv = host => {
	const rootNode = host && host.getRootNode && host.getRootNode();
	return rootNode ? rootNode.querySelector("div") : null;
};
