import { ATTACHMENT_DELETE } from "../constants";
import { ATTACHMENT_ACTIONS } from "../../../constants/attachmentConstants";
import { ADD_NOTIFICATIONS } from "../../../constants/constants";
import { attachmentGraphQLDelete } from "./attachmentGraphQLDelete";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { t } from "sn-translate";

const createDeleteActions = () => {
	const handleStart = createGraphQLEffect(attachmentGraphQLDelete, {
		variableList: ["sys_id"],
		templateVarList: [],
		startActionType: ATTACHMENT_DELETE.ACTIONS.STARTED,
		successActionType: ATTACHMENT_DELETE.ACTIONS.SUCCEEDED,
		errorActionType: ATTACHMENT_DELETE.ACTIONS.FAILED
	});

	const handleSucceeded = ({ dispatch, properties }) => {
		const { table, sysId } = properties;

		dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY, {
			sysId,
			table
		});
	};

	const handleFailed = ({ state, action, dispatch }) => {
		const {
			data: { message }
		} = action.payload;
		const name = _.get(state, "modal.data.name", "");
		dispatch(ADD_NOTIFICATIONS, {
			notifications: [
				{
					type: "error",
					message: t(`Error deleting '{0}': {1}`, name, message)
				}
			]
		});
	};

	return {
		actions: {
			[ATTACHMENT_DELETE.ACTIONS.START]: {
				private: true
			},
			[ATTACHMENT_DELETE.ACTIONS.SUCCEEDED]: {
				private: true
			},
			[ATTACHMENT_DELETE.ACTIONS.FAILED]: {
				private: true
			}
		},
		actionHandlers: {
			[ATTACHMENT_DELETE.ACTIONS.START]: handleStart,
			[ATTACHMENT_DELETE.ACTIONS.SUCCEEDED]: handleSucceeded,
			[ATTACHMENT_DELETE.ACTIONS.FAILED]: handleFailed
		}
	};
};

export default createDeleteActions();
