import { t } from "sn-translate";
import "@servicenow/now-pill";
import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import { ACTIONS, LABELS, VIEWABLE_BY, COMPONENT_ADD_TAG } from "../constants";
import { escapeSeismicSpecialSymbols } from "../../../utils/seismicUtils";

export const tagListSection = (tagList, viewableBy) => {
	if (tagList.length === 0) return null;

	return (
		<div className="sn-tag-component-section">
			<span className="sn-tag-component-sectionheader">{viewableBy}</span>
			<div className="sn-tag-component-tags-list">{tagList}</div>
		</div>
	);
};

const createPillFromTag = ({ sysId, canEdit, name, labelEntry }) => {
	const pillAriDescriptionId = `${sysId}-ally-description`;
	return (
		<Fragment>
			<now-pill
				can-dismiss
				manage-selected
				label={escapeSeismicSpecialSymbols(name)}
				size="sm"
				className="sn-tag-component-tag-pill"
				component-name={labelEntry}
				data-id={sysId}
				append-to-payload={{ tagId: sysId, canEdit }}
				aria-describedby={pillAriDescriptionId}
			/>
			<span className="aria-description" id={pillAriDescriptionId}>
				{LABELS.TAG_LIST_PILL_ARIA_DESCRIPTION}
			</span>
		</Fragment>
	);
};

export const createTagSections = taglist => {
	let tagListMe = [],
		tagListGroups = [],
		tagListEveryone = [];
	taglist.map(tag => {
		const pill = createPillFromTag(tag);
		switch (tag.viewableBy) {
			case VIEWABLE_BY.EVERYONE.value:
				tagListEveryone.push(pill);
				break;
			case VIEWABLE_BY.GROUPS_AND_USERS.value:
				tagListGroups.push(pill);
				break;
			default:
				tagListMe.push(pill);
		}
	});
	const tagSections = { tagListMe, tagListGroups, tagListEveryone };
	return tagSections;
};

export const handleClick = (e, dispatch) => {
	e.stopPropagation();
	dispatch(ACTIONS.TAG_LIST_CLICKED, {
		openDropDown: e.target.localName === COMPONENT_ADD_TAG
	});
};

export const createTitle = (tableName, displayValue) => {
	return `${_.upperFirst(tableName)} - ${displayValue}`;
};
