import { ACTIONS, GQL_RESULTS } from "../constants";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { ADD_TAG_QUERIES } from "./addTagGraphQLQuery";
import { createUpdateStateInterceptor } from "@devsnc/library-form";
import { createTagLabel } from "./addTagUtils";

const fetchTagsWithPrefix = createGraphQLEffect(
	ADD_TAG_QUERIES.TAG_PREFIX_GET_QUERY,
	{
		variableList: ["prefix"],
		successActionType: ACTIONS.TAG_PREFIX_FETCH_SUCCESS,
		errorActionType: ACTIONS.TAG_GRAPHQL_ERROR
	}
);

const createNewTagLabelEntry = createGraphQLEffect(
	ADD_TAG_QUERIES.TAG_INSERT_LABEL_ENTRY,
	{
		variableList: ["tagSysId", "table", "sysId", "title"],
		successActionType: ACTIONS.TAG_LABEL_ENTRY_SUCCESS,
		errorActionType: ACTIONS.TAG_GRAPHQL_ERROR
	}
);

const createNewTag = createGraphQLEffect(ADD_TAG_QUERIES.TAG_CREATE_LABEL, {
	variableList: ["text"],
	successActionType: ACTIONS.TAG_CREATE_SUCCESS,
	errorActionType: ACTIONS.TAG_GRAPHQL_ERROR
});

const tagsFromRecords = (records = []) =>
	records.map(({ sysId, name, viewableBy }) => ({
		sysId,
		name,
		viewableBy
	}));

const recordsToTagsAndFilter = (records = [], existingTags) => {
	const newtags = tagsFromRecords(records);
	return _.differenceWith(newtags, existingTags, (x, y) => x.sysId === y.sysId);
};

const checkIfTagExistsEffect = createGraphQLEffect(
	ADD_TAG_QUERIES.TAG_PREFIX_GET_QUERY,
	{
		variableList: ["prefix"],
		successActionType: ACTIONS.TAG_EXISTS_CHECK_SUCCESS,
		errorActionType: ACTIONS.TAG_EXISTS_CHECK_FAILURE
	}
);

export default {
	actions: {
		[ACTIONS.INPUT_TAG_TYPED]: {
			private: true
		},
		[ACTIONS.TAG_PREFIX_FETCH_SUCCESS]: {
			private: true
		},
		[ACTIONS.CREATE_TAG_LABEL]: {
			private: true
		},
		[ACTIONS.TAG_LABEL_ENTRY_SUCCESS]: {
			private: true
		},
		[ACTIONS.TAG_CREATE]: {
			private: true
		},
		[ACTIONS.TAG_CREATE_SUCCESS]: {
			private: true
		},
		[ACTIONS.ADD_TAG_FIELD_CHANGED]: {
			private: true
		},
		[ACTIONS.ADD_TAG_FIELD_CLEARED]: {
			private: true
		},
		[ACTIONS.RESET_ADD_TAG_COMPONENT]: {
			private: true
		},
		[ACTIONS.ADD_TAG_DESCENDANT_SELECTED]: {
			private: true
		},
		[ACTIONS.TAG_EXISTS_CHECK]: {
			private: true
		},
		[ACTIONS.TAG_EXISTS_CHECK_SUCCESS]: {
			private: true
		},
		[ACTIONS.TAG_EXISTS_CHECK_FAILURE]: {
			private: true
		}
	},
	actionHandlers: {
		[ACTIONS.INPUT_TAG_TYPED]: fetchTagsWithPrefix,
		[ACTIONS.TAG_PREFIX_FETCH_SUCCESS]: ({ updateState, state, action }) => {
			const {
				properties: { existingTags }
			} = state;
			updateState({
				tags: recordsToTagsAndFilter(
					_.get(action, GQL_RESULTS.TAG_FETCH_VIEWABLE_TAGS_RESULT, []),
					existingTags
				)
			});
		},
		[ACTIONS.CREATE_TAG_LABEL]: createNewTagLabelEntry,
		[ACTIONS.TAG_LABEL_ENTRY_SUCCESS]: ({ updateState, dispatch }) => {
			updateState({
				inputField: false,
				userText: "",
				tags: []
			});
			dispatch(ACTIONS.TAG_UPDATE_REFLECT);
		},
		[ACTIONS.TAG_EXISTS_CHECK]: checkIfTagExistsEffect,
		[ACTIONS.TAG_EXISTS_CHECK_SUCCESS]: ({ state, action, dispatch }) => {
			const tagName = _.get(action, GQL_RESULTS.CHECK_TAG_VALUE, "");
			const tags = tagsFromRecords(
				_.get(action, GQL_RESULTS.TAG_FETCH_VIEWABLE_TAGS_RESULT, [])
			);
			const tag = tags.find(tag => tag.name === tagName);
			if (tag) createTagLabel(dispatch, tag.sysId, state.properties);
			else dispatch(ACTIONS.TAG_CREATE, { text: tagName });
		},
		[ACTIONS.TAG_EXISTS_CHECK_FAILURE]: ({ state, action, dispatch }) => {
			const tagName = _.get(action, GQL_RESULTS.CHECK_TAG_VALUE);
			dispatch(ACTIONS.TAG_CREATE, { text: tagName });
		},
		[ACTIONS.TAG_CREATE]: createNewTag,
		[ACTIONS.TAG_CREATE_SUCCESS]: ({
			state: {
				properties: { table, sysId, displayValue: title }
			},
			action,
			dispatch
		}) => {
			const tagSysId = _.get(
				action,
				GQL_RESULTS.TAG_AFTER_INSERT_NEW_SYS_ID,
				""
			);
			if (tagSysId)
				dispatch(ACTIONS.CREATE_TAG_LABEL, { tagSysId, table, sysId, title });
		},
		[ACTIONS.ADD_TAG_FIELD_CHANGED]: ({
			updateState,
			action: {
				payload: { text }
			}
		}) => {
			updateState({
				inputField: true,
				userText: text,
				activeDescendant: ""
			});
		},
		[ACTIONS.ADD_TAG_FIELD_CLEARED]: ({ updateState }) => {
			updateState({
				inputField: false,
				userText: "",
				tags: [],
				activeDescendant: ""
			});
		},
		[ACTIONS.RESET_ADD_TAG_COMPONENT]: ({ updateState }) => {
			updateState({
				userText: "",
				tags: [],
				activeDescendant: ""
			});
		},
		[ACTIONS.ADD_TAG_DESCENDANT_SELECTED]: ({
			updateState,
			action: {
				payload: { activeDescendant }
			}
		}) => {
			updateState({
				activeDescendant
			});
		}
	}
};
