import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { default as uiActionDataProvider } from "./data/uiActionDataProvider";
import { default as UIACTION_ELEMENT } from "./UIActionBarElement";
import {
	UI_ACTION_BAR_ACTIONS,
	UI_ACTION_BAR_CONNECTED_ACTIONS
} from "./UIActionBarConst";

// Create view for renderer
const view = state => {
	const {
		properties: { actionNodes }
	} = state;
	return <UIACTION_ELEMENT action-nodes={actionNodes} />;
};

const UIACTIONBAR_CONNECT_ELEMENT = "now-record-common-uiactionbar-connected";

// Create header component
createCustomElement(UIACTIONBAR_CONNECT_ELEMENT, {
	properties: uiActionDataProvider.properties,
	renderer: {
		type: snabbdom,
		view
	},
	actions: {
		[UI_ACTION_BAR_ACTIONS.ACTION_CLICK]: {
			private: true
		},
		...uiActionDataProvider.actions
	},
	actionHandlers: {
		...uiActionDataProvider.actionHandlers,
		[UI_ACTION_BAR_ACTIONS.ACTION_CLICK]: ({ action, dispatch }) => {
			const {
				payload: { actionSysId }
			} = action;

			dispatch(UI_ACTION_BAR_CONNECTED_ACTIONS.ACTION_CLICK, {
				actionSysId
			});
		}
	}
});

export default UIACTIONBAR_CONNECT_ELEMENT;
