import { ACTIONS } from "../constants";
import { keyUtils } from "@devsnc/library-form";

export const handleNonEnterKeys = (dispatch, inputRef, userText) => {
	return e => {
		e.stopPropagation();
		const text = inputRef.current.value.trim();
		if (text !== "" && text !== userText) {
			dispatch(ACTIONS.ADD_TAG_FIELD_CHANGED, { text });
			dispatch(ACTIONS.INPUT_TAG_TYPED, { prefix: text });
		} else if (text === "") dispatch(ACTIONS.ADD_TAG_FIELD_CLEARED);
	};
};

function findTag(tagList, text) {
	return tagList.find(element => element.name === text);
}

const createTag = (dispatch, prefix) =>
	dispatch(ACTIONS.TAG_EXISTS_CHECK, { prefix });

export const createTagLabel = (
	dispatch,
	tagSysId,
	{ table, sysId, displayValue: title }
) => dispatch(ACTIONS.CREATE_TAG_LABEL, { tagSysId, table, sysId, title });

export const createTagOrTagLabel = (state, dispatch, text) => {
	const { tags = [], properties } = state;

	if (tags.length) {
		const existingTag = findTag(tags, text);
		if (existingTag) {
			createTagLabel(dispatch, existingTag.sysId, properties);
		} else createTag(dispatch, text);
	} else createTag(dispatch, text);
};

export const handleOnFocus = dispatch => {
	return () => {
		dispatch(ACTIONS.TAG_LIST_CLICKED, { openDropDown: true });
	};
};

export const handleEnterKey = (dispatch, state, inputRef) => {
	return e => {
		if (keyUtils.onEnter(e)) {
			const text = inputRef.current.value.trim();
			if (!text) return;

			createTagOrTagLabel(state, dispatch, text);
			inputRef.current.value = "";
			e.preventDefault();
			e.stopPropagation();
			e.cancelBubble = true;
		}
	};
};
