import keyBindingBehavior from "@servicenow/behavior-key-binding";
import { SIDEBAR } from "../constants/constants";

const ARROW_KEY_PRESSED_ACTION = "ARROW_KEY_PRESSED_ACTION";
const ENTER_KEY_PRESSED_ACTION = "ENTER_KEY_PRESSED_ACTION";

const getActiveAction = el => {
	return el.shadowRoot && el.shadowRoot.activeElement && el.index === undefined
		? getActiveAction(el.shadowRoot.activeElement)
		: el;
};

const getActiveActionIndex = el => {
	const activeElement = getActiveAction(el);
	return activeElement && activeElement.index !== undefined
		? activeElement.index
		: -1;
};

const getMoreButton = host =>
	host.shadowRoot &&
	host.shadowRoot.querySelector(SIDEBAR.SELECTORS.MORE_BUTTON);

const eventCameFromMoreButton = (host, event) => {
	const moreButton = getMoreButton(host);
	if (!moreButton) return false;
	return event.composedPath()[0] === moreButton;
};

const eventCameFromMenuAction = event => {
	const classes = event.composedPath()[0].className || "";
	return classes.indexOf("sidebar-toolbar-menu-item") > -1;
};

const nextActionIndex = (actions, index) =>
	index + 1 > actions.length - 1 ? 0 : index + 1;

const prevActionIndex = (actions, index) =>
	index - 1 >= 0 ? index - 1 : actions.length - 1;

export const focusActionByName = (target, name, shouldOpenMore) => {
	if (target && target.shadowRoot) {
		if (shouldOpenMore) {
			const menu = target.shadowRoot.querySelector(SIDEBAR.ELEMENTS.MENU);
			if (menu && menu.shadowRoot) {
				const menuItem = menu.shadowRoot.querySelector(
					`${SIDEBAR.ELEMENTS.MENU_ITEM}[name='${name}']`
				);
				if (menuItem) menuItem.shadowRoot.querySelector("div").focus();
			}
		} else {
			const sidebarAction = target.shadowRoot.querySelector(
				`${SIDEBAR.ELEMENTS.TOOLBAR_ACTION}[name='${name}']`
			);
			if (sidebarAction && sidebarAction.shadowRoot) {
				const button = sidebarAction.shadowRoot.querySelector("button").focus();
				if (button) button.focus();
			}
		}
	}
};

const selectAction = findActionFn => (state, dispatch, target) => {
	const {
		properties: { selectedAction, actions },
		visibleActions = actions
	} = state;
	const index = actions.findIndex(a => a.name === selectedAction);
	const focusIndex = index === -1 ? getActiveActionIndex(target) : index;
	const newIndex = findActionFn(
		actions,
		focusIndex === undefined ? -1 : focusIndex
	);
	const shouldOpenMore = newIndex > visibleActions.length - 1;

	if (index === newIndex) return;

	if (!shouldOpenMore)
		focusActionByName(target, actions[newIndex].name, shouldOpenMore);

	const payload = {
		name: actions[newIndex].name,
		openedMore: shouldOpenMore
	};

	dispatch(SIDEBAR.INTERNAL_ACTIONS.ACTION_CLICKED, payload);
};

const nextAction = selectAction(nextActionIndex);
const prevAction = selectAction(prevActionIndex);

const selectMoreAdjacentAction = (state, dispatch, target, after = false) => {
	const {
		properties: { actions },
		visibleActions = actions,
		hasMore = false
	} = state;
	if (!hasMore) return;

	const newIndex = visibleActions.length + (after ? 1 : -1);
	const payload = {
		name: actions[newIndex].name,
		openedMore: after
	};
	focusActionByName(target, actions[newIndex].name, after);
	dispatch(SIDEBAR.INTERNAL_ACTIONS.ACTION_CLICKED, payload);
};

const selectActionBeforeMore = (state, dispatch, target) =>
	selectMoreAdjacentAction(state, dispatch, target, false);
const selectActionAfterMore = (state, dispatch, target) =>
	selectMoreAdjacentAction(state, dispatch, target, true);

const focusFirstOrLastAction = (host, getLast = false, fromMenu = false) => {
	if (!host.shadowRoot) return;
	const action = host.shadowRoot.querySelector(
		`${
			fromMenu ? SIDEBAR.ELEMENTS.MENU_ITEM : SIDEBAR.ELEMENTS.TOOLBAR_ACTION
		}${getLast ? ":last-child" : ""}`
	);
	if (!action || !action.shadowRoot) return;
	const element = action.shadowRoot.querySelector(fromMenu ? "div" : "button");
	if (element) element.focus();
};

const focusLastAction = host => focusFirstOrLastAction(host, true);
const focusLastMenuAction = host => focusFirstOrLastAction(host, true, true);
const focusFirstTabbable = host => {
	if (host.shadowRoot)
		host.shadowRoot.querySelector(SIDEBAR.SELECTORS.TOOLBAR_CONTAINER).focus();
};

const focusLastActionOrMoreButton = host => {
	if (!host.shadowRoot) return;
	const menu = host.shadowRoot.querySelector(SIDEBAR.ELEMENTS.MENU);
	if (menu) {
		focusLastMenuAction(menu);
	} else {
		const moreButton = getMoreButton(host);
		if (moreButton && !moreButton.hidden) {
			moreButton.focus();
		} else focusLastAction(host);
	}
};

export const createSidebarKeyBindingBehavior = () => {
	return {
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				onEnterKeyPress: ["Enter", " "],
				onArrowKeyPress: ["ArrowUp", "ArrowDown"],
				onTabKeyPress: ["Tab"]
			},
			handlers: {
				onArrowKeyPress: (host, event, dispatch) => {
					if (event.ctrlKey || event.shiftKey) return true;
					dispatch(ARROW_KEY_PRESSED_ACTION, {
						host,
						event
					});
				},
				onEnterKeyPress: (host, event, dispatch) => {
					if (!eventCameFromMenuAction(event)) return;
					dispatch(ENTER_KEY_PRESSED_ACTION, {
						host,
						event
					});
				},
				onTabKeyPress: (host, event) => {
					if (event.shiftKey) focusFirstTabbable(host);
					else focusLastActionOrMoreButton(host);
					return false;
				}
			}
		}
	};
};

export const createSidebarKeyBindingActionsHandlers = () => {
	return {
		[ARROW_KEY_PRESSED_ACTION]: ({ action, state, dispatch }) => {
			const {
				properties: { openedMore }
			} = state;
			const {
				event,
				event: { target, code },
				host
			} = action.payload;
			switch (code) {
				case "Down":
				case "ArrowDown": {
					if (!openedMore && eventCameFromMoreButton(host, event))
						selectActionAfterMore(state, dispatch, host);
					else nextAction(state, dispatch, host);
					return;
				}
				case "Up":
				case "ArrowUp": {
					if (!openedMore && eventCameFromMoreButton(host, event))
						selectActionBeforeMore(state, dispatch, host);
					else prevAction(state, dispatch, host);
					return;
				}
			}
		},
		[ENTER_KEY_PRESSED_ACTION]: ({ action, state, dispatch }) => {
			const {
				properties: { openedMore }
			} = state;
			const { host } = action.payload;
			if (!openedMore) return;
			const moreButton = getMoreButton(host);
			if (moreButton) moreButton.focus();
			dispatch(SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED, { opened: false });
		}
	};
};

export const createSidebarKeyBindingActions = () => {
	return {
		[ARROW_KEY_PRESSED_ACTION]: {
			private: true
		},
		[ENTER_KEY_PRESSED_ACTION]: {
			private: true
		}
	};
};
