export const VIEWABLE_TAG_QUERY_KEY = "GlideViewableTagQuery_Query";
export const VIEWABLE_RECORD_TAGS = "viewableRecordTags";

const TAGS_GET_FRAGMENT_QUERY = `
        ${VIEWABLE_TAG_QUERY_KEY} {
            ${VIEWABLE_RECORD_TAGS}(table: $table, sysId: $sysId) {
                canCreateGlobalTags
                enabled
                records {
                    name
                    canEdit
                    sysId
                    viewableBy
                    labelEntry
                }
            }
        }
`;

export const TAG_QUERIES = {
	TAGS_GET_QUERY: `query($table: String!, $sysId: String!) {
        ${TAGS_GET_FRAGMENT_QUERY}
	}`,
	TAGS_GET_FRAGMENT_QUERY,
	TAG_DELETE_LABEL_ENTRY: `mutation($sysId: String!) {
        GlideRecord_Mutation {
            delete_label_entry(sys_id: $sysId) {
                modified_count
            }
        }
    }`
};
