import { createHeaderDataSource } from "./datasource/headerDataSource";
import { createViewableRecordTagDataSource } from "../tags/tag/tagDataSource";
import { createDataProvider, getFetchActions } from "@devsnc/library-form";

const HEADER_DATA_PROVIDER = "header-data-provider";

export const headerFetchActions = getFetchActions(HEADER_DATA_PROVIDER);
export const createHeaderDataProvider = () => {
	return createDataProvider({
		name: HEADER_DATA_PROVIDER,
		dataSources: [
			createHeaderDataSource(""),
			createViewableRecordTagDataSource("")
		]
	});
};
