// Extending now dropdown panel
import { createCustomElement } from "@servicenow/ui-core";
import "@servicenow/now-avatar";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./_dropdown_panel.scss";
import behaviors from "./behaviors";
import { DropdownPanel } from "./views";
import { TYPEAHEAD } from "../constants";
import {
	addOutsideClickListener,
	removeOutsideClickListener
} from "../../../../utils/outsideClickHandler";
import { COMPONENT_TAG_TYPE_AHEAD_DROPDOWN_PANEL } from "../../constants";

/** @seismicElement sn-component-dropdown-panel */
createCustomElement(COMPONENT_TAG_TYPE_AHEAD_DROPDOWN_PANEL, {
	properties: {
		/**
		 * An array of item objects describing each item in the dropdown.
		 * Each object must have a unique value and displayValue.
		 * @type {{value: (string|number), displayValue: string, secondaryDisplayValue: string, icon: string}[]}
		 */
		items: { default: [], cast: "array" },

		/**
		 * label to be display at the top of the items (header)
		 * @type (string)
		 */
		label: { default: "" },

		/**
		 * Whether the dropdown is open or not
		 * @type (boolean)
		 */
		opened: { default: false, manageable: true }
	},
	renderer: {
		type: snabbdom,
		view: DropdownPanel
	},
	styles,
	behaviors,
	onBootstrap(host, dispatch) {
		const { table, sysId } = host;
		addOutsideClickListener(host, dispatch, (host, dispatch, event) => {
			if (host.opened) {
				const paths = event.composedPath();
				const rootHost = host.getRootNode();
				if (paths.length > 0 && paths[0].getRootNode() !== rootHost) {
					dispatch(TYPEAHEAD.ACTIONS.DROPDOWN_PANEL_OPENED_SET, {
						value: false
					});
				}
			}
		});
	},
	onDisconnect(host) {
		removeOutsideClickListener(host);
	}
});
