import {
	getLabelValuePairContainer,
	getLabelValuePairs
} from "./labelValueProvider";

const delimiterPlaceholder = "{{delimiter}}";
const delimiterLabelPlaceholder = "{{label}}";
const delimiterInlineStylePlaceholder = "{{inlineStyle}}";
const delimiterClass = "delimiter-item";
const delimiterClassSelector = `.${delimiterClass}`;
const delimiterValueClass = "delimiter-value";
const delimiterDisplayInlineStyle = "display:flex;align-items:baseline;";
const delimiterItemTemplate = `<div class="now-label-value-pair ${delimiterClass}" aria-hidden style="min-width: 0;width: .1rem;${delimiterInlineStylePlaceholder}"><dt aria-hidden><div class="now-line-height-crop">${delimiterLabelPlaceholder}<div class="now-label-value-label"></div></div></dt><dd aria-hidden><div class="now-line-height-crop ${delimiterValueClass}">${delimiterPlaceholder}</div></dd></div>`;

const getDelimiterHtml = (delimiter, hideLabels, displayInline) =>
	delimiterItemTemplate
		.replace(delimiterPlaceholder, delimiter)
		.replace(
			delimiterLabelPlaceholder,
			hideLabels || displayInline ? "" : "&nbsp;"
		)
		.replace(
			delimiterInlineStylePlaceholder,
			displayInline ? delimiterDisplayInlineStyle : ""
		);

const createDelimiterNode = (delimiter, hideLabels, displayInline) =>
	document
		.createRange()
		.createContextualFragment(
			getDelimiterHtml(delimiter, hideLabels, displayInline)
		).firstChild;

const replaceDelimiterNodes = (
	nodeWrapper,
	delimiterNodes,
	delimiter,
	hideLabels,
	displayInline
) => {
	delimiterNodes.forEach(node => {
		nodeWrapper.replaceChild(
			createDelimiterNode(delimiter, hideLabels, displayInline),
			node
		);
	});
};

const removeDelimiterNodes = (nodeWrapper, delimiterNodes) => {
	if (delimiterNodes.length)
		delimiterNodes.forEach(node => nodeWrapper.removeChild(node));
};

const getDelimiterNodes = toggleContainer => {
	const dl = getLabelValuePairContainer(toggleContainer);
	return { delimiterNodes: dl.querySelectorAll(delimiterClassSelector), dl };
};

const removeDelimiterNodesFromContainer = toggleContainer => {
	const { delimiterNodes, dl } = getDelimiterNodes(toggleContainer);
	if (delimiterNodes.length)
		delimiterNodes.forEach(node => dl.removeChild(node));
};

const handleDelimiter = (state, toggleContainer) => {
	const {
		properties: { delimiter, hideLabels, displayInline }
	} = state;

	const { delimiterNodes, dl } = getDelimiterNodes(toggleContainer);

	if (delimiterNodes && delimiterNodes.length) {
		if (!delimiter) removeDelimiterNodes(dl, delimiterNodes);
		else
			replaceDelimiterNodes(
				dl,
				delimiterNodes,
				delimiter,
				hideLabels,
				displayInline
			);
	} else if (delimiter) {
		const labelValuePairNodes = getLabelValuePairs(toggleContainer);
		labelValuePairNodes
			.filter(pair => !pair.classList.contains(delimiterClass))
			.forEach((labelValuePair, index) => {
				const isLastNode = index === labelValuePairNodes.length - 1;
				if (!isLastNode) {
					labelValuePair.insertAdjacentHTML(
						"afterend",
						getDelimiterHtml(delimiter, hideLabels, displayInline)
					);
				}
			});
	}
};

export {
	delimiterClass,
	getDelimiterNodes,
	removeDelimiterNodesFromContainer,
	handleDelimiter
};
