import {
	ATTACHMENT_PREVIEW_LOAD_ACTION,
	ATTACHMENT_PREVIEW_DISMISS_ACTION
} from "../constants";
import {
	ATTACHMENT_DOWNLOAD_ACTION,
	ATTACHMENT_ADDED
} from "../../../constants/attachmentConstants";
import { getActiveElement } from '@servicenow/library-enhanced-utils';
import { SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN } from "@devsnc/sn-scripted-modal";

const downloadAttachment = sysId => {
	window.location.href = `/sys_attachment.do?sys_id=${sysId || null}`;
};

export const getReadOnlyPreviewOverlayProps = (state, properties) => {
	const { showAttachmentPreview, attachmentPreviewItem } = state;

	const { table, sysId } = properties;

	return {
		canUploadAttachment: false,
		isReadOnly: true,
		showAttachmentPreview,
		attachmentPreviewItem,
		table,
		sysId
	};
};

const showAttachmentPreview = (updateState, item, dispatch) => {
	const lastTriggerCard = getActiveElement();
	updateState({
		showAttachmentPreview: true,
		attachmentPreviewItem: { ...item },
		lastTriggerCard
	});
	dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, true);
};

const hideAttachmentPreview = (updateState, lastTriggerCard, dispatch) => {
	if (lastTriggerCard) lastTriggerCard.focus();
	updateState({
		showAttachmentPreview: false,
		attachmentPreviewItem: null,
		lastTriggerCard: null
	});
	dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, false)
};

export default {
	actions: {
		[ATTACHMENT_PREVIEW_LOAD_ACTION]: {
			private: true
		},
		[ATTACHMENT_PREVIEW_DISMISS_ACTION]: {},
		[ATTACHMENT_DOWNLOAD_ACTION]: {
			private: true
		},
		[ATTACHMENT_ADDED]: {}
	},
	actionHandlers: {
		/**
		 * Open the attachment preview overly
		 * @param item (name, readonly, type, canDelete)
		 */
		[ATTACHMENT_PREVIEW_LOAD_ACTION]: ({ updateState, action, dispatch }) => {
			action.stopPropagation();
			showAttachmentPreview(updateState, action.payload.item, dispatch);
		},
		/**
		 * Dismiss attachment preview overlay
		 */
		[ATTACHMENT_PREVIEW_DISMISS_ACTION]: ({
			state: { lastTriggerCard },
			updateState,
			action,
			properties,
			dispatch
		}) => {
			if (properties.showAttachmentPreview) return; //if properties.showAttachmentPreview is set, preview came from the container, let action passes to the container to handler.

			action.stopPropagation();
			hideAttachmentPreview(updateState, lastTriggerCard, dispatch);
		},
		/**
		 * Download the attachment
		 * @param sysId sys_id of the attachment
		 */
		[ATTACHMENT_DOWNLOAD_ACTION]: ({ action }) => {
			action.stopPropagation();
			const { sysId } = action.payload;
			if (sysId) downloadAttachment(sysId);
		},
		/**
		 * Handles attachment added action
		 */
		[ATTACHMENT_ADDED]: {
			effect: ({ updateState }) => {
				updateState({ loadDocViewerIfNecessary: true});
			},
			stopPropagation:  true
		},
		
		

	}
};
