import {
	QUERY_FRAGMENT,
	HEADER_CONFIGURATION_QUERY_KEY,
	HEADER_CONFIGURATION_TABLE
} from "./configurationQuery";
import { createDataProviderDataSource } from "@devsnc/library-form";

const getParams = properties => ({
	conditions: `table=${_.get(properties, "table", "")}^active=1^ORDERBYorder`
});

const getPlatformURL = (table, sysId) => `/${table}.do?sys_id=${sysId}`;

const getURL = data => {
	const headerConfiguration = _.first(
		_.get(
			data,
			`${HEADER_CONFIGURATION_QUERY_KEY}.${HEADER_CONFIGURATION_TABLE}._results`,
			[]
		)
	);
	if (!headerConfiguration) return "";

	const sysId = _.get(headerConfiguration, `sys_id.value`);
	if (!sysId) return {};

	return { url: getPlatformURL(HEADER_CONFIGURATION_TABLE, sysId) };
};

export const createHeaderConfigurationDataSource = () =>
	createDataProviderDataSource({
		query: QUERY_FRAGMENT,
		variables: ["conditions"],
		getParams,
		transform: getURL,
		props: {
			table: { default: "" }
		},
		selectableProps: {
			url: {
				default: ""
			}
		}
	});
