import {
	ATTACHMENT_CARD_ITEM_CLICKED,
	ATTACHMENT_ACTIONS,
	ATTACHMENT_DELETE_ACTION,
	ATTACHMENT_DOWNLOAD_ACTION,
	ATTACHMENT_ENABLE_EDIT_ACTION,
	ATTACHMENT_CARD_DROPDOWN
} from "../constants/attachmentConstants";
import _ from "lodash";

const getAction = id => {
	switch (id) {
		case ATTACHMENT_ACTIONS.DOWNLOAD.id:
			return ATTACHMENT_DOWNLOAD_ACTION;
		case ATTACHMENT_ACTIONS.REMOVE.id:
			return ATTACHMENT_DELETE_ACTION;
		case ATTACHMENT_ACTIONS.RENAME.id:
			return ATTACHMENT_ENABLE_EDIT_ACTION;
	}
};

const attachmentActionHandler = ({ action, dispatch, properties }) => {
	if (action.meta.componentName !== ATTACHMENT_CARD_DROPDOWN) return;
	const {
		item: { sysId, name }
	} = properties;
	const actionId = _.get(action, "payload.item.id", "");

	const payload = { sysId, name };
	const actionName = getAction(actionId);
	if (actionName) dispatch(actionName, payload);
};

export default {
	actions: {
		[ATTACHMENT_CARD_ITEM_CLICKED]: {
			private: true
		},
		[ATTACHMENT_ENABLE_EDIT_ACTION]: {
			private: true
		}
	},
	handlers: {
		[ATTACHMENT_CARD_ITEM_CLICKED]: attachmentActionHandler,
		[ATTACHMENT_ENABLE_EDIT_ACTION]: ({ updateState, action }) =>
			updateState({
				enableEditingTriggered: {
					timestamp: Date.now()
				}
			})
	}
};
