export const EDIT_TAG_MODAL_QUERIES = {
	GET_TAG_DETAILS: `
        query($tagId: String!) {
            GlideRecord_Query {
                label(sys_id: $tagId) {
                    _results{
						sys_id { value }
						name { value, canWrite }
						viewable_by { value }
						user_list { value displayValue }
						group_list { value displayValue }
						owner { value displayValue}
                    }
                }
            }
        }
    `,
	UPDATE_TAG: `
        mutation ($sys_id: String!, $name: String!, $user_list: String, $group_list: String, $viewable_by: String!) {
            GlideRecord_Mutation {
                update_label(
                    sys_id: $sys_id
                    name: $name,
                    user_list: $user_list,
                    group_list: $group_list
                    viewable_by: $viewable_by
                ) {
                    name { value }
                    viewable_by { value }
                }
            }
        }
    `,
	GET_USERS: `
        query($queryConditions: String!) {
            GlideRecord_Query {
                sys_user(queryConditions: $queryConditions, pagination: { limit: 10}) {
                    _results {
                        name { value }
                        sys_id { value }
                        email { value }
                        avatar { value }
                    }
                    _rowCount
                }
            }
        }
    `,
	GET_AVATARS: `
        query($queryConditions: String!) {
            GlideRecord_Query {
                sys_user(queryConditions: $queryConditions, pagination: { limit: 20}) {
                    _results {
                        sys_id { value }
                        avatar { value }
                    }
                    _rowCount
                }
            }
        }
    `,
	GET_USER_GROUPS: `
        query($queryConditions: String!) {
            GlideRecord_Query {
                sys_user_group(queryConditions: $queryConditions, pagination: { limit: 10}) {
                    _results {
                        name { value }
                        sys_id { value }
                    }
                    _rowCount
                }
            }
        }
    `
};
