import { createCustomElement, actionTypes } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { createSidebarDataProvider } from "../dataProvider/sidebarDataProvider";
import { default as SIDEBAR_COMPONENT } from "./sidebar";
import _ from "lodash";
import { SIDEBAR } from "../constants/constants";
import { preWsConfigSysId } from "sn-uxpage-presource";

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const view = state => {
	const {
		properties: { sidebarActions, formModel, sidebarPanelExtras },
		opened
	} = state;

	if (_.isEmpty(sidebarActions)) return null;

	return (
		<SIDEBAR_COMPONENT
			actions={sidebarActions}
			opened={opened}
			form-model={formModel}
			sidebar-panel-extras={sidebarPanelExtras}
			manage-opened={true}
		/>
	);
};

const SIDEBAR_CONNECTED_COMPONENT = "now-record-common-sidebar-connected";
const sidebarDataProvider = createSidebarDataProvider();
createCustomElement(SIDEBAR_CONNECTED_COMPONENT, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [preWsConfigSysId],
	properties: {
		formModel: {
			default: {},
			unstableParse: true
		},
		opened: {
			default: true,
			unstableParse: true
		},
		sidebarPanelExtras: {
			default: {}
		},
		...sidebarDataProvider.properties
	},
	initialState: {
		opened: true
	},
	actions: sidebarDataProvider.actions,
	actionHandlers: {
		...sidebarDataProvider.actionHandlers,
		[SIDEBAR.ACTIONS.OPENED_SET]: ({ action, state, updateState }) => {
			const {
				payload: { value: opened }
			} = action;
			if (opened !== state.opened) updateState({ opened });
			action.stopPropagation();
		},
		[COMPONENT_BOOTSTRAPPED]: ({
			properties: { opened },
			state,
			updateState
		}) => {
			if (opened !== state.opened) updateState({ opened });
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, state, updateState }) => {
			const { name, value } = action.payload;
			if (name === "opened" && value !== state.opened)
				updateState({ opened: value });
		}
	}
});

export default SIDEBAR_CONNECTED_COMPONENT;
