import { getMaxLineHeight } from "./lineHeightCalculator";

const ELLIPSIS = "…";

// do search to find index of word which is the last word that will be able to fit
// and then another to find the best letter in the last word to pu the ellipsis
const setEllipsisPosition = (element, words, maxHeight) => {
	let max = words.length - 1;
	let min = 0;
	let middleWordIndex = 0;
	let bestFitWords = "";
	if (max > 0)
		// binary search for best word
		while (min <= max) {
			middleWordIndex = Math.floor((min + max) / 2);
			element.innerText = words.slice(0, middleWordIndex).join(" ") + ELLIPSIS;
			if (element.scrollHeight > maxHeight) {
				max = middleWordIndex - 1;
			} else {
				min = middleWordIndex + 1;
				bestFitWords = words.slice(0, middleWordIndex).join(" ");
			}
		}

	element.innerText = bestFitWords;

	if (middleWordIndex > words.length - 1) return;
	const wordToTruncate = words[middleWordIndex];
	max = wordToTruncate.length - 1;
	min = 0;
	let middleLetterIndex = 0;
	let bestFitFinal = bestFitWords + ELLIPSIS;
	//binary search to find best place in word
	while (min <= max) {
		middleLetterIndex = Math.floor((min + max) / 2);
		element.innerText =
			bestFitWords +
			" " +
			wordToTruncate.slice(0, middleLetterIndex) +
			ELLIPSIS;
		if (element.scrollHeight > maxHeight) max = middleLetterIndex - 1;
		else {
			min = middleLetterIndex + 1;
			bestFitFinal = element.innerText;
		}
	}

	element.innerText = bestFitFinal.trim();
};

export function lineClamp(element, lineCount) {
	// reset back to original text if already been clamped
	element.innerText = element.getAttribute("title");
	// if no lineCount or 0 then just leave it
	if (typeof lineCount !== "number" || lineCount === 0) {
		element.style.cssText = "white-space:normal;word-break:break-all;";
		return;
	}

	const words = element.innerText.split(" ");

	if (!words.length) return;

	//calc the max height
	const maxHeight = getMaxLineHeight(lineCount);

	// If already fits, were good to go
	if (element.scrollHeight <= maxHeight) return;

	setEllipsisPosition(element, words, maxHeight);
}
