import { createHttpEffect } from "@servicenow/ui-effect-http";
import _ from "lodash";
import { ATTACHMENT_ACTIONS } from "../../../constants/attachmentConstants";
import { ADD_NOTIFICATIONS } from "../../../constants/constants";

import { ATTACHMENT_UPLOAD_SAVE, FILE_UPLOAD } from "../constants";
import { t } from "sn-translate";
import { getFileFromFormData, transformLoadingItem } from "./attachmentUploadUtil";
import AttachmentElement from "../../../attachments/AttachmentElement";
import { loadDocViewer } from '../attachmentOverlayUtil';

const createUploadAttachmentEffect = createHttpEffect(
	"/angular.do?sysparm_type=ngk_attachments&sysparm_verbose_session_message=true",
	{
		method: "POST",
		headers: { "X-WantSessionNotificationMessages": true },
		queryParams: ["action", "table", "sys_id", "load_attachment_record"],
		dataParam: "data",
		batch: false,
		startActionType: FILE_UPLOAD.ACTIONS.STARTED,
		successActionType: ATTACHMENT_UPLOAD_SAVE.ACTIONS.SUCCESS,
		errorActionType: ATTACHMENT_UPLOAD_SAVE.ACTIONS.FAIL
	}
);

const createCheckErrorOnUploadInterceptor = context => {
	const { coeffects } = context;
	const { state, action, dispatch } = coeffects;

	const errors = _.get(action, "payload.errors", []);
	if (_.isEmpty(errors)) return context;
	else {
		const fileName = action.payload.file_name;
		let notifications = [];
		errors.forEach(error => {
			if (!_.isEmpty(error.message))
				notifications.push({
					type: "error",
					message: t(`Error uploading ${fileName}: ${error.message}`)
				});
		});
		if (notifications.length)
			dispatch(ADD_NOTIFICATIONS, {
				notifications
			});
		return {
			...context,
			effects: []
		};
	}
};

export default {
	actions: {
		[ATTACHMENT_UPLOAD_SAVE.ACTIONS.START]: {
			private: true
		},
		[ATTACHMENT_UPLOAD_SAVE.ACTIONS.SUCCESS]: {
			private: true
		},
		[ATTACHMENT_UPLOAD_SAVE.ACTIONS.FAIL]: {
			private: true
		},
		[FILE_UPLOAD.ACTIONS.STARTED]: {
			stopPropagation: true
		},
		[FILE_UPLOAD.ACTIONS.SUCCEEDED]: {
			stopPropagation: true
		},
		[FILE_UPLOAD.ACTIONS.FAILED]: {
			stopPropagation: true
		}
	},
	actionHandlers: {
		[ATTACHMENT_UPLOAD_SAVE.ACTIONS.START]: createUploadAttachmentEffect,
		[ATTACHMENT_UPLOAD_SAVE.ACTIONS.FAIL]: ({ state, action, dispatch }) => {
			const {
				data,
				options: { params }
			} = action.payload;
			const message = data && data.message;
			dispatch(ADD_NOTIFICATIONS, {
				notifications: [
					{
						type: "error",
						message: t(
							`Error uploading '{0}'{1}`,
							state["currentFileName"],
							message ? ": " + message : ""
						)
					}
				]
			});
			const file = getFileFromFormData(action);
			dispatch(FILE_UPLOAD.ACTIONS.FAILED, { ...action.payload, file });
		},
		[ATTACHMENT_UPLOAD_SAVE.ACTIONS.SUCCESS]: {
			effect: ({ properties, dispatch, action }) => {
				const { table, sysId } = properties;
				const file = getFileFromFormData(action);
				dispatch(FILE_UPLOAD.ACTIONS.SUCCEEDED, { ...action.payload, file });
			},
			interceptors: [{ after: createCheckErrorOnUploadInterceptor }]
		},
		[FILE_UPLOAD.ACTIONS.STARTED]: ({ state, updateState, action }) => {
			const file = getFileFromFormData(action);
			const { loadingItems, docViewerEnabled, docViewerState } = state;
			const item = {
				canDelete: false,
				canEditName: false,
				name: file.name,
				size: file.size,
				state: "uploading",
				type: file.type,
				file
			};
			updateState(({ state: { loadingItems } }) => ({
				loadingItems: [...loadingItems, item]
			}));
			if (docViewerEnabled)
				loadDocViewer(docViewerState, updateState);
		},
		[FILE_UPLOAD.ACTIONS.SUCCEEDED]: ({ state, updateState, action, dispatch }) => {
			const { loadingItems, list, data, data: {attachments=[], count=0} } = state;
			const {file} = action.payload;
			let items = [...loadingItems];
			const itemIndex = items.findIndex(item => item.file === file);
			let [item] = items.splice(itemIndex, 1);
			item = transformLoadingItem(item, action.payload);

			updateState({
				data: { ...data, attachments: [...attachments, item], count: count + 1 },
				list: [...list, item],
				count: list.length + 1,
				loadingItems: items
			});

			dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_UPLOAD_SUCCESS,action.payload);

			dispatch("NOW_RECORD_FORM#ACTION_STATUS_UPDATED", {
				component: AttachmentElement,
				type: "badge",
				value: (count+1).toString()
			});

		},
		[FILE_UPLOAD.ACTIONS.FAILED]: ({ state, updateState, action }) => {
			const { loadingItems } = state;
			const { file } = action.payload;
			let items = [...loadingItems];
			let itemIndex = items.findIndex(item => item.file === file);
			items.splice(itemIndex, 1);
			updateState({ loadingItems: items });
		}
	}
};
