import _ from "lodash";
import { createCustomElement, actionTypes } from "@servicenow/ui-core";
import { eventually } from "@tectonic/seismic-testing-helpers";
const { COMPONENT_RENDERED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

import react from "@servicenow/ui-renderer-react";
import {
	TemplateGQLRead as read,
	TemplateDataGQLRead as readData
} from "./datasources/TemplateGQLRead";
import { TemplateGQLDestroy as destroy } from "./datasources/TemplateGQLDestroy";
import {
	default as SnComponentTemplate,
	getPaginationObject,
	constructQueryString
} from "./Template";
import style from "./Template.scss";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import {
	TEMPLATE_ACTIONS,
	TEMPLATE_EVENT_SOURCE,
	TEMPLATE_ADD_NOTIFICATIONS,
	TEMPLATE_CLEAR_NOTIFICATIONS,
	TEMPLATE_PREVIEW_RECORD
} from "../constants/templateConstants";
import { ADD_NOTIFICATIONS, CLEAR_NOTIFICATIONS } from "../constants/constants";
import { SEARCH_INPUT_ENTERED } from "../constants/searchbarConstants";
import { t } from "sn-translate";
import {
	executeEffectIfValidEventSource,
	transformPreviewRecordPayload
} from "@devsnc/library-form";

const NOW_DROPDOWN_ITEM_CLICKED = "NOW_DROPDOWN#ITEM_CLICKED";
const NOW_TABS_SELECTED_ITEM_SET = "NOW_TABS#SELECTED_ITEM_SET";
const NOW_MODAL_OPENED_SET_ACTION = "NOW_MODAL#OPENED_SET";
const REFRESH_REQUESTED = "refreshRequested";
const PREVIEW_RECORD = 'PREVIEW_RECORD';

const applyTemplateData = createGraphQLEffect(readData, {
	variableList: ["sysId", "targetSysId", "encodedRecord"],

	successActionType: TEMPLATE_ACTIONS.TEMPLATE_APPLY_SUCCESS,
	errorActionType: TEMPLATE_ACTIONS.TEMPLATE_APPLY_ERROR
});

const fetchTemplateData = createGraphQLEffect(read, {
	variableList: ["queryConditions", "paginationVar"],
	successActionType: TEMPLATE_ACTIONS.TEMPLATE_FETCH_SUCCESS,
	errorActionType: TEMPLATE_ACTIONS.TEMPLATE_FETCH_ERROR
});

const deleteTemplate = createGraphQLEffect(destroy, {
	variableList: ["sys_id"],
	successActionType: TEMPLATE_ACTIONS.TEMPLATE_DESTROY_SUCCESS,
	errorActionType: TEMPLATE_ACTIONS.TEMPLATE_DESTROY_ERROR
});

const search = ({
	action: { payload },
	dispatch,
	updateState,
	properties,
	state: { selectedTab, searchFilter }
}) => {
	const filter = payload.filter;
	updateState({
		searchFilter: {
			...searchFilter,
			[selectedTab]: filter
		},
		offset: 0,
		shouldRender: false
	});
	refetchData(dispatch, properties, selectedTab, filter);
};

export const refetchData = (
	dispatch,
	properties,
	selectedTab,
	searchQuery = "",
	actionMeta
) => {
	const queryConditions = constructQueryString(properties.table, searchQuery, selectedTab);
	dispatch(
		TEMPLATE_ACTIONS.TEMPLATE_FETCH_ACTION,
		{
			queryConditions,
			paginationVar: getPaginationObject()
		},
		actionMeta
	);
};

export const escapeQueryString = query => {
	return query.replace(/\^/g, "^^");
};

export const getTemplateString = fields => {
	const nonEmptyFields = fields && Object.keys(fields)
		.filter(field => {
			if (!fields[field].visible || fields[field].readonly) return false;
			return !_.isEmpty(fields[field].value);
		})
		.sort();

	if (nonEmptyFields && nonEmptyFields.length > 0)
		return (
			nonEmptyFields.reduce(
				(acc, field) =>
					acc + `${field}=${escapeQueryString(fields[field].value)}^`,
				""
			) + "EQ"
		);

	return "";
};

const focusTemplateCard = (templateSysId, host) => {
	setTimeout(() => {
		const card = host.shadowRoot.querySelector(
			`div[data-sys-id="${templateSysId}"`
		);
		if (card) card.focus();
	});
};

const focusSearch = host => {
	eventually(
		() => {
			const search = host.shadowRoot.querySelector(
				`sn-form-internal-searchbar`
			);
			if (!search) return false;

			const input = search.shadowRoot.querySelector("input");
			if (!input) return false;
			input.focus();
			return true;
		},
		{ timeout: 100, interval: 5 }
	);
};

export const addNotificationsEffect = ({
	action,
	properties: { classicForm },
	dispatch
}) => {
	if (classicForm) return;

	const {
		payload: { notifications }
	} = action;
	dispatch(TEMPLATE_ADD_NOTIFICATIONS, {
		items: notifications
	});
	action.stopPropagation();
};

export const clearNotificationsEffect = ({
	action,
	properties: { classicForm },
	dispatch
}) => {
	if (classicForm) return;

	dispatch(TEMPLATE_CLEAR_NOTIFICATIONS);
	action.stopPropagation();
};

const acceptedProperties = {
	/**
	 * Table the templates are applied to.
	 * @type {string}
	 */
	table: {},
	/**
	 * Record Sys ID.
	 * @type {string}
	 */
	sysId: {},
	/**
	 * Encoded Record.
	 * @type {string}
	 */
	encodedRecord: {},
	/**
	 * Map of fields to show in the form in the following format: {field_name: {...}, ...}.
	 * @type {object}
	 */
	fields: {},
	/**
	 * Form view to use when creating or editing a template.
	 * @type {string}
	 */
	view: { default: "default view" },
	/**
	 * Flag that indicates whether the component is rendered in a classic form
	 * @type {boolean}
	 * @private
	 */
	classicForm: {},
	/**
	 * Set this prop to timestamp to force re-fetch data
	 * @private
	 */
	refreshRequested: {}
};

const TemplateElement = "now-record-common-templates-connected";

/**
 * Displays a list of templates that the user can apply to a record. 
 * Selecting a template card applies the template to the associated form.
 *
 * ```jsx
 * <now-record-common-templates-connected
 *    table = 'incident'
 *    sysId = '57af7aec73d423002728660c4cf6a71c'
 *    fields = {}
 *    view = 'workspace'
 * />
 * ```
 *
 * @seismicElement now-record-common-templates-connected
 * @summary Provides a list of templates that the user can choose from, and applies the selected template to the record. 
 *
 */
createCustomElement(TemplateElement, {
	renderer: {
		type: react,
		view: SnComponentTemplate
	},
	properties: acceptedProperties,
	initialState: {
		selectedTab: "all",
		searchFilter: {}
	},
	dispatches: {
		/**
		 * Dispatched to show notification messages.
		 * @type {AddNotificationsPayload}
		 */
		[ADD_NOTIFICATIONS]: {},
		/**
		 * Dispatched to show notification messages when a template is inside a record page in UI Builder.
		 * @type {
		 	TemplateAddNotificationsPayload
		 }
		 */
		[TEMPLATE_ADD_NOTIFICATIONS]: {},
		/**
		 * Dispatched to clear all notification messages.
		 */
		[CLEAR_NOTIFICATIONS]: {},
		/**
		 * Dispatched to clear all notification messages when a template is inside a record page in UI Builder.
		 */
		[TEMPLATE_CLEAR_NOTIFICATIONS]: {}
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				action: {
					payload: { name, value, previousValue }
				},
				state: { selectedTab, searchFilter },
				properties,
				dispatch,
				updateState
			} = context;

			const searchTerm = searchFilter ?  (searchFilter.all ? searchFilter.all : searchFilter.my_templates) : "";
			if (name === REFRESH_REQUESTED) {
				if (value && !previousValue) {
					refetchData(dispatch, properties, selectedTab, searchTerm);
				} else if (value.timestamp > previousValue.timestamp) {
					refetchData(dispatch, properties, selectedTab, searchTerm);
				}
			}
		},
		[COMPONENT_RENDERED]: ({ updateState, state, host }) => {
			const { focusNext } = state;
			if (!focusNext) return;

			const { templateSysId, tag } = focusNext;
			if (templateSysId) focusTemplateCard(templateSysId, host);
			else focusSearch(host);

			updateState({
				focusNext: null,
				shouldRender: false
			});
		},
		[TEMPLATE_ACTIONS.TEMPLATE_FETCH_ACTION]: {
			...fetchTemplateData,
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_FETCH_SUCCESS]: {
			effect: ({ updateState, action, state }) => {
				const {
					meta: { focusNext, options }
				} = action;
				const query = _.get(options, "variables.queryConditions");
				const resultTab = query.includes("roles") ? "all" : "my_templates";

				updateState({
					templateData: {
						...state.templateData,
						[resultTab]: action.payload
					},
					...(focusNext ? { focusNext } : {})
				});
			},
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_FETCH_ERROR]: {
			effect: ({ action }) => {
				console.error(`Fetching template failed. ERROR: ${action}`);
			},
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_APPLY_ACTION]: {
			...applyTemplateData,
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_APPLY_SUCCESS]: {
			effect: ({ updateState, action }) => {
				updateState({ applyTemplate: action.payload });
			},
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_APPLY_ERROR]: {
			effect: ({ action }) => {
				console.error(`Applying template failed. ERROR: ${action}`);
			},
			stopPropagation: true
		},
		[SEARCH_INPUT_ENTERED]: {
			effect: search,
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_DESTROY_ACTION]: {
			...deleteTemplate,
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_DESTROY_SUCCESS]: {
			effect: ({
				dispatch,
				properties,
				state: { searchFilter, selectedTab },
				updateState,
				action
			}) => {
				dispatch(ADD_NOTIFICATIONS, {
					notifications: [
						{
							type: "info",
							message: t("The template has been deleted")
						}
					]
				});
				updateState({
					templateData: {},
					shouldRender: false
				});

				const templateSysId = _.get(action, "meta.nextFocusTemplateId");
				const actionMeta = {
					focusNext: templateSysId
						? { templateSysId }
						: { tag: "sn-form-internal-searchbar" }
				};

				refetchData(
					dispatch,
					properties,
					selectedTab,
					searchFilter[selectedTab],
					actionMeta
				);
			},
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_DESTROY_ERROR]: {
			effect: ({ action, host }) => {
				dispatch(ADD_NOTIFICATIONS, {
					notifications: [
						{
							type: "error",
							message: t("Deleting template failed")
						}
					]
				});
				const templateSysId = _.get(action, "meta.deletingTemplateId");
				if (!templateSysId) focusTemplateCard(templateSysId, host);
			},
			stopPropagation: true
		},
		[NOW_DROPDOWN_ITEM_CLICKED]: executeEffectIfValidEventSource(
			TEMPLATE_EVENT_SOURCE.TEMPLATE_MENU_ITEM,
			{
				effect: ({ action, updateState, dispatch, properties: {classicForm} }) => {
					const { template, item } = _.get(action, "payload");
					if (item.id === "delete") {
						updateState({
							displayDeleteModal: true,
							deletingTemplate: template
						});
					} else if (item.id === "edit") {
						const previewAction = classicForm ? PREVIEW_RECORD : TEMPLATE_PREVIEW_RECORD;
						dispatch(previewAction, transformPreviewRecordPayload({
							table: "sys_template",
							sysId: template.sys_id.value
						}));
					}
				},
				stopPropagation: true
			}
		),
		[TEMPLATE_ACTIONS.DELETE_CANCELED_ACTION]: {
			effect: ({ updateState }) => {
				updateState({
					displayDeleteModal: false,
					deletingTemplate: null
				});
			},
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.DELETE_CONFIRMED_ACTION]: {
			effect: ({ action, updateState, dispatch, state }) => {
				const sysId = _.get(action, "payload.template.sys_id.value");
				updateState({
					displayDeleteModal: false,
					deletingTemplate: null
				});
				const templates = _.get(
					state.templateData[state.selectedTab],
					"data.GlideRecord_Query.sys_template._results",
					[]
				);
				const index = templates.findIndex(
					template => template.sys_id.value === sysId
				);
				const nextTemplate = templates[index + 1];
				const extraMeta = nextTemplate
					? { nextFocusTemplateId: nextTemplate.sys_id.value }
					: {};
				dispatch(
					TEMPLATE_ACTIONS.TEMPLATE_DESTROY_ACTION,
					{ sys_id: sysId },
					{ deletingTemplateId: sysId, ...extraMeta }
				);
			},
			stopPropagation: true
		},
		[NOW_TABS_SELECTED_ITEM_SET]: {
			effect: ({ action, updateState, state, properties, dispatch }) => {
				const selectedTab = action.payload.value;
				const shouldRender = state.templateData[selectedTab] ? true : false;

				updateState({
					selectedTab,
					shouldRender
				});

				// If a tab selected for the first time
				// Or if the templateData needs to be updated,
				// Query for the templates

				if (!state.templateData[selectedTab])
					refetchData(
						dispatch,
						properties,
						selectedTab,
						state.searchFilter[selectedTab]
					);
			},
			stopPropagation: true
		},
		[NOW_MODAL_OPENED_SET_ACTION]: {
			effect: ({ updateState }) => {
				updateState({
					displayDeleteModal: false,
					deletingTemplate: null
				});
			},
			stopPropagation: true
		},
		[TEMPLATE_ACTIONS.TEMPLATE_PLUS_BUTTON_CLICKED]: executeEffectIfValidEventSource(
			TEMPLATE_EVENT_SOURCE.NEW_TEMPLATE_ICON,
			{
				effect: ({ dispatch }) => {
					dispatch(TEMPLATE_ACTIONS.TEMPLATE_CREATE_NEW);
				},
				stopPropagation: true
			}
		),
		[TEMPLATE_ACTIONS.TEMPLATE_CREATE_BUTTON_CLICKED]: executeEffectIfValidEventSource(
			TEMPLATE_EVENT_SOURCE.NEW_TEMPLATE_BUTTON,
			{
				effect: ({ dispatch }) => {
					dispatch(TEMPLATE_ACTIONS.TEMPLATE_CREATE_NEW);
				},
				stopPropagation: true
			}
		),
		[ADD_NOTIFICATIONS]: addNotificationsEffect,
		[CLEAR_NOTIFICATIONS]: clearNotificationsEffect,

		[TEMPLATE_ACTIONS.TEMPLATE_CREATE_NEW]: {
			effect: ({ dispatch, properties }) => {
				const previewAction = properties.classicForm ? PREVIEW_RECORD : TEMPLATE_PREVIEW_RECORD;
				dispatch(previewAction, transformPreviewRecordPayload({
					table: "sys_template",
					sysId: "-1",
					view: properties.view,
					query: `table=${properties.table}^template=${escapeQueryString(
						getTemplateString(properties.fields)
					)}`
				}));
			},
			stopPropagation: true
		}
	},
	styles: style
});

export default TemplateElement;
