import { createCustomElement } from "@servicenow/ui-core";
import react from "@servicenow/ui-renderer-react";
import "@servicenow/now-button";
import { t } from "sn-translate";

import SearchbarElement from "../searchbar";
import styles from "./AttachmentHeader.scss";
import {
	NOW_BUTTON_ICONIC_CLICKED,
	NOW_BUTTON_BARE_CLICKED,
	ATTACHMENT_UPLOAD_BUTTON_CLICKED,
	ATTACHMENT_ACTIONS
} from "../constants/attachmentConstants.js";

function renderFullMode(properties) {
	return (
		<div className={`sn-panel-header`}>
			<h2 className="sn-panel-header-title">{properties.title}</h2>
			<div className="sn-panel-header-search">
				<div className="sn-panel-header-content">
					{properties.hasAttachments && (
						<SearchbarElement placeholder={t("Search attachments")} />
					)}
				</div>
				<div className="sn-panel-header-content">
					{properties.canCreate && properties.hasAttachments && (
						<now-button-iconic
							icon="plusOutline"
							configAria={{ "aria-label": ATTACHMENT_ACTIONS.ADD_FILE.label }}
							variant="tertiary"
							tooltipContent={ATTACHMENT_ACTIONS.ADD_FILE.label}
						/>
					)}
				</div>
			</div>
		</div>
	);
}

function renderCompactMode(properties) {
	return (
		<div className={`sn-panel-header -compact`}>
			<h2 className="sn-panel-header-title -compact">{properties.title}</h2>
			{properties.canCreate && (
				<now-button-bare
					iconStart="plusOutline"
					label={ATTACHMENT_ACTIONS.ADD_FILE.label}
					size="sm"
					variant="secondary"
				/>
			)}
		</div>
	);
}

function view(state) {
	const { properties } = state;

	return properties.mode === "compact"
		? renderCompactMode(properties)
		: renderFullMode(properties);
}

/**
 * Component used to render the header of the attachment component.
 * This component displays the component title and an upload button in the compact mode,
 * as well as a searchbar in the full mode.
 *
 * ```jsx
 * <sn-form-internal-attachments-header
 *		title={...}
 *		hasAttachments={...}
 *		canCreate={...}
 *		mode={...}
 * />
 * ```
 *
 * @seismicElement sn-form-internal-attachments-header
 * @summary Header for the attachment component
 *
 */
createCustomElement("sn-form-internal-attachments-header", {
	renderer: {
		type: react,
		view
	},
	styles,
	properties: {
		/**
		 * Flag that indicates whether a user can upload attachments.
		 * If false, the upload button is not displayed in the header
		 * @type {boolean}
		 */
		canCreate: {},
		/**
		 * Flag that indicates whether the record has any attachments.
		 * If false, the searchbar and upload button are not displayed in the full mode
		 * @type {boolean}
		 */
		hasAttachments: {},
		/**
		 * The layout in which the component is rendered.
		 * The current supported layouts are "full" and "compact".
		 * @type {string}
		 */
		mode: { default: "full" },
		/**
		 * The title of the attachment component displayed in the header
		 * @type {string}
		 */
		title: {}
	},
	actions: {
		[NOW_BUTTON_ICONIC_CLICKED]: {
			private: true
		},
		[NOW_BUTTON_BARE_CLICKED]: {
			private: true
		}
	},
	dispatches: {
		/**
		 * Action dispatched when the upload button in the header is clicked
		 * @type {{}}
		 */
		[ATTACHMENT_UPLOAD_BUTTON_CLICKED]: {}
	},
	actionHandlers: {
		[NOW_BUTTON_ICONIC_CLICKED]: ({ dispatch }) => {
			dispatch(ATTACHMENT_UPLOAD_BUTTON_CLICKED);
		},
		[NOW_BUTTON_BARE_CLICKED]: ({ dispatch }) => {
			dispatch(ATTACHMENT_UPLOAD_BUTTON_CLICKED);
		}
	}
});
