import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-text-link";
import styles from "./HeaderLabelValueCommon.scss";
import { _addMissingRequiredProperty } from "./HeaderCommon.js";
import { dispatchPreviewRecord } from "@devsnc/library-form";
const NOW_TEXT_LINK_CLICKED = "NOW_TEXT_LINK#CLICKED";

export const acceptedProperties = {
	fieldLabel: {
		default: "",
		required: true
	},
	displayValue: {
		default: "",
		required: true
	},
	table: {
		default: "",
		required: true
	},
	sysId: {
		default: "",
		required: true
	},
	ddRef: { default: null },
	dtRef: { default: null }
};

// Create view for renderer
export const view = (state, { updateProperties }) => {
	if (state.properties.missingRequiredProperty) return;

	const {
		componentId,
		properties: { fieldLabel, displayValue, dtRef, ddRef }
	} = state;
	const labelId = `label-${componentId}`;
	return (
		<div className="sn-label-value-pair">
			<dt
				className="sn-label-value-label"
				role="listitem"
				id={labelId}
				hook={{
					insert: ({ elm }) => updateProperties({ dtRef: elm }),
					remove: () => updateProperties({ dtRef: null })
				}}
			>
				{fieldLabel}
			</dt>
			<dd
				className="sn-label-value-value"
				aria-labelledby={labelId}
				hook={{
					insert: ({ elm }) => updateProperties({ ddRef: elm }),
					remove: () => updateProperties({ ddRef: null })
				}}
			>
				<now-text-link
					label={displayValue}
					variant="primary"
					href="javascript:void(0)"
				/>
			</dd>
			<now-tooltip
				target-ref={dtRef}
				content={fieldLabel}
				position={["top-start", "top-end", "bottom-start", "bottom-end"]}
			/>
			<now-tooltip
				target-ref={ddRef}
				content={displayValue}
				position={["bottom-start", "bottom-end"]}
			/>
		</div>
	);
};

createCustomElement("sn-form-internal-header-label-reference-value", {
	properties: acceptedProperties,
	renderer: {
		type: snabbdom,
		transformProps(state) {
			return _addMissingRequiredProperty(state, acceptedProperties);
		}
	},
	actions: {
		[NOW_TEXT_LINK_CLICKED]: {
			private: true
		}
	},
	actionHandlers: {
		[NOW_TEXT_LINK_CLICKED]: coeffects => {
			const {
				dispatch,
				state: {
					properties: { table, sysId }
				}
			} = coeffects;
			dispatchPreviewRecord(dispatch, { table, sysId });
		}
	},
	view,
	styles
});
