import { createCustomElement } from "@servicenow/ui-core";
import "@servicenow/now-tooltip";
import snabbdom, {
	createRef,
	Fragment
} from "@servicenow/ui-renderer-snabbdom";
import styles from "./HeaderLabelValueCommon.scss";
import { _addMissingRequiredProperty } from "./HeaderCommon.js";

export const acceptedProperties = {
	fieldLabel: {
		default: "",
		required: true
	},
	displayValue: {
		default: "",
		required: true
	},
	ddRef: { default: null },
	dtRef: { default: null }
};

// Create view for renderer
export const view = (state, { updateProperties }) => {
	if (state.properties.missingRequiredProperty) return;
	const {
		componentId,
		properties: { fieldLabel, displayValue, dtRef, ddRef }
	} = state;
	const labelId = `label-${componentId}`;
	return (
		<div className="sn-label-value-pair">
			<dt
				className="sn-label-value-label"
				role="listitem"
				id={labelId}
				hook={{
					insert: ({ elm }) => updateProperties({ dtRef: elm }),
					remove: () => updateProperties({ dtRef: null })
				}}
			>
				{fieldLabel}
			</dt>
			<dd
				className="sn-label-value-value"
				hook={{
					insert: ({ elm }) => updateProperties({ ddRef: elm }),
					remove: () => updateProperties({ ddRef: null })
				}}
				aria-labelledby={labelId}
			>
				{displayValue}
			</dd>
			<now-tooltip
				target-ref={dtRef}
				content={fieldLabel}
				position={["top-start", "top-end", "bottom-start", "bottom-end"]}
			/>
			<now-tooltip
				target-ref={ddRef}
				content={displayValue}
				position={["bottom-start", "bottom-end"]}
			/>
		</div>
	);
};

createCustomElement("sn-form-internal-header-label-value", {
	properties: acceptedProperties,
	renderer: {
		type: snabbdom,
		transformProps(state) {
			return _addMissingRequiredProperty(state, acceptedProperties);
		}
	},
	view,
	styles
});
