import react from "@servicenow/ui-renderer-react";
import { createCustomElement } from "@servicenow/ui-core";
import { default as AttachmentCard } from "./AttachmentCard";
import attachmentActions from "./attachmentCardActions";
import style from "./AttachmentCard.scss";

/**
 * Tile Component used to display an attachment item.
 *
 * ```jsx
 * <now-record-common-attachment-card
 *		item={...}
 *		maxFileNameLength={...}
 *		canCreate={...}
 *		mode={...}
 * />
 * ```
 *
 * @seismicElement now-record-common-attachment-card
 * @summary Tile Component used to display an attachment
 */
export default createCustomElement("now-record-common-attachment-card", {
	renderer: {
		type: react,
		view: AttachmentCard
	},

	properties: {
		/**
		 * Flag that indicates whether the actions to modify the attachment (rename, delete) should be displayed
		 * @type {boolean}
		 */
		canCreate: {},
		/**
		 * Object containing details of the attachment record that are used to render the tile
		 *
		 * Shape
		 * `type: boolean` canDelete - flag that indicates whether a user can delete the attachment
		 * `type: boolean` canEditName - flag that indicates whether a user can rename the attachment
		 * `type: string` date - the date/time stamp for when the attachment was updated
		 * `type: string` dateLabel - the label corresponding to the date
		 * `type: string` name - the name of the attachment
		 * `type: string` size - the size of the attachment in bytes
		 * `type: string` state - the state of the attachment
		 * `type: string` sysId - the sys id of the attachment record
		 * `type: string` type - the attachment file type
		 * `type: string` userId - the sys id of the user
		 * `type: string` userName - the name of the user
		 * @type{object}
		 */
		item: {},
		/**
		 * The maximum length of the file name that can be specified for the attachment
		 * @type {integer}
		 */
		maxFileNameLength: {},
		/**
		 * The layout in which the component is rendered.
		 * The current supported layouts are "full" and "compact".
		 * @type {string}
		 */
		mode: { default: "full" },
		/**
		 * Whether to display upload progress bar.
		 * @type {boolean}
		 */
		displayUploadProgressBar: { default: false }
	},

	actions: {
		...attachmentActions.actions
	},

	actionHandlers: {
		...attachmentActions.handlers
	},
	styles: style
});
