import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import _ from "lodash";

const { createElement } = snabbdom;

export const view = state => {
	const {
		properties: {
			componentName,
			formModel,
			actionAttributes = {},
			tabContentExtras = {}
		}
	} = state;

	if (!componentName) return null;

	const modelAttribute = formModel || {};

	return createElement(componentName, {
		...formModel,
		...actionAttributes,
		...tabContentExtras,
		model: modelAttribute
	});
};

const RELATED_ITEM = "sn-form-internal-related-item";
createCustomElement(RELATED_ITEM, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		componentName: {},
		formModel: {
			default: {}
		},
		actionAttributes: {
			default: {}
		},
		tabContentExtras: {
			default: {}
		}
	}
});

export default RELATED_ITEM;
