import { createCustomElement } from "@servicenow/ui-core";
import react from "@servicenow/ui-renderer-react";
import "@servicenow/now-button";
import _ from "lodash";
import React from "react";
import { t } from "sn-translate";

import styles from "./AttachmentList.scss";
import "../attachmentCard/AttachmentCardElement";
import {
	ATTACHMENT_UPLOAD_BUTTON_CLICKED,
	NOW_BUTTON_CLICKED
} from "../constants/attachmentConstants.js";

/**
 * Render panel, which conists of a list of cards, or cards and a card with an input
 * @param properties
 * @returns {*}
 */
function renderPanel(properties) {
	const {
		list,
		maxFileNameLength,
		canCreate,
		mode,
		loadingList = []
	} = properties;
	const isCompact = mode === "compact" ? "-compact" : "";
	const modifierClassName = `${isCompact}`;

	return (
		<ul className={`sn-card-list ${modifierClassName}`}>
			{list.map((item, index) => {
				return (
					<li key={index} data-index={index} className="sn-card">
						<now-record-common-attachment-card
							item={item}
							maxFileNameLength={maxFileNameLength}
							canCreate={canCreate}
							mode={mode}
						/>
					</li>
				);
			})}
			{loadingList.map((item, index) => {
				return (
					<li
						key={list.length + index}
						data-index={list.length + index}
						className="sn-card"
					>
						<now-record-common-attachment-card
							item={item}
							displayUploadProgressBar={true}
						/>
					</li>
				);
			})}
		</ul>
	);
}

/**
 * Renders a no match found empty panel with the corresponding svg
 * @returns {*}
 */
function renderNotFoundState() {
	return (
		<div>
			<div className="sn-empty-state-row">
				<div className="sn-empty-state-img -no-match" />
			</div>
			<h1 className="sn-empty-state-row -header">{t("No Match Found")}</h1>
			<div className="sn-empty-state-row">
				{t("We could not find what you were looking for.")}
			</div>
		</div>
	);
}

/**
 * Renders a panel with an upload button and the No Attachments message.
 * @returns {*}
 */
function renderEmptyState(canCreate) {
	return (
		<React.Fragment>
			<div className="sn-empty-state-row">
				<div className="sn-empty-state-img -no-attachments" />
			</div>
			<div className="sn-empty-state-row -header">
				{t("No Attachments Available")}
			</div>
			{canCreate && (
				<React.Fragment>
					<div className="sn-empty-state-row">
						{t("Browse for a file to add it as an attachment.")}
					</div>
					<div className="sn-empty-state-row">
						<now-button
							size="md"
							variant="primary"
							label={t("Browse")}
							configAria={{ "aria-label": t("Browse attachments") }}
						/>
					</div>
				</React.Fragment>
			)}
		</React.Fragment>
	);
}

function renderEmptyPanel(properties) {
	return properties.mode === "compact" ? (
		<div className="sn-compact-empty-state" />
	) : (
		<div className="sn-empty-state">
			{properties.attachmentsNotFound
				? renderNotFoundState()
				: renderEmptyState(properties.canCreate)}
		</div>
	);
}

function view(state) {
	const { properties } = state;
	const { mode } = properties;
	const isCompact = mode === "compact" ? "-compact" : "";
	const panelBodyClass = `sn-panel-body ${isCompact}`;

	return (
		<div className={panelBodyClass}>
			{properties.list.length === 0 && properties.loadingList.length === 0
				? renderEmptyPanel(properties)
				: renderPanel(properties)}
		</div>
	);
}

/**
 * The body of the attachment component. It displays the list of attachments passed in as tiles.
 *
 * ```jsx
 * <sn-form-internal-attachments-list
 *		list={...}
 *		maxFileNameLength={...}
 *		canCreate={...}
 *		attachmentsNotFound={...}
 *		mode={...}
 * />
 * ```
 *
 * @seismicElement sn-form-internal-attachments-list
 * @summary Presentational view of attachments
 */
createCustomElement("sn-form-internal-attachments-list", {
	renderer: {
		type: react,
		view
	},
	styles,
	properties: {
		/**
		 * Flag that indicates whether any attachments were found.
		 * If true, the not found state of the component is rendered when there are no attachments.
		 * @type {boolean}
		 */
		attachmentsNotFound: {},
		/**
		 * Flag that indicates whether a user can upload attachments.
		 * If false, the upload button is not displayed when the list of attachments is empty
		 * @type {boolean}
		 */
		canCreate: {},
		/**
		 * The list of attachments to be displayed.
		 * Each entry is an object that is used to render the attachment card.
		 * See <now-record-common-attachment-card> for details.
		 * @type {Array<Object>}
		 */
		list: {
			default: []
		},
		/**
		 * The list of attachments that are currently being uploadede.
		 * Each entry is an object that is used to render the attachment card.
		 * See <now-record-common-attachment-card> for details.
		 * @type {Array<Object>}
		 */
		loadingList: {
			default: []
		},
		/**
		 * The maximum length of the file name that can be specified for the attachments
		 * @type {integer}
		 */
		maxFileNameLength: {},
		/**
		 * The layout in which the component is rendered.
		 * The current supported layouts are "full" and "compact".
		 * @type {string}
		 */
		mode: { default: "full" }
	},
	actions: {
		[NOW_BUTTON_CLICKED]: {
			private: true
		}
	},
	dispatches: {
		/**
		 * Action dispatched when the upload button in the body is clicked
		 * @type {{}}
		 */
		[ATTACHMENT_UPLOAD_BUTTON_CLICKED]: {}
	},
	actionHandlers: {
		[NOW_BUTTON_CLICKED]: ({ action, dispatch }) => {
			dispatch(ATTACHMENT_UPLOAD_BUTTON_CLICKED);
		}
	}
});
