import { createDeclarativeFormActionDataSource } from "../dataSource/sidebarDataSource";
import { createDataProvider } from "@devsnc/library-form";

const SIDEBAR_DATA_PROVIDER = "sidebar-data-provider";

export const createSidebarDataProvider = () =>
	createDataProvider({
		name: SIDEBAR_DATA_PROVIDER,
		dataSources: [createDeclarativeFormActionDataSource()]
	});
