import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-label-value";
import "@servicenow/now-highlighted-value";
import "@servicenow/now-text-link";
import styles from "./HeaderLabelValueCommon.scss";
import "./HeaderLabelValue.js";
import "./HeaderLabelHighlightedValue.js";
import "./HeaderLabelReferenceValue.js";
import _ from "lodash";
import { ITEM_TYPES } from "./headerConst";

const renderSimpleItem = item => {
	return (
		<sn-form-internal-header-label-value
			fieldLabel={item.fieldLabel}
			displayValue={item.displayValue}
		/>
	);
};

const renderHighlightedItem = item => {
	return (
		<sn-form-internal-header-label-highlighted-value
			fieldLabel={item.fieldLabel}
			displayValue={item.displayValue}
			status={item.status}
			showIcon={item.showIcon}
		/>
	);
};

const renderReferenceItem = item => {
	return (
		<sn-form-internal-header-label-reference-value
			fieldLabel={item.fieldLabel}
			displayValue={item.displayValue}
			table={item.table}
			sysId={item.sysId}
		/>
	);
};

export const view = state => {
	let items = _.get(state, "properties.items", []);
	if (items.length === 0) return;

	items = items.map(item => {
		// Default type
		item.type = item.type || ITEM_TYPES.SIMPLE;

		// Simple label/value
		if (item.type === ITEM_TYPES.SIMPLE) return renderSimpleItem(item);

		// Highlighted
		if (item.type === ITEM_TYPES.HIGHLIGHTED)
			return renderHighlightedItem(item);

		// Reference
		if (item.type === ITEM_TYPES.REFERENCE) return renderReferenceItem(item);
	});

	return <dl className="sn-label-value-set">{items}</dl>;
};

createCustomElement("sn-form-internal-header-label-value-set", {
	properties: {
		items: {}
	},
	renderer: {
		type: snabbdom,
		view
	},
	styles
});
