import { createCustomElement, actionTypes } from "@servicenow/ui-core";
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { SIDEBAR, SIDEBAR_ACTION } from "../constants/constants";
import "../toolbar/sidebarToolbar";
import "../panel/sidebarPanel";
import styles from "./sidebar.scss";
import _ from "lodash";
import {
	sidebarTransformer,
	toggleSelectedOrDefaultAction
} from "./sidebarTransformer";
import { loadActionComponents } from "@devsnc/library-form";
import { EmptyStateElement } from "../../emptyState";
import { t } from "sn-translate";

const renderPanels = state => {
	const {
		properties: {
			formModel,
			classicForm,
			recordDisplayValue,
			sidebarPanelExtras
		},
		transformedActions: actions,
		selectedAction,
		componentId,
		renderedPanels
	} = state;
	return actions.map(action =>
		renderedPanels[action.name] || selectedAction === action.name ? (
			<sn-form-internal-sidebar-panel
				className={selectedAction === action.name ? "" : "is-hidden"}
				component-name={action.actionComponent}
				form-model={formModel}
				action-attributes={action.actionAttributes}
				component-id={`${componentId}_panel_${action.assignmentId}`}
				classicForm={classicForm}
				record-display-value={recordDisplayValue}
				sidebar-panel-extras={sidebarPanelExtras[action.actionComponent] || {}}
			/>
		) : null
	);
};

const shouldHidePanel = state => {
	const {
		selectedAction,
		properties: { manageOpened, opened }
	} = state;
	return manageOpened ? !opened : !selectedAction;
};

const view = state => {
	const {
		transformedActions: actions,
		selectedAction,
		openedMore,
		componentId,
		properties: { classicForm }
	} = state;

	if (!actions || _.isEmpty(actions))
		return !classicForm ? (
			<EmptyStateElement message={t("No data to display")} />
		) : null;

	return (
		<div className="sidebar">
			<sn-form-internal-sidebar-toolbar
				actions={actions}
				opened-more={openedMore}
				selected-action={selectedAction}
				component-id={`${componentId}_toolbar`}
			/>
			<div
				className={`sidebar-panel ${shouldHidePanel(state) ? "is-hidden" : ""}`}
			>
				{renderPanels(state)}
			</div>
		</div>
	);
};

const loadComponents = state => {
	const { transformedActions = [] } = state;
	loadActionComponents(transformedActions);
};

const SIDEBAR_COMPONENT = "now-record-common-sidebar";

/**
 * Vertical panel of buttons which loads components in another slide out panel when clicked
 *
 * ```jsx
 * <now-record-common-sidebar
 *		formModal={...}
 *		actions={...}
 *		opened={...}
 *		manageOpened={...}
 *		sidebarPanelExtras={...}
 *		/>
 * ```
 *
 * @seismicElement now-record-common-sidebar
 * @summary Vertical panel of buttons that when clicked toggles open/closed a larger panel
 * with a component defined by a declarative action
 */
createCustomElement(SIDEBAR_COMPONENT, {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
		 * this is an object which contains the data needed for all of the components represented by all of the actions to load
		 * @type {SidebarFormModel}
		 */
		formModel: {
			default: {},
			unstableParse: true
		},
		/**
		 * array of SidebarFormActions representing the actions to show in the sidepanel
		 * @type {Array.<SidebarFormAction>}
		 */
		actions: {
			unstableParse: true
		},
		/**
		 * whether the component panel should be opened or closed
		 * @type {boolean}
		 */
		opened: {
			default: false,
			unstableParse: true
		},
		/**
		 * object whose properties will be spread and added to the components being rendered as additional attributes
		 * @type {SidebarPanelExtra}
		 */
		sidebarPanelExtras: {
			default: {}
		},
		/**
		 * whether the consumer of this component should manage the opened state manually
		 * @type {boolean}
		 */
		manageOpened: {
			default: false
		},
		/**
		 * whether or not this is rendered in the workspace Form
		 * @type {boolean}
		 */
		classicForm: {},
		/**
		 * current record's display value
		 * @type {string}
		 */
		recordDisplayValue: {}
	},
	initialState: {
		renderedPanels: {}
	},
	actions: {
		[SIDEBAR.INTERNAL_ACTIONS.ACTION_CLICKED]: {
			private: true
		},
		[SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED]: {
			private: true
		},
		[SIDEBAR_ACTION.ACTION_STATUS_UPDATED]: {
			private: true
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state, properties, updateState }) => {
			const transformedState = sidebarTransformer(state, properties);
			loadComponents(transformedState);
			updateState(transformedState);
		},
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				action: {
					payload: { name, value, previousValue }
				},
				updateState,
				state,
				properties
			} = context;
			if (value === previousValue) return;
			switch (name) {
				case "formModal":
				case "actions":
					const transformedState = sidebarTransformer(state, properties);
					loadComponents(transformedState);
					updateState(transformedState);
					return;
				case "opened":
					updateState(toggleSelectedOrDefaultAction(state, properties));
					return;
			}
		},
		[SIDEBAR.INTERNAL_ACTIONS.ACTION_CLICKED]: ({
			state,
			properties,
			action,
			updateState,
			dispatch
		}) => {
			const {
				payload: { name, openedMore }
			} = action;
			const { manageOpened, opened } = properties;

			if ((manageOpened && state.selectedAction === name) || !opened) {
				const newOpened = state.selectedAction === name ? !opened : true;
				dispatch(SIDEBAR.ACTIONS.OPENED_SET, { value: newOpened });
			} else if (manageOpened && state.selectedAction === "") {
				dispatch(SIDEBAR.ACTIONS.OPENED_SET, { value: true });
			}
			updateState({
				selectedAction: state.selectedAction === name ? "" : name,
				renderedPanels:
					state.selectedAction === name
						? state.renderedPanels
						: {
							...(state.renderedPanels || {}),
							[name]: true
						},
				openedMore: !!openedMore
			});
		},
		[SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED]: ({
			updateState,
			action: {
				payload: { opened }
			}
		}) => {
			updateState({
				openedMore: opened
			});
		},
		[SIDEBAR_ACTION.ACTION_STATUS_UPDATED]: {
			effect: ({
				updateState,
				state: { transformedActions = [] },
				action: {
					payload: { component, type, value }
				}
			}) => {
				if (type === "badge") {
					const actionIndex = transformedActions.findIndex(
						action => action.actionComponent === component
					);
					if (actionIndex > -1) {
						updateState({
							path: `transformedActions[${actionIndex}]`,
							value: { badge: value },
							operation: "merge"
						});
					}
				}
			},
			stopPropagation: true
		}
	},
	dispatches: {
		/**
		 * Dispatched when the sidebar panel opened state is changed based on clicks from the user
		 * if manageOpened is true
		 *
		 * @type {SidbarOpenedSetPayload}
		 */
		"NOW_RECORD_COMMON_SIDEBAR#OPENED_SET": {}
	}
});

export default SIDEBAR_COMPONENT;
