import {
	createDataProviderDataSource,
	getValue
} from "@devsnc/library-form";
import { CONTEXTUAL_LINK_NAMES } from "../../../constants/contextualLinks";
import { UI_ACTION_TYPES, UX_ACTION_TYPES } from "../UIActionBarConst";

export const UI_ACTION_QUERY_KEY = "GlideUIAction_Query";
export const UI_ACTIONS_KEY = "uiActions";
export const UI_ACTION_FORM_ACTIONS = "formActions";
export const UI_ACTION_MESSAGES = "messages";
export const ACTION_NODES = "actionNodes";
export const FIXED_BUTTON_NUMBER = "fixedButtonNumber";

export const QUERY_FRAGMENT = `
${UI_ACTION_QUERY_KEY} {
    ${UI_ACTIONS_KEY}(table:$table, sysId:$sysId, query: $query, view: $view, workspaceConfigId: $workspaceConfigId) {
    	${UI_ACTION_MESSAGES} {
			name
			value
		}
		${FIXED_BUTTON_NUMBER}
		${ACTION_NODES} {
			... on GlideUIAction_UIAction {
				label
				name
				sysId
				order
				hasClientScript
				clientScript
				type
				style
				hint
			}
			... on GlideUIAction_UIActionGroupLayout {
				label
				overflow
				type
				style
				order
				groupActions {
					label
					name
					sysId
					order
					hasClientScript
					clientScript
					type
					style
					hint
				}
			}
		}
    }
}`;

const transformMessages = (messages = []) =>
	messages.reduce(
		(loadedMessages, message) =>
			message.name
				? {
						...loadedMessages,
						[message.name]: message.value
				  }
				: loadedMessages,
		{}
	);

const transformAction = action => ({
	id: action.sysId,
	label: action.label || action.title,
	hint: action.hint
});

const transformActions = actions => {
	return actions.map(action => transformAction(action));
};

const getActionsFromNodes = nodes => {
	const existingKeys = [];
	const transformNode = node => ({
		...node,
		id: node.sysId,
		uxActionType: UX_ACTION_TYPES.UI_ACTION
	});

	return nodes.reduce((arr, node) => {
		if (!node.groupActions) {
			if (!(node.sysId in existingKeys)) {
				existingKeys[node.sysId] = null;
				arr.push(transformNode(node));
			}
		} else {
			for (const action of node.groupActions) {
				if (!(action.sysId in existingKeys)) {
					existingKeys[action.sysId] = null;
					arr.push(transformNode(action));
				}
			}
		}
		return arr;
	}, []);
};

const transformActionNodes = nodes => {
	let nodeId = 1;
	return nodes.map(node => {
		const hasChildren = node.groupActions;
		const hasMultipleChildren = hasChildren && node.groupActions.length > 1;
		return {
			id: nodeId++,
			label: node.type === UI_ACTION_TYPES.MENU ? node.label : "",
			overflow: "overflow" in node ? node.overflow : node.type === "menuItem",
			color: node.style,
			type: hasMultipleChildren ? node.type : UI_ACTION_TYPES.BUTTON,
			children: hasChildren
				? transformActions(node.groupActions)
				: [transformAction(node)]
		};
	});
};

const createTransform = getValueOrDefault => (data, state, properties) => {
	const {
		isFormReadonly,
		properties: { table }
	} = state;
	const { record: { trueTable = table } = {} } = state;

	const actionNodeData = getValueOrDefault(ACTION_NODES, [])(data);
	const actionNodes = isFormReadonly
		? []
		: transformActionNodes(actionNodeData);

	const formActions =
		!actionNodes || isFormReadonly ? [] : getActionsFromNodes(actionNodeData);

	const encodedQuery = encodeURIComponent(
		`table=${trueTable}^ORtable=global^form_button_v2=true^ORform_menu_button_v2=true^active=true`
	);
	const configurationItem = {
		order: CONTEXTUAL_LINK_NAMES.uiActions.order,
		label: CONTEXTUAL_LINK_NAMES.uiActions.name,
		url: encodeURIComponent(
			`sys_ui_action_list.do?sysparm_query=${encodedQuery}`
		)
	};

	const messages = transformMessages(
		getValueOrDefault(UI_ACTION_MESSAGES, [])(data)
	);

	const fixedButtonNumber = getValueOrDefault(FIXED_BUTTON_NUMBER, 0)(data);
	return {
		formActions,
		actionNodes,
		configurationItem,
		messages,
		fixedButtonNumber
	};
};

export const createUIActionDataSource = (queryPrefix = "") => {
	const baseKey = `${queryPrefix}${UI_ACTION_QUERY_KEY}.${UI_ACTIONS_KEY}`;
	const getValueOrDefault = getValue(baseKey);
	const transform = createTransform(getValueOrDefault);

	return createDataProviderDataSource({
		query: QUERY_FRAGMENT,
		variables: ["table", "view", "sysId", "query", "workspaceConfigId"],
		transform,
		props: {
			table: { default: "" },
			view: { default: "" },
			sysId: { default: "" },
			query: { default: "" },
			workspaceConfigId: { default: "" }
		},
		selectableProps: {
			formActions: {
				default: []
			},
			configurationItem: {},
			messages: {
				default: []
			},
			actionNodes: {
				default: []
			},
			daClientActionContract: {
				default: {}
			}
		}
	});
};
