import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import {
	ACTIONS,
	LABELS,
	COMPONENT_EDIT_TAG_MODAL,
	COMPONENT_TAG_LIST
} from "../constants";
import "@servicenow/now-button";
import "@servicenow/now-popover";
import tagActions from "./tagActions";
import "../tagList/tagList";
import "../editTagModal/editTagModal";
import { EmptyStateElement } from "../../emptyState";
import { t } from "sn-translate";

const getTagIcon = (tags = []) => {
	switch (tags.length) {
		case 0:
			return "tag-outline";
		case 1:
			return "tag-fill";
		default:
			return "tags-fill";
	}
};

const closeTagListOnBlur = (state, dispatch) => () => {
	if (!state.tagIconJustClicked) {
		dispatch(ACTIONS.TAG_ICON_CLICKED, { eventSource: ACTIONS.EVENT_SOURCE });
	}
};

const onTagIconMouseDown = dispatch => () => {
	dispatch(ACTIONS.SET_TAG_STATE, { tagIconJustClicked: true });
};

const renderTagList = (state, dispatch) => {
	const {
		tags = [],
		properties: { table, sysId, displayValue },
		opened
	} = state;
	return (
		<COMPONENT_TAG_LIST
			slot="content"
			taglist={tags}
			table={table}
			sysId={sysId}
			tabindex={opened ? "0" : "-1"}
			displayValue={displayValue}
			on-blur={closeTagListOnBlur(state, dispatch)}
		/>
	);
};

const view = (state, { dispatch }) => {
	const {
		tagEnabled,
		tags = [],
		properties: { table, sysId, classicForm, headerSubComponent },
		editMode,
		editTagId,
		opened,
		hoistOverlayToContainer
	} = state;

	if (classicForm && !tagEnabled) return null;
	else if (!headerSubComponent && (!table || !sysId))
		return <EmptyStateElement message={t("No data to display")} />;

	const tagIcon = getTagIcon(tags);
	const tooltip = tags.length > 1 ? LABELS.TAGS : LABELS.TAG;
	const ariaLabel = `${LABELS.TAG_ICON_ARIA_LABEL} - ${tags.length} ${tooltip}`;
	const editTagModalProps = {
		opened: editMode,
		tabindex: editMode ? "0" : "-1",
		tagId: editTagId
	};

	return (
		<div className="sn-form-header-tag">
			<now-popover
				hide-tail={true}
				append-to-payload={{
					eventSource: ACTIONS.EVENT_SOURCE
				}}
				opened={opened}
			>
				<now-button
					type="tertiary"
					size="md"
					bare
					componentName="tag-button"
					icon-name={tagIcon}
					tooltipContent={tooltip}
					config-aria={{ "aria-label": ariaLabel }}
					slot="trigger"
					on-mousedown={onTagIconMouseDown(dispatch)}
				/>
				{renderTagList(state, dispatch)}
			</now-popover>
			{opened || editMode ? (
				hoistOverlayToContainer ?
					<COMPONENT_EDIT_TAG_MODAL nowHoist nowHoistPrependTo={hoistOverlayToContainer} {...editTagModalProps} /> :
					<COMPONENT_EDIT_TAG_MODAL {...editTagModalProps} />
			) : null}
		</div>
	);
};

/**
 * Lets users add text label tags to records in order to group and organize them.
 * Users can add, delete, and modify tags, or set permissions to restrict who can see them.
 * ```jsx
 * <sn-record-tags-connected
 *		table={...}
 *		sysId={...}
 *		opened={...}
 *		tagId={...}
 *		recordTags={...}
 *		displayValue={...}
 * />
 * ```
 *
 * @seismicElement sn-record-tags-connected
 * @summary Text label added to help organize records.  
 *
 */
export default createCustomElement("sn-record-tags-connected", {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		canCreateGlobalTags: true,
		tagEnabled: false,
		tags: [],
		editTagId: "",
		editMode: false,
		tagIconJustClicked: false,
		opened: false,
		getTagsFromProps: true
	},
	properties: {
		/**
		 * Table of the record to tag.
		 * @type {string}
		 */
		table: { default: "" },
		/**
		 * Sys ID of the record to tag.
		 * @type {string}
		 */
		sysId: { default: "" },
		/**
		 * Tags to load into the component.
		 * @type {string[]}
		 */
		recordTags: {},
		/**
		 * Record display value used to create the tag title
		 * @type {string}
		 * @private
		 */
		displayValue: { default: "" },
		/**
		 * When true, the tag list is opened.
		 * @type {boolean}
		 */
		opened: {
			schema: {
				type: "boolean"
			},
			default: false
		},
		/**
		 * When true, the component is rendered in a workspace form.
		 * @type {boolean}
		 * @private
		 */
		classicForm: {},
		/**
		 * When true, the component is rendered inside the form header.
		 * @type {boolean}
		 * @private
		 */
		headerSubComponent: {},
		/**
		 * String of the selector for the container.
		 * @type {string}
		 */
		overlayContainer: {
			schema: {
				type: "string"
			},
			default: ""
		}
	},
	...tagActions
});
