import { createCustomElement, actionTypes } from "@servicenow/ui-core";
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import _ from "lodash";
import "./sidebarAction";
import resizeBehavior from "@servicenow/behavior-resize";
import {
	createSidebarKeyBindingBehavior,
	createSidebarKeyBindingActions,
	createSidebarKeyBindingActionsHandlers
} from "./sidebarKeyBinding";
import { SIDEBAR } from "../constants/constants";
import "@servicenow/now-button";
import "../menu/sidebarMenu";
import styles from "./sidebarToolbar.scss";
import "@servicenow/now-legacy-icon";
import { focusActionByName } from "./sidebarKeyBinding";

const BUTTON_HEIGHT = 40;
const SIDEBAR_TOOLBAR_ACTIONS_THRESHOLD = 3;
const isSelectMore = (moreActions, selectedAction) =>
	moreActions.findIndex(a => a.name === selectedAction) > -1;

const transformState = (state, actions, updateState) => {
	const {
		behaviors: {
			resize: { size }
		}
	} = state;
	const { height } = size;
	if (!height) return;

	// fitHeight: height that will fit the number of buttons without the empty space so we'll use that to set the height of the internal div that hold actions.
	// maxActions: maximum number of action buttons that can fit in the fitHeight.
	// If the maxActions is less than the number of actions we need to show, those don't fit in the space will be push to the overflow, and the overflow button will be shown.
	const fitHeight = height - (height % BUTTON_HEIGHT);
	const maxActions = Math.max(
		fitHeight ? fitHeight / BUTTON_HEIGHT : actions.length,
		SIDEBAR_TOOLBAR_ACTIONS_THRESHOLD
	); //always show minimum of SIDEBAR_TOOLBAR_ACTIONS_THRESHOLD buttons
	const hasMore = actions.length > maxActions;
	const visibleActions = hasMore ? actions.slice(0, maxActions - 1) : actions;
	const moreActions = hasMore ? actions.slice(maxActions - 1) : [];

	updateState({
		hasMore,
		visibleActions,
		moreActions,
		fitHeight,
		height
	});
};

const focusSelectedAction = (state, dispatch, target) => {
	const {
		properties: { selectedAction, openedMore, actions },
		visibleActions = actions,
		hasMore = false
	} = state;
	const index = actions.findIndex(a => a.name === selectedAction);
	const newIndex = index === -1 ? 0 : index;
	const shouldOpenMore = newIndex > visibleActions.length - 1;

	if (hasMore && shouldOpenMore && !openedMore) {
		const activeElement = _.get(target, "shadowRoot.activeElement");
		if (activeElement) activeElement.blur();
		dispatch(SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED, { opened: true });
	} else focusActionByName(target, actions[newIndex].name, shouldOpenMore);
};

const view = (state, { dispatch, updateState }) => {
	const {
		properties: { actions = [], selectedAction, openedMore },
		hasMore,
		visibleActions = actions,
		moreActions = [],
		fitHeight,
		height,
		behaviors: { resize = {} },
		componentId,
		targetRef
	} = state;

	if (
		resize.size.height !== height &&
		actions.length > SIDEBAR_TOOLBAR_ACTIONS_THRESHOLD
	) {
		transformState(state, actions, updateState);
	}
	const inlineStyle = fitHeight
		? {
				height: `${fitHeight}px;`
		  }
		: {};
	const menuStyle = fitHeight
		? {
				bottom: `${height - fitHeight}px;`
		  }
		: {};

	const moreButtonId = `${componentId}_moreButton`;
	const isSelectedInMenu =
		hasMore && selectedAction
			? isSelectMore(moreActions, selectedAction)
			: false;
	const moreButtonClass = `sidebar-action-button ${
		SIDEBAR.CLASSES.MORE_BUTTON
	} ${isSelectedInMenu ? "is-selected" : ""}`;

	const isFirstOrLastAction = index =>
		index === 0 || index === visibleActions.length - 1;
	let focusFromClick = false;

	return (
		<div className="sidebar-action-wrapper" style={inlineStyle}>
			<div className={SIDEBAR.CLASSES.TOOLBAR_CONTAINER} tabIndex="-1">
				{visibleActions.map((action, index) => (
					<sn-form-internal-sidebar-action
						{...action}
						selected={selectedAction === action.name}
						index={index}
						on-mousedown={() => {
							if (isFirstOrLastAction(index)) focusFromClick = true;
						}}
						on-focus={e => {
							if (isFirstOrLastAction(index) && !focusFromClick) {
								const target = e
									.composedPath()
									.find(
										x =>
											x.tagName &&
											x.tagName.toLowerCase() === SIDEBAR.ELEMENTS.TOOLBAR
									);
								focusSelectedAction(state, dispatch, target);
							}
						}}
						on-mouseup={() => {
							if (isFirstOrLastAction(index)) focusFromClick = false;
						}}
						component-id={`${componentId}_action_${action.assignmentId}`}
					/>
				))}
			</div>
			{hasMore ? (
				<div>
					<button
						className={moreButtonClass}
						on-mousedown={e => {
							e.stopPropagation();
							dispatch(SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED, {
								opened: !openedMore
							});
						}}
						on-keydown={event => {
							if (event.code === "Space" || event.code === "Enter") {
								event.preventDefault();
								dispatch(SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED, {
									opened: !openedMore
								});
							}
						}}
						hidden={!hasMore}
						tabindex="-1"
						hook={{
							insert: ({ elm }) =>
								elm ? setTimeout(() => updateState({ targetRef: elm })) : null
						}}
						aria-label={SIDEBAR.MORE_ACTION.tooltip}
						id={moreButtonId}
					>
						{" "}
						<now-icon icon={SIDEBAR.MORE_ACTION.icon} size="md" />
					</button>
					<now-tooltip
						target-ref={targetRef}
						content={SIDEBAR.MORE_ACTION.tooltip}
					/>
				</div>
			) : null}
			{openedMore ? (
				<sn-form-internal-sidebar-menu
					on-focusout={e => {
						e.stopPropagation();
						dispatch(SIDEBAR.INTERNAL_ACTIONS.MORE_CLICKED, {
							opened: false
						});
					}}
					style={menuStyle}
					actions={moreActions}
					selected-action={selectedAction}
					component-id={`${componentId}_menu`}
				/>
			) : null}
		</div>
	);
};

createCustomElement(SIDEBAR.ELEMENTS.TOOLBAR, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		actions: {
			default: []
		},
		selectedAction: {
			default: ""
		},
		openedMore: {
			default: false
		}
	},
	behaviors: [createSidebarKeyBindingBehavior(), { behavior: resizeBehavior }],
	actions: {
		...createSidebarKeyBindingActions()
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				action: {
					payload: { name, value, previousValue }
				},
				state,
				updateState
			} = context;
			if (name && name === "actions" && !_.isEqual(value, previousValue)) {
				transformState(state, value, updateState);
			}
		},
		...createSidebarKeyBindingActionsHandlers()
	},
	styles
});
