import { USERS, GROUPS, EDIT_TAG_MODAL } from "./constants";
import { VIEWABLE_BY } from "../constants";
import { t } from "sn-translate";
import _ from "lodash";

let cache = { [USERS]: {}, [GROUPS]: {} };

const isGroupAndUsers = viewable_by =>
	viewable_by === VIEWABLE_BY.GROUPS_AND_USERS.value;
const getImageUrl = avatar => avatar && "/" + avatar + ".iix?t=small";

const createField = (name, label, value, displayValue) => ({
	fieldName: name,
	label,
	value,
	displayValue
});

export default class TagDetails {
	constructor(state) {
		this.tagDetails = state.tagDetails;
		this.userListDetails = state[USERS];
		this.groupListDetails = state[GROUPS];
	}

	set avatars(avatars) {
		this.tagDetails.user_list.avatars = this.formatAvatars(avatars);
	}

	get value() {
		const {
			sys_id: { value: sys_id },
			name: { value: name },
			user_list: { value: user_list },
			group_list: { value: group_list },
			viewable_by: { value: viewable_by }
		} = this.tagDetails;

		return isGroupAndUsers(viewable_by)
			? { sys_id, name, viewable_by, user_list, group_list }
			: { sys_id, name, viewable_by };
	}

	get canEdit() {
		return this.tagDetails.name && this.tagDetails.name.canWrite;
	}

	get sysId() {
		return this.tagDetails.sys_id;
	}

	get name() {
		return createField(
			"name",
			EDIT_TAG_MODAL.LABELS.NAME,
			this.tagDetails.name.value,
			this.tagDetails.name.displayValue
		);
	}

	get viewableBy() {
		return createField(
			"viewable_by",
			EDIT_TAG_MODAL.LABELS.VIEWABLE_BY,
			this.tagDetails.viewable_by.value,
			this.tagDetails.viewable_by.displayValue
		);
	}

	get groupList() {
		return new ListField(
			"group_list",
			EDIT_TAG_MODAL.LABELS.GROUPS,
			this.groupListDetails,
			this.formatSelectedItems(this.tagDetails.group_list),
			cache[GROUPS]
		);
	}

	get userList() {
		return new ListField(
			"user_list",
			EDIT_TAG_MODAL.LABELS.USERS,
			this.userListDetails,
			this.formatSelectedItems(this.tagDetails.user_list),
			cache[USERS]
		);
	}

	get properties() {
		return this.tagDetails;
	}

	getList(fieldName) {
		if (fieldName === "user_list") return this.userList;
		else if (fieldName === "group_list") return this.groupList;
	}

	get isViewableBySetToUsersAndGroups() {
		return VIEWABLE_BY.GROUPS_AND_USERS.value === this.viewableBy.value;
	}

	formatSelectedItems(data) {
		const values = data.value ? data.value.split(",") : [];
		const displayValues = data.displayValue ? data.displayValue.split(",") : [];
		const avatars = data.avatars ? data.avatars.split(",") : [];
		return values.map((value, index) => ({
			value,
			displayValue: displayValues[index],
			avatar: avatars[index],
			icon: getImageUrl(avatars[index])
		}));
	}

	formatAvatars(avatars) {
		if (!this.userList.value) return "";
		const groupedAvatars = avatars.reduce((accumulator, avatar) => {
			accumulator[avatar.sys_id.value] = avatar;
			return accumulator;
		}, {});
		return this.userList.value
			.split(",")
			.map(userId =>
				groupedAvatars[userId].avatar ? groupedAvatars[userId].avatar.value : ""
			)
			.join(",");
	}
}

class ListField {
	constructor(fieldName, label, props, selected, cache) {
		this.props = props;
		this.fieldName = fieldName;
		this.label = label;
		this.selected = selected;
		this.cache = cache;
	}

	get dropdownLabel() {
		return t(
			"Showing {0} - {1} of {2}",
			1,
			this.nonSelected.length,
			this.total
		);
	}

	get typeaheadLabel() {
		return t("{0} ({1})", this.label, this.selected.length);
	}

	get dropdownOpened() {
		return this.props.dropdown;
	}

	get filter() {
		return this.props.filter;
	}

	get available() {
		return this.props.available;
	}

	get nonSelected() {
		return _.differenceBy(this.available, this.selected, "value");
	}

	get total() {
		return this.props.total - (this.available.length - this.nonSelected.length);
	}

	get value() {
		return this.selected.map(i => i.value).join(",");
	}

	get displayValue() {
		return this.selected.map(i => i.displayValue).join(",");
	}

	get avatars() {
		return this.listKey === USERS
			? this.selected.map(i => i.avatar).join(",")
			: "";
	}

	get listKey() {
		if (this.fieldName === "user_list") return USERS;
		else if (this.fieldName === "group_list") return GROUPS;
	}

	// If user types ab and we already have results from a (where totalResults is >= results.length),
	// then we can prepare results of "ab" from results of "a" when results.length equals totalResults
	prepareItemsFromCache(key) {
		const lowerKey = key.toLowerCase();
		let x = key;
		while (x !== "") {
			x = x.slice(0, -1);
			if (
				this.cache[x] &&
				this.cache[x].results.length == this.cache[x].totalResults
			) {
				const results = this.cache[x].results.filter(
					i => i.displayValue.toLowerCase().lastIndexOf(lowerKey, 0) === 0
				);
				return { results, totalResults: results.length };
			}
		}
	}

	loadAvailableItemsFromCache(key) {
		const cachedItems = this.cached(key);
		if (cachedItems) {
			this.loadAvailable(cachedItems);
			return true;
		}
		return false;
	}

	set filter(filter) {
		this.props.filter = filter;
	}

	set dropdown(dropdown) {
		this.props.dropdown = dropdown;
	}

	updateSelectedItems(items) {
		this.selected = items;
	}

	updateAvailableItems(key, { results: raw, totalResults }) {
		const results = this.formatLoadedItems(raw);
		this.cache[key] = { results, totalResults };
		this.loadAvailable({ results, totalResults });
	}

	loadAvailable({ results: available, totalResults: total }) {
		this.props = { ...this.props, available, total };
		this.props.dropdown = this.nonSelected.length !== 0;
	}

	get properties() {
		return this.props;
	}

	formatLoadedItems(items) {
		return items.map(item => ({
			value: item.sys_id.value,
			displayValue: item.name.value,
			secondaryDisplayValue: item.email && item.email.value,
			avatar: item.avatar && item.avatar.value,
			icon: item.avatar && getImageUrl(item.avatar.value)
		}));
	}

	cached(key) {
		return this.cache[key] || this.prepareItemsFromCache(key);
	}
}
