import React from "react";
import react from "@servicenow/ui-renderer-react";
import { t } from "sn-translate";
import _ from "lodash";
import "../attachments/AttachmentHeader";
import "../attachments/AttachmentList";
import "@servicenow/now-modal";
import {
	ATTACHMENT_ACTIONS,
	ATTACHMENT_DELETE_CONFIRM_ACTION,
	ATTACHMENT_MODAL_CLOSED_ACTION,
	REMOVE_MODAL_EVENT_SOURCE,
	INVALID_TYPE_MODAL_EVENT_SOURCE
} from "../constants/attachmentConstants";
import { getActiveElement } from "../../utils/domUtils";
import "./overlay/attachmentOverlay";
import { getReadOnlyPreviewOverlayProps } from "./overlay/preview/attachmentPreviewActions";

const MODAL_PROPS = {
	invalid_type: () => ({
		headerLabel: t("Invalid Attachment Type"),
		content: t("This file type is not supported"),
		footerActions: [
			{
				label: t("Okay"),
				clickActionType: ATTACHMENT_MODAL_CLOSED_ACTION
			}
		],
		appendToPayload: {
			eventSource: INVALID_TYPE_MODAL_EVENT_SOURCE
		}
	}),
	delete_confirmation: (data = {}) => ({
		headerLabel: t("Delete"),
		content: t(
			"Are you sure you want to delete this file?  Changes cannot be undone."
		),
		footerActions: [
			{
				label: t("Cancel"),
				clickActionType: ATTACHMENT_MODAL_CLOSED_ACTION
			},
			{
				label: t("Delete"),
				variant: "primary-negative",
				data,
				clickActionType: ATTACHMENT_DELETE_CONFIRM_ACTION
			}
		],
		appendToPayload: {
			eventSource: REMOVE_MODAL_EVENT_SOURCE
		}
	})
};

/**
 * Attachment Component, for listing, uploading, and editing
 * attachments.
 */
class Attachment extends React.Component {
	/**
	 * Constructor
	 * @param props {Object}
	 */
	constructor(props) {
		super(props);
		this.uploadAttachments = this.uploadAttachments.bind(this);
		this.setFileInputRef = this.setFileInputRef.bind(this);
		this.getExtensionFromFileName = this.getExtensionFromFileName.bind(this);
		this.isInvalidFileType = this.isInvalidFileType.bind(this);
		this.state = {
			list: [],
			count: 0
		};
	}

	/**
	 * Read attachments if we have a proper table and sysId
	 */
	componentWillMount() {
		const {
			properties: { table, sysId }
		} = this.props;
		if (!table || !sysId) return;

		this.read(table, sysId);
	}

	/**
	 * Utility method to check if the current state is the same as the incoming state change
	 * @param changes
	 * @param prop
	 * @returns {boolean}
	 */
	isStateDifferent(changes, prop) {
		return changes[prop] && this.props[prop] !== changes[prop];
	}

	hasAttachments() {
		return !_.isEmpty(_.get(this.props, "data.attachments", []));
	}

	canCreate() {
		const { isAttachmentUploadDisabled, isReadOnly } = this.props.properties;
		const { data = {} } = this.props;
		const { canCreate = true } = data;
		return !isReadOnly && canCreate && !isAttachmentUploadDisabled;
	}

	/**
	 * Read attachments (GraphQL datasource)
	 */
	read(table, sysId) {
		if (!table || !sysId) return;

		this.props.dispatch(ATTACHMENT_ACTIONS.ATTACHMENT_READ_QUERY, {
			sysId: sysId,
			table: table
		});
	}

	/**
	 * Return the file extension if found else return ''.
	 */
	getExtensionFromFileName(name) {
		if (name.lastIndexOf(".") === -1) return "";

		return name.substring(name.lastIndexOf(".") + 1).toLowerCase();
	}

	/**
	 * Types are extensions set in glide.attachment.extensions property
	 * if set
	 * 		only allowed specified extensions
	 * else
	 * 		allows everything
	 */
	isInvalidFileType(file, types) {
		return !types.includes(this.getExtensionFromFileName(file.name));
	}

	/**
	 * Uploads a new attachment (REST datasource)
	 * @param e
	 */
	uploadAttachments(e) {
		const {
			properties: { table, sysId },
			dispatch: { updateState }
		} = this.props;
		const files = [...(e.target.files || e.dataTransfer.files)];
		updateState({ uploadFiles: files });
		e.target.value = null;
	}

	componentDidUpdate(prevProps, prevState) {
		if (this.state.showAttachmentPreview) {
			// Need to do this hack, as by this time the shadowroot is empty.
			setTimeout(() => {
				const button =
					this.previewDownloadButton &&
					this.previewDownloadButton.shadowRoot.querySelector("button");
				if (button) {
					button.focus();
					const event = new FocusEvent("focus", {
						view: window,
						bubbles: true,
						cancelable: true,
						composed: true
					});
					button.dispatchEvent(event); //only composed events escape shadow dom, needed for Firefox
				}
			});
			this.previousActiveElement = getActiveElement(document.activeElement);
		} else if (this.previousActiveElement && this.previousActiveElement.focus) {
			this.previousActiveElement.focus();
			this.previousActiveElement = null;
		}

		if (
			(!this.hasAttachments(prevProps) &&
				(this.props.list || []).length === (prevProps.list || []).length) ||
			!this.prevState ||
			(this.prevState &&
				this.prevState.previousAttachmentSysId &&
				!this.state.previousAttachmentSysId)
		) {
			return;
		}
	}

	setFileInputRef(node) {
		const { updateState } = this.props.dispatch;

		updateState({
			fileInputRef: node,
			shouldRender: false
		});
	}

	/**
	 * Utility method to establish a files availability.  For now, this may be verbose.  However, there are 4
	 * sys_attachment states that have been introduced:
	 *
	 * available, not_available, pending, and available_conditionally
	 *
	 * @param item
	 * @returns {boolean}
	 */
	isFileUnavailable(item) {
		return item.state === "not_available";
	}

	updateAttachmentName = ({ value: currentFileName }) => {
		this.props.dispatch.updateState({ currentFileName });
	};

	setPreviewRef = node => {
		if (node) this.previewOverlayRef = node;
	};

	setPreviewDownloadRef = node => {
		this.previewDownloadButton = node;
	};

	createFileDataURL = file => {
		var reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => {
			if (reader.result !== this.state.uploadFileDataURL)
				this.setState({
					uploadFileDataURL: reader.result
				});
		};
	};

	/**
	 * Renders the attachment overlay for upload and preview.
	 * @returns overlay panel
	 */
	renderOverlay() {
		const {
			hoistOverlayToContainer,
			componentId,
			docViewerState,
			docViewerEnabled,
			uploadFiles,
			properties : { disableUploadModal }
		} = this.props;

		if (hoistOverlayToContainer === undefined) return null;

		const { recordDisplayValue } = this.props.properties;
		const canUploadAttachment = this.canCreate();

		const overlayProps = {
			...getReadOnlyPreviewOverlayProps(this.props, this.props.properties),
			canUploadAttachment,
			isReadOnly: !canUploadAttachment,
			recordDisplayValue,
			componentId: `${componentId}_overlay`, 
			docViewerState,
			docViewerEnabled,
			uploadFiles,
			isHoisted: hoistOverlayToContainer,
			disableUploadModal
		};

		return hoistOverlayToContainer ? (
			<sn-form-internal-attachment-overlay
				nowHoist
				nowHoistPrependTo={hoistOverlayToContainer}
				{...overlayProps}
			/>
		) : (
			<sn-form-internal-attachment-overlay {...overlayProps} />
		);
	}

	/**
	 * Renders the component, filtering out items that don't meet some criteria.
	 * @returns {*}
	 */
	render() {
		const { sysId, classicForm, mode } = this.props.properties;
		const {
			modal = {},
			list,
			loadingItems = [],
			data: { maxFileNameLength } = { maxFileNameLength: 0 },
			hoistOverlayToContainer
		} = this.props;
		const { display: displayModal, type: modalType, data } = modal;
		const modalProps = displayModal ? MODAL_PROPS[modalType](data) : {};
		const isCompact = mode === "compact" ? "-compact" : "";
		const isWorkspace = classicForm ? "-workspace" : "";
		const modifierClassName = `${isCompact} ${isWorkspace}`;

		const hasAttachments = this.hasAttachments();
		const canCreate = this.canCreate();

		if (!hasAttachments && !this.props.attachmentsInitialized)
			return !classicForm ? this.renderNodata() : null;

		return (
			<div className={`sn-panel ${modifierClassName}`}>
				{this.renderOverlay()}
				<input
					id={`fileInput_${sysId}`}
					className="now-a11y-label"
					type="file"
					tabIndex="-1"
					aria-hidden="true"
					multiple="true"
					onChange={this.uploadAttachments}
					ref={this.setFileInputRef}
				/>
				<sn-form-internal-attachments-header
					className={modifierClassName}
					title={t("Attachments")}
					hasAttachments={hasAttachments}
					canCreate={canCreate}
					mode={mode}
				/>
				<sn-form-internal-attachments-list
					className={modifierClassName}
					list={list}
					loadingList={loadingItems}
					canCreate={canCreate}
					maxFileNameLength={maxFileNameLength}
					attachmentsNotFound={
						this.props.searchString !== "" && hasAttachments
					}
					mode={mode}
					classicForm={classicForm}
				/>
				{hoistOverlayToContainer ? 
					<now-modal nowHoist nowHoistPrependTo={hoistOverlayToContainer} opened={displayModal} {...modalProps} size="md" /> :
					<now-modal opened={displayModal} {...modalProps} size="md" />
				}
			</div>
		);
	}

	/**
	 * Renders a panel with No data to display message.
	 * @returns {*}
	 */
	renderNodata() {
		const text = t("No data to display");
		return (
			<div className="sn-empty-state">
				<div className="sn-empty-state-row">
					<div className="sn-empty-state-img -no-attachments" />
				</div>
				<div className="sn-empty-state-row -header">{text}</div>
			</div>
		);
	}
}

export default Attachment;
